<template>
  <div class="jh_teacherCont">
    <teacherConttent
      :teachersCont="teachersCont"
      :teacher="teacher"
      :goodClass="goodClass"
      :studentTag="studentTag"
      :studentInfo="studentInfo"
      :teacherInfo_intro="teacherInfo_intro"
      :id="id"
    ></teacherConttent>
  </div>
</template>

<script>
import {
  getTeachers,
  getTeachersInfo,
  mainCourse,
  comment
} from "../../http/api";

import teacherConttent from "../../components/msmk_index/teacherConttent";
export default {
  components: {
    teacherConttent
  },
  data() {
    return {
      isShow: true,
      goodClass: [],
      active: 0,
      id: "", //教师id
      teachersCont: [], //教师详情
      teacherInfo_intro: "", //老师简介
      teacher: [], //,
      page: 1, //
      limit: 10, //
      studentInfo: [], //学生信息
      studentTag: [] //学生评价
    };
  },
  created() {
    this.id = this.$route.query.id;
  },
  mounted() {
    this.getTeachers();
    this.getTeachersInfo();
    this.mainCourse();
    this.comment();
  },
  methods: {
    up() {
      this.isShow = true;
    },
    down() {
      this.isShow = false;
    },
    //   返回
    onClickLeft() {
      this.$router.go(-1);
    },
    // 获取教师个人信息
    getTeachers() {
      return getTeachers(this.id).then(res => {
        //   console.log(res)
        this.teachersCont.push(res.data.data.teacher);
      });
    },
    // 获取教师介绍
    getTeachersInfo() {
      return getTeachersInfo(this.id).then(res => {
        // console.log(res.data.data);
        this.teacherInfo_intro = res.data.data.intro;
        this.teacher = res.data.data.attr.splice(
          res.data.data.attr.length - 2,
          res.data.data.attr.length - 1
        );
      });
    },
    // 教师主讲课程
    mainCourse() {
      let token = localStorage.getItem("token");
      return mainCourse(this.page, this.limit, this.id, token).then(res => {
        // console.log(res.data.data.list)
        this.goodClass = res.data.data.list;
      });
    },
    // 获取教师评价
    comment() {
      let token = localStorage.getItem("token");
      return comment(this.page, this.limit, this.id, token).then(res => {
        // console.log(res.data.data.comment.list);
        // console.log(res.data.data.tag);
        this.studentInfo = res.data.data.comment.list;
        this.studentTag = res.data.data.tag;
      });
    },
   
  }
};
</script>

