<template>
  <div class="jh_vip">
    <!-- 导航 -->
    <div>
      <returnHome :title="title"></returnHome>
    </div>
    <!-- 导航结束 -->
    <!-- 个人信息 -->
    <div class="jh_user">
      <div>
        <img :src="user.avatar" alt />
      </div>
      <div>
        <p>{{user.nickname}}</p>
        <p>开通会员免费学习</p>
      </div>
    </div>
    <!-- 个人信息结束 -->

    <!-- 购买列表 -->
    <div class="jh_vip_cont">
      <div>
        <p>购买会员</p>
        <span>购买会员后可免费观看会员课程</span>
      </div>
      <ul>
        <li
          v-for="(value,index) in vipList"
          :key="index"
          :class="activeIndex==index?'active':'null'"
          @click="indexToggle({index:index,shop_id:value.id})"
        >
          <p>{{value.name}}</p>
          <p>
            <img
              src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20191HHDExgz0u1567065946.png"
              alt
            />
            <span>{{value.price|toFixPrice}}</span>
          </p>
        </li>
      </ul>
    </div>
    <!-- 购买列表结束 -->
    <!-- 支付 -->
    <div class="jh_nowPay" @click="nowPay">立即支付</div>
    <!-- 支付 -->
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { vipGrade, userInfo, buyVip } from "../../http/api";
import Vant from "vant";
import { Dialog } from "vant";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      title: "开通会员",
      vipList: [], //会员价格列表,
      user: {}, //用户信息,
      activeIndex: 0, //下报
      shop_id: 1
    };
  },
  created() {},
  mounted() {
    this.userInfo();
    this.vipGrade();
  },
  methods: {
    // 获取用户信息
    userInfo() {
      let token = localStorage.getItem("token");
      return userInfo(token).then(res => {
        this.user = res.data.data;
      });
    },
    // 价格列表
    vipGrade() {
      let token = localStorage.getItem("token");
      return vipGrade(token).then(res => {
        this.vipList = res.data.data;
      });
    },
    // index切换
    indexToggle({ index, shop_id }) {
      this.shop_id = shop_id;
      this.activeIndex = index;
    },
    // 立即支付
    nowPay() {
      let token = localStorage.getItem("token");
      Dialog.alert({
        title: "提示",
        message: "你确定购买此会员吗?",
        showCancelButton: "true"
      })
        .then(() => {
          return buyVip(token, this.shop_id).then(res => {
            console.log(res);
            Dialog.confirm({
              title: "提示",
              message: res.data.data.msg,
              showCancelButton: "true",
              confirmButtonText: "去充值 "
            }).then(() => {
              this.$router.push("/myMoney");
            }) .catch(() => {});
          });
        })
        .catch(() => {});
    }
  }
};
</script>

<style scoped lang="scss">
.jh_vip {
  width: 100%;
}
// 个人信息区域
.jh_user {
  border-top: 1px solid #f0f2f5;
  width: 100%;
  height: 150px;
  background: #fff;
  padding: 0px 30px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  div:nth-of-type(2) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 40px 0px;
    box-sizing: border-box;
    margin-left: 30px;
  }
  div {
    img {
      width: 88px;
      height: 88px;
    }
    p:nth-of-type(1) {
      font-size: 26px;
      color: #595959;
    }
    p:nth-of-type(2) {
      font-size: 25px;
      color: #8c8c8c;
    }
  }
}
// 个人信息区域

// 价格区域
.jh_vip_cont {
  width: 100%;
  padding: 75px 30px;
  box-sizing: border-box;
  > div {
    display: flex;
    align-items: center;
    p {
      font-size: 30px;
      color: #595959;
      margin-right: 20px;
    }
    span {
      font-size: 24px;
      color: #8c8c8c;
    }
  }
  ul {
    padding: 40px 0px;
    display: flex;
    justify-content: space-between;
    li.active {
      border: 2px solid #e6c37f;
      background-color: #f9f0d9;
    }
    li {
      border-radius: 10px;
      width: 200px;
      height: 225px;
      border: 2px solid #d9d9d9;
      background: #fff;
      display: flex;
      flex-direction: column;
      padding: 50px 0px;
      box-sizing: border-box;
      justify-content: space-between;
      align-items: center;
      p:nth-of-type(1) {
        font-size: 30px;
        font-weight: bold;
      }
      p:nth-of-type(2) {
        font-size: 45px;
        font-weight: bold;
        color: #d5a322;
        img {
          width: 30px;
          height: 30px;
          margin-right: 20px;
        }
      }
    }
  }
}
// 价格区域结束

//支付
.jh_nowPay {
  position: fixed;
  bottom: 60px;
  left: 30px;
  right: 30px;
  background: linear-gradient(90deg, #eac687, #c8ae84);
  border-radius: 50px;
  color: #fff;
  height: 90px;
  text-align: center;
  line-height: 90px;
}
</style>
