<template>
  <div class="jh_messageDetail">
    <div>
      <returnHome :title="title"></returnHome>
    </div>
    <div class="jh_messageDetail_cont">
      <ul>
        <li v-for="(value,index) in list" :key="index">
          <p>{{value.created_at*1000|timeA}}</p>
          
          <p>{{value.content}}  <router-link :to="{path:'/orderDetail',query:{order_id:value.order_id}}" tag="span"  v-if="value.link_type===1">点击支付>></router-link></p>
        </li>
      </ul>
      <p>没有更多了</p>
    </div>
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { getMessage } from "../../http/api";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      title: "",
      limit: 10,
      page: 1,
      message_classify: "",
      list: []
    };
  },
  created() {
    this.title = this.$route.query.title;
    this.message_classify = this.$route.query.message_classify;
  },
  mounted() {
    this.getMessage();
  },
  methods: {
    getMessage() {
      let token = localStorage.getItem("token");
      return getMessage(
        token,
        this.limit,
        this.message_classify,
        this.page
      ).then(res => {
        console.log(res);
        this.list = res.data.data.list;
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_messageDetail {
  width: 100%;
  height: 100%;
  overflow: auto;
  background: #fff;
}
.jh_messageDetail_cont {
  padding: 0px 30px;
  box-sizing: border-box;
  width: 100%;
  >P{
      font-size:26px;
    color: #8c8c8c;
    text-align: center;
    line-height: 50px;
  }
  ul {
    width: 100%;
    li {
      width: 100%;
      padding: 30px 0px;
      box-sizing: border-box;
      border-bottom: 1px solid #f5f5f5;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      p{
          font-size: 26px;
          color:#8c8c8c;
          span{
            color:#eb6100;
        }
      }
      p:nth-of-type(1) {
        width: 200px;
        height: 50px;
        background: #b7b7b7;
        color: #fff;
        font-size: 24px;
        text-align: center;
        line-height: 50px;
        border-radius: 10px;
        margin-bottom: 35px;
        
      }
    }
  }
}
</style>
