<template>
  <div class="jh_collect">
    <div class="jh_collect_nav">
      <returnHome :title="title" ></returnHome>
    </div>
    <div class="jh_collect_list" v-if="collect_list.length>0">
      <ul>
        <router-link
          :to="{path:'/classCont',query:{id:`${value.course_id}`,course_type:value.course_type}}"
          tag="li"
          v-for="(value,index) in collect_list"
          :key="index"
        >
          <i class="jh_star">
            <van-icon name="star" color="#eb6100" size="18px" @click.stop="unCollect(value.collect_id)" />
          </i>
          <p>{{value.title}}</p>
          <div>
            <img src="../../assets/clock.png" alt class="jh_clock" />
            <span>{{value.start_play_date*1000|time}}~{{value.end_play_date*1000|time}}</span>
            <span>|</span>
            <span>共{{value.section_num}}课时</span>
          </div>
          <p>
            <img :src="value.teachersAvatar" alt />
            <span>{{value.teachers}}</span>
          </p>
          <p>
            <span>{{value.sales_num}}人已报名</span>
            <span>
              <img src="../../assets/20191HHDExgz0u1567065946.png" alt class="jh_money_img" />
              <span></span>
              {{value.price==0?`${value.price}`:`${value.price/100}.00`}}
            </span>
          </p>
        </router-link>
      </ul>
    </div>

    <div class="jh_noCollect" v-else>
          <img src="@/assets/empty.0d284c2e.png" alt="">
          <p>暂无收藏</p>
    </div>
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { collectList,unCollect } from "../../http/api";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      title: "",
      limit: 10,
      page: 1,
      collect_list: []
    };
  },
  created() {
    this.title = this.$route.meta.title;
  },
  mounted() {
    this.collectList();
  },
  methods: {
    // 获取收藏数据
    collectList() {
      let token = localStorage.getItem("token");
      return collectList(this.limit, this.page, token).then(res => {
        this.collect_list = res.data.data.list;
      });
    },
    // 取消收藏
    unCollect(CollectData){
        let token=localStorage.getItem('token')
        return unCollect(token, CollectData, 1).then(res=>{
            if(res.data.code==200){
              this.$toast.success({
                message:"取消收藏"
              })
                this.collectList();
            }
        })
    }
  }
};
</script>

<style scoped lang='scss'>
// 收藏列表
.jh_collect_list {
  .jh_money_img {
    width: 18px;
    height: 18px;
    border-radius: 0px;
    margin-right: 10px;
  }
  .jh_clock {
    width: 25px;
    height: 25px;
  }
  width: 100%;
  padding: 15px 30px;
  box-sizing: border-box;
  ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    li:nth-last-of-type(1) {
      margin-bottom: 0px;
    }
    li {
      height: 400px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      background: #fff;
      padding: 30px 20px;
      box-sizing: border-box;
      margin-bottom: 20px;
      border-radius: 20px;
      position: relative;
      .jh_star {
        position: absolute;
        right: 30px;
        top: 30px;
      }
      p {
        display: flex;
        align-items: center;
        color: #333;
        font-size: 30px;
      }
      p:nth-of-type(1) {
        width: 90%;
      }
      div {
        color: #666;
        font-size: 24px;
        display: flex;
        align-items: center;
        span:nth-of-type(2) {
          margin: 0px 10px;
        }
      }
      p:nth-of-type(2) {
        justify-content: left;
        color: rgba(0, 0, 0, 0.45);
        font-size: 18px;
        border-bottom: 2px solid #f5f5f5;
        padding: 25px 0px;
        box-sizing: border-box;
        img {
          width: 80px;
          height: 80px;
          border-radius: 50%;
          margin-right: 20px;
        }
      }
      p:nth-last-of-type(1) {
        display: flex;
        justify-content: space-between;
        > span:nth-of-type(1) {
          color: rgba(0, 0, 0, 0.45);
          font-size: 25px;
        }
        > span {
          color: #ee1f1f;
        }
        > span:nth-last-of-type(1) {
          margin-left: 10px;
          font-size: 40px;
        }
      }
    }
  }
}

// 暂无收藏
.jh_noCollect{
  width:100%;
  height:100%;
  img{
    width:300px;
    height:300px;
  }
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top:150px;
  p{
    font-size: 30px;
    color:#8c8c8c;
    line-height: 100px;
  }

}
</style>
