<template>
  <div class="jh_return">
    <div>
      <img src="../../assets/return.png" alt @click="goHome" />
      <p>{{title}}</p>
      <img src="../../assets/share111.png" alt @click="share" class="jh_share" v-if="title=='课程详情'" />
      <p v-else></p>
    </div>
  </div>
</template>

<script>
export default {
  props: ["title", "isshow"],

  data() {
    return {
      list: ["设置", "课程订单", "消息通知", "意见反馈"]
    };
  },
  created() {},
  mounted() {},
  methods: {
    goHome() {
      if (this.list.includes(this.title)) {
        this.$router.push("/login");
      } else if (this.title == "订单通知") {
        this.$router.push("/message");
      } else if (this.title == "成绩统计") {
        this.$router.push("/point");
      } else {
        this.$router.go(-1);
      }
    },
    share() {
      this.$store.commit("share", { location: location.href, isShow: true });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_return {
  width: 100%;
  height: 86px;
  div {
    p:nth-of-type(1) {
      width: 70%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      color: #595959;
      text-align: center;
    }
    background: #fff;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #f0f2f5;
    padding: 0px 36px;
    box-sizing: border-box;
    align-items: center;
    height: 86px;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 99999999999999999999;
    background: #fff;
    img {
      width: 20px;
      height: 35px;
    }
  }
  
}
.jh_share{
    width:30px!important;
  }
</style>
