    };
  },
  created() {},
  mounted() {
    // 获取token用户信息
    this.getToken();
  },
  methods: {
    goSetInfo() {
      this.$router.push("/info");
    },
    /************************************************************************************************************************************************ */
    // 获取token
    getToken() {
      let token = localStorage.getItem("token");

      if (token == null) {
        this.$router.push("/regLogin");
      }
      this.getuserInfo(token);
      this.getUCenterInfo(token);
    },
    /*********************************************************************************************************************************** */
    // 获取个人课程信息
    getUCenterInfo(token) {
      this.$axios({
        url: "https://www.365msmk.com/api/app/getUCenterInfo?",
        headers: {
          "Content-Type": "application/json",
          DeviceType: "H5",
          Authorization: `Bearer ${token}`
        }
      }).then(res => {
        this.UCenterInfo = res.data.data;
      });
    },
    /****************************************************************************************************************************************** */
    // 获取用户信息
    getuserInfo(token) {
      this.$axios({
        url: "https://www.365msmk.com/api/app/userInfo?",
        headers: {
          "Content-Type": "application/json",
          DeviceType: "H5",
          Authorization: `Bearer ${token}`
        },
        method: "get"
      }).then(res => {
        this.userInfo = res.data.data;
      });
    },
