<template>
  <div class="jh_result">
    <!-- 导航区域 -->
    <div class="jh_nav">
      <returnHome :title="title"></returnHome>
    </div>
    <!-- 导航区域结束 -->

    <!-- 卷子信息 -->
    <div class="jh_result_conth">
      <!-- 科目 -->
      <ul>
        <li>
          <img src="@/assets/10010.png" alt />
          <span>{{resultList.name}}</span>
        </li>
        <li>
          <img src="@/assets/10011.png" alt />
          <span>答题时间:{{resultList.report_time_long|timeE}}</span>
        </li>
        <li>
          <img src="@/assets/10012.png" alt />
          <span>交卷时间:{{resultList.created_at}}</span>
        </li>
      </ul>
      <!-- 科目结束 -->
      <!-- 正确率 -->
      <div class="jh_tongji">
        <div>
          <p>
            <span>答对</span>
            <span>{{resultList.right_count}}</span>
          </p>
          <p>
            <span>错误</span>
            <span>{{resultList.wrong_count}}</span>
          </p>
          <p>
            <span>未做</span>
            <span>{{resultList.not_count}}</span>
          </p>
        </div>

        <div>
          <p>
            <span>正确率</span>
            <span>{{parseInt(resultList.correct_rate*100)}}%</span>
          </p>
          <p>共{{length}}到题</p>
        </div>
      </div>
      <!-- 正确率结束 -->
    </div>
    <!-- 卷子信息结束 -->

    <!-- 答题卡 -->
    <div class="jh_datika">
      <!-- 第一 -->
      <div>
        <div>答题卡</div>
        <div>
          <p>
            <i class="jh_right"></i>
            <span>答对</span>
          </p>
          <p>
            <i class="jh_wrong"></i>
            <span>答错</span>
          </p>
          <p>
            <i class="jh_no"></i>
            <span>未做</span>
          </p>
        </div>
      </div>
      <!-- 第一结束 -->

      <ul>
        <li
          v-for="(value,index) in reports"
          :key="index"
          :class="[{ jh_right: value.is_right==1, jh_wrong:value.is_right==2 }]"
        >{{index*1+1}}</li>
      </ul>
    </div>
    <!-- 答题卡结束 -->

    <!-- 答案解析 -->
    <div class="jh_jiexi" @click="jx">答案解析</div>
    <!-- 答案解析结束 -->
  </div>
</template>

<script>
import { getResult } from "../../http/api";
import returnHome from "../../components/return/return";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      length: "", //   \\
      title: "", //   \\
      id: "", //   \\
      paper_id: "", //   \\
      type: "", //    \\
      number: "", //   \\
      resultList: [], //   \\
      reports: [] //   \\
    };
  },
  created() {
    this.title = this.$route.meta.title;
    this.id = this.$route.query.id;
    this.paper_id = this.$route.query.paper_id;
    this.type = this.$route.query.type;
    this.number = this.$route.query.number;
  },
  mounted() {
    this.getResult();
  },
  methods: {
    //   获取成绩结果
    getResult() {
      let token = localStorage.getItem("token");
      return getResult(token, this.id).then(res => {
        this.resultList = res.data.data;
        this.length = this.resultList.reports.length;
        this.reports = this.resultList.reports;
      });
    },
    // 获取成绩结果结束
    jx() {
      this.$router.push({
        path: "/fenxi",
        query: {
          type: "Point",
          id: this.paper_id,
          report_id: this.id,
          number:15,
        }
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_result {
  position: relative;
  width: 100%;
  min-height: 100%;
  overflow: auto;
}
// 内容区域
.jh_result_conth {
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  ul {
    width: 100%;
    background: #fff;
    margin-bottom: 30px;
    height: 250px;
    border-radius: 10px;
    padding: 30px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    li {
      display: flex;
      align-items: center;
      font-size: 30px;
      color: #595959;
      height: 50px;
      img {
        width: 28px;
        height: 28px;
        margin-right: 10px;
      }
    }
  }
  .jh_tongji {
    padding: 30px;
    box-sizing: border-box;
    background: #fff;
    height: 250px;
    margin-bottom: 30px;
    > div:nth-of-type(1) {
      display: flex;
      justify-content: space-between;
      padding: 0px 20px;
      box-sizing: border-box;
      margin-bottom: 40px;
      p {
        width: 160px;
        height: 104px;
        font-size: 24px;
        border: 1px solid #d9d9d9;
        text-align: center;
        display: flex;
        flex-direction: column;
        border-radius: 10px;
        justify-content: space-around;
        span {
          font-size: 30px;
          color: #8c8c8c;
        }
      }
      p:nth-of-type(1) {
        span:nth-of-type(2) {
          color: #0ccaa4;
        }
      }
      p:nth-of-type(2) {
        span:nth-of-type(2) {
          color: #ff4157;
        }
      }
      p:nth-of-type(3) {
        span:nth-of-type(2) {
          color: #b7b7b7;
        }
      }
    }
    > div:nth-of-type(2) {
      display: flex;
      font-size: 26px;
      justify-content: space-between;
      color: #8c8c8c;
      > p:nth-of-type(1) {
        span:nth-of-type(2) {
          color: #ff4157;
          margin-left: 10px;
          font-size: 32px;
        }
      }
    }
  }
}
// 内容区域

// 答题卡区域
.jh_datika {
  width: 100%;
  background: #fff;
  > div:nth-of-type(1) {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #eaeaea;
    font-size: 30px;
    height: 86px;
    align-items: center;
    padding: 0px 30px;
    box-sizing: border-box;
    > div:nth-of-type(2) {
      display: flex;
      justify-content: space-between;
      font-size: 26px;
      color: #8c8c8c;

      p {
        display: flex;
        align-items: center;
        margin-left: 20px;
      }
      .jh_right {
        background: #0ccaa4;
        border: none;
      }
      .jh_wrong {
        background: #ff4157;
        border: none;
      }
      i {
        display: inline-block;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        border: 1px solid #8c8c8c;
        box-sizing: border-box;
        margin-right: 10px;
      }
    }
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    padding-top: 20px;
    box-sizing: border-box;
    .jh_right {
      background: #0ccaa4;
      border: none;
      color: #fff;
    }
    .jh_wrong {
      background: #ff4157;
      border: none;
      color: #fff;
    }
    li {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      border: 1px solid #eaeaea;
      color: #b8b8b8;
      text-align: center;
      line-height: 60px;
      margin: 0px 40px 20px 40px;
    }
  }
}
// 答题卡区域结束

// 答案解析
.jh_jiexi {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 90px;
  text-align: center;
  line-height: 90px;
  color: #fff;
  background: #eb6100;
}
// 答案解析结束
</style>
