<template>
  <div class="jh_practise">
    <div class="jh_practise_title">练习</div>
    <!-- 导航区域 -->
    <div class="jh_practise_nav">
      <ul>
        <li v-for="(value,index) in navList" :key="index" @click="go(index)">
          <div>
            <img :src="require(`../../assets/${value.url}`)" alt />
          </div>
          <p>{{value.cont}}</p>
        </li>
      </ul>
    </div>
    <!-- 导航区域结束 -->
    <!-- 近期模考区域 -->
    <div class="jh_imitate_test">
      <p>近期模考</p>
      <p>
        <span>默认排序</span>
        <span>时间</span>
      </p>
      <div>
        <img src="../../assets/empty@2x.a2fa524d.png" alt />
        <p>暂无考试安排,敬请期待</p>
      </div>
    </div>
    <!-- 近期模考区域结束 -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      navList: [
        { cont: "考点练习", url: "0.png" },
        { cont: "套卷练习", url: "1.png" },
        { cont: "仿真模考", url: "2.png" },
        { cont: "错题练习", url: "3.png" },
        { cont: "评估记录", url: "4.png" },
        { cont: "习题收藏", url: "5.png" }
      ]
    };
  },
  created() {},
  mounted() {},
  methods: {
    go(index) {
      if (localStorage.getItem("token") == null) {
        this.$router.push("/regLogin");
      } else {
        if (index == 2) {
          this.$router.push("/exam");
        } else if (index == 1) {
          this.$router.push("/paperPackage");
        } else if (index == 5) {
          this.$router.push("/quesCollect");
        } else if (index == 0) {
          this.$router.push("/point");
        } else if (index == 3) {
          this.$router.push("/errorQuestion");
        } else if (index == 4) {
          this.$router.push("/assess");
        }
      }
    }
  }
};
</script>

<style scoped lang="scss">
.jh_practise {
  width: 100%;
  //   标题
  .jh_practise_title {
    text-align: center;
    width: 100%;
    height: 90px;
    color: #595959;
    font-size: 36px;
    background: #fff;
    line-height: 90px;
  }
  //   标题结束
  // 导航
  .jh_practise_nav {
    width: 100%;
    padding: 20px 20px;
    box-sizing: border-box;
    ul {
      background: #fff;
      display: flex;
      flex-wrap: wrap;
      padding: 35px 0px 10px 0;
      box-sizing: border-box;
      li {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 25%;

        p {
          font-size: 25px;
          color: #595959;
          line-height: 80px;
        }
        > div {
          width: 75px;
          height: 75px;
          border-radius: 50%;
          box-shadow: 0 0 3.2vw 0 rgba(10, 0, 24, 0.07);
          display: flex;
          align-items: center;
          justify-content: center;

          img {
            width: 35px;
            height: 35px;
          }
        }
      }
    }
  }
  //   导航结束
  .jh_imitate_test {
    width: 100%;
    background: #fff;
    color: #8c8c8c;
    padding: 20px 30px;
    box-sizing: border-box;
    > p:nth-of-type(1) {
      font-size: 30px;
    }
    > p:nth-of-type(2) {
      font-size: 26px;
      display: flex;
      justify-content: space-between;
      line-height: 80px;
      > span:nth-of-type(1) {
        color: #eb6100;
      }
    }
    div {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      img {
        width: 170px;
        margin-bottom: 40px;
      }
      p {
        color: #8c8c8c;
        font-size: 28px;
      }
    }
  }
}
</style>
