<template>
  <div class="jh_regLogin">
    <div class="jh_regLogin_content">
      <div class="jh_regLogin_img">
        <img
          src="https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/2019pILfAg7Avr1567732916.png"
          alt
        />
      </div>
      <div class="jh_regLogin_passLog" v-if="isShow">
        <p>
          <input type="text" placeholder="请输入手机号" v-model="mobile" />
        </p>
        <p>
          <input type="password" placeholder="请输入密码" v-model="password" />
        </p>
        <p>
          <span @click="goForget">找回密码</span>
          <span @click="toggle">注册/验证码登入</span>
        </p>
        <div class="jh_regLogin_button" @click="add(1)">登入</div>
      </div>
      <div class="jh_regLogin_yanLog" v-else>
        <p>
          <input type="text" placeholder="请输入手机号" v-model="mobile" />
          <span @click="getsms_code" v-if="show">获取验证码</span>
          <span v-else>({{num}})秒后重新发送</span>
        </p>
        <p>
          <input type="password" placeholder="请输入短信验证码" v-model="sms_code" />
        </p>
        <p>
          <span>*未注册的手机号将自动注册</span>
          <span @click="toggle">使用密码登入</span>
        </p>
        <div class="jh_regLogin_button" @click="sms_code_add(2)">登入</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mobile: "",
      password: "",
      isShow: true,
      sms_code: "",
      show: true,
      num: 60
    };
  },
  created() {},
  mounted() {},
  methods: {
    // 找回密码
    goForget(){
      this.$router.push('/forgetPass')
    },
    //   切换登入方式
    toggle() {
      this.isShow = !this.isShow;
    },
    //提交
    add(num) {
      this.$axios
        .post("https://www.365msmk.com/api/app/login", {
          mobile: this.mobile,
          password: this.password,
          type: num
        })
        .then(res => {
          if (res.data.code != 200) {
            this.$toast(res.data.msg);
          } else {
            localStorage.setItem("token", res.data.data.remember_token);
            this.getuserInfo(num);
          }
        });
    },
    // 倒计时
    countsecond() {
      let time = 60;
      let timer = setInterval(() => {
        if (time < 1) {
          clearInterval(timer);
          this.show = true;
        }
        this.num = time;
        time--;
      }, 1000);
    },
    //获取验证码
    getsms_code() {
      let num = this.num;
      this.$axios
        .post("https://www.365msmk.com/api/app/smsCode", {
          mobile: this.mobile,
          sms_type: "login"
        })
        .then(res => {
          if (res.data.data == true) {
            this.$toast.success({
              message: "发送成功"
            });
            this.show = false;
            this.countsecond();
          } else {
            this.$toast.fail({
              message: res.data.msg
            });
          }
        });
    },
    // 验证码登入
    sms_code_add(num) {
      let formdata = new FormData();
      formdata.append("mobile", this.mobile);
      formdata.append("sms_code", this.sms_code);
      formdata.append("type", num);
      this.$axios
        .post("https://www.365msmk.com/api/app/login", formdata)
        .then(res => {
          if (res.data.code == 200) {
            localStorage.setItem("token", res.data.data.remember_token);
            this.getuserInfo(num);
          } else {
            this.$toast.fail(res.data.msg);
          }
        });
    },
    // 获取用户信息
    getuserInfo(num) {
      let token = localStorage.getItem("token");
      this.$axios({
        url: "https://www.365msmk.com/api/app/userInfo?",
        headers: {
          "Content-Type": "application/json",
          DeviceType: "H5",
          Authorization: `Bearer ${token}`
        },
        method: "get",
        params: {
          mobile: this.mobile,
          type: num,
          password: this.password,
          sms_code: this.sms_code
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$toast.success({
            message: "登入成功"
          });
        }
        this.$router.go(-1);
      });
    }
  }
};
</script>

<style scoped lang="scss">
input {
  width: 100%;
  border: none;
  height: 70px;
  background: white !important;
  border-bottom: 1px solid #f1f1f1;
  &::placeholder {
    font-size: 30px;
    color: #999;
  }
}

.jh_regLogin {
  height: 100%;
  background: #fff;
  z-index: 9999999999999999999999999;
}
.jh_regLogin_img {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 80px 0px 120px 0px;
  box-sizing: border-box;
  img {
    width: 60%;
  }
}
.jh_regLogin_passLog,
.jh_regLogin_yanLog {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.jh_regLogin_passLog > p,
.jh_regLogin_yanLog > p {
  text-align: center;
  width: 70%;
  height: 100px;
}
.jh_regLogin_yanLog > p:nth-of-type(1) {
  position: relative;
}
.jh_regLogin_yanLog > p:nth-of-type(1) > span {
  position: absolute;
  right: 0px;
  top: 21px;
  font-size: 20px;
  color: #eb6100;
}
.jh_regLogin_passLog > p:nth-of-type(3),
.jh_regLogin_yanLog > p:nth-of-type(3) {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  color: #999;
}
.jh_regLogin_button {
  width: 100%;
}
.jh_regLogin_button {
  background: -webkit-linear-gradient(left, orange, orangered);
  width: 80%;
  margin: 150px auto;
  height: 80px;
  text-align: center;
  line-height: 80px;
  color: #fff;
  border-radius: 50px;
}
</style>
