<template>
  <div class="jh_paperPackage">
    <!-- 导航 -->
    <div class="jh_paperPackage_nav">
      <van-nav-bar title="套卷练习" left-arrow @click-left="onClickLeft">
        <template #right>
          <van-icon name="search" size="18" @click="goSearch" />
        </template>
      </van-nav-bar>
    </div>
    <!-- 导航结束 -->
    <div class="jh_filter">
      <van-dropdown-menu>
        <van-dropdown-item title="套卷分类" ref="classify">
          <van-tree-select
            height="55vw"
            :items="examClassifyList"
            :main-active-index.sync="active"
            @click-nav="clickNav"
          >
            <template #content>
              <ul v-for="(value,index) in examClassifyList " :key="index" class="jh_classity">
                <li v-if="index==active">
                  <div v-for="(val,info) in value.children" :key="info">
                    <div
                      v-if="index==active"
                      :class="classifyIndex==index?'active':''"
                      @click="getPackage({index:index,id:val.id})"
                    >{{val.name}}</div>
                  </div>
                </li>
              </ul>
            </template>
          </van-tree-select>
        </van-dropdown-item>
        <van-dropdown-item title="套卷状态" ref="done_status">
          <ul>
            <li
              v-for="(value,index) in done_statusList"
              :key="index"
              :class="doneIndex==index?'active':'null'"
              @click="checkStatus({index:index,done_status:value.done_status})"
            >{{value.cont}}</li>
          </ul>
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>
    <!-- 没数据 -->
    <div class="jh_keyWords" v-if="kShow">"{{name}}"的搜索结果</div>
    <div class="jh_noContent" v-if="list.length==0">
      <img src="@/assets/empty.0d284c2e.png" alt />
      <p>请稍候，套卷正在赶来的路上啦</p>
    </div>
    <!-- 没数据结束 -->
  </div>
</template>

<script>
import Vue from "vue";
import { examClassify, packagePractice } from "../../http/api";
export default {
  data() {
    return {
      active: 0, //分类选择器下标
      examClassifyList: [], // 套卷分类
      kShow: false,
      name: "", //套卷名字

      //   套卷状态
      done_statusList: [
        { cont: "全部", done_status: 0 },
        { cont: "已做", done_status: 1 },
        { cont: "未做", done_status: 2 }
      ],
      classifyIndex: 0,
      doneIndex: 0,
      done_status: 0, //状态
      classify_id: 0, //分类
      list: [] //数据列表
    };
  },
  created() {
    this.name = this.$route.params.name;
    if (this.name == undefined) {
      this.name=''
      this.kShow = false;
    } else {
      this.kShow = true;
    }
  },
  mounted() {
    this.packagePractice();
    this.examClassify();
  },
  methods: {
    //   返回
    onClickLeft() {
      this.$router.push("/practise");
    },
    // 搜索
    goSearch() {
      this.$router.push({
        path: "/search",
        query: { name: this.$route.name }
      });
    },
    // 套卷分类
    examClassify() {
      let token = localStorage.getItem(token);
      return examClassify(token).then(res => {
        let obj = { text: "全部", classify_id: 0 };
        res.data.rows.forEach(element => {
          Vue.set(element, "text", element.name);
          element.children.forEach((val, info) => {
            Vue.set(val, "text", val.name);
          });
        });
        res.data.rows.unshift(obj);
        this.examClassifyList = res.data.rows;
      });
    },
    // 获取套卷数据列表
    packagePractice() {
      let token = localStorage.getItem("token");
      return packagePractice(
        token,
        this.done_status,
        this.classify_id,
        this.name
      ).then(res => {
        this.list = res.data.data.list;
      });
    },
    // 左侧导航点击
    clickNav(index) {
      this.active = index;
      if (index == 0) {
        this.done_status = 0;
        this.classify_id = 0;
        this.name = "";
        this.packagePractice();
        this.$refs.classify.toggle();
      }
    },
    // 套卷分类点击
    getPackage({ index, id }) {
      this.classifyIndex = index;
      this.classify_id = id;
      this.packagePractice();
      this.$refs.classify.toggle();
    },
    // 套卷状态
    checkStatus({ index, done_status }) {
      this.doneIndex = index;
      this.done_status = done_status;
      this.packagePractice();
      this.$refs.done_status.toggle();
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_paperPackage{
  width:100%;
  height:100%;
  background: #fff;
}
// 导航区域
// 导航区域
.jh_paperPackage_nav /deep/.van-nav-bar,
.van-hairline--bottom {
  height: 87px;
  border-bottom: 3px solid #f6f6f6;
}
.jh_paperPackage_nav /deep/ .van-nav-bar__title,
.van-ellipsis {
  font-size: 36px;
  color: #656464;
}
.jh_paperPackage_nav /deep/.van-icon,
van-icon-search {
  color: #656464;
  font-size: 36px;
}
// 导航结束
/deep/.van-dropdown-menu__bar {
  height: 80px;
}
/deep/.van-ellipsis {
  height: 80px;
  font-size: 30px;
  line-height: 80px;
}
/deep/.van-dropdown-menu__title::after {
  border-width: 8px;
  right: -30px;
  top: 40%;
}
/deep/.van-popup,
.van-popup--top,
.van-dropdown-item__content {
  z-index: 99999999999999999999999999999999999 !important;
}
// 没有数据
.jh_noContent {
  width: 100%;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 320px;
    height: 320px;
  }
  p {
    font-size: 30px;
    color: #8c8c8c;
    line-height: 90px;
  }
}
// 搜索结果
.jh_keyWords {
  font-size: 26px;
  color: #8c8c8c;
  padding: 0px 30px;
  box-sizing: border-box;
  line-height: 60px;
}
// 筛选
.jh_filter {
  width: 100%;
  ul {
    width: 100%;
    li,
    .active {
      color: #eb6100;
    }
    li {
      border-bottom: 1px solid #f5f5f5;
    }
    li {
      height: 100px;
      text-align: center;
      line-height: 100px;
      font-size: 30px;
      color: #595959;
    }
  }
}
.jh_paperPackage /deep/.van-tree-select__content {
  ul {
    li {
      div {
        padding: 0px 20px;
        box-sizing: border-box;
        text-align: left;
      }
    }
  }
}
.jh_paperPackage /deep/.van-tree-select__nav-item {
  width: 100%;
  height: 90px;
  font-size: 30px;
  color: #595959;
  text-align: center;
  line-height: 75px;
}
.jh_paperPackage /deep/.van-sidebar-item--select::before {
  width: 3px;
  height: 50px;
}
.jh_paperPackage /deep/.van-sidebar-item--select,
.van-tree-select__nav-item {
  color: #eb6100;
}
</style>
