import Vue from "vue";
import VueRouter from "vue-router";
import routersList from "./list";
import HelloWorld from "@/components/HelloWorld";
Vue.use(VueRouter);
// 导入路由文件
const routes = [
  {
    path: "/",
    name: "HelloWorld",
    component: HelloWorld,
    redirect: "/index"
  },
  ...routersList
];

const router = new VueRouter({
  routes
});

export default router;
