<template>
  <div class="jh_quesCollect">
    <!-- 导航 -->
    <div class="jh_quesCollect_nav">
      <van-nav-bar title="套卷练习" left-arrow @click-left="onClickLeft">
        <template #right>
          <van-icon name="search" size="18" @click="goSearch" />
        </template>
      </van-nav-bar>
    </div>
    <!-- 导航结束 -->
    <!-- 导航结束 -->
    <!-- 选择区域 -->
    <div class="jh_filter">
      <van-dropdown-menu>
        <van-dropdown-item title="题目类型" ref="quesiton">
          <ul>
            <li
              v-for="(value,index) in typeList"
              :key="index"
              :class="typeIndex==index?'active':'null'"
              @click="checkType({index:index,ques_type:value.ques_type})"
            >{{value.cont}}</li>
          </ul>
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>
    <!-- 选择区域结束 -->

    <!-- 内容区域 -->
    <div class="jh_keyWords" v-if="kShow">"{{mix_keyword}}"的搜索结果</div>
    <div class="jh_noContent" v-if="quesCollectList.length==0">
      <img src="@/assets/empty.0d284c2e.png" alt />
      <p>暂无收藏记录</p>
    </div>
    <div class="jh_quesCollect_content" v-else>
      <ul>
        <li v-for="(value,index) in quesCollectList" :key="index">
          <div>
            <span>[{{value.ques_type|questionYype}}]</span>
            <p v-html="value.ques_stem"></p>
          </div>
          <div>
            <div></div>
            <p @click="look(value.ques_id)">查看详情</p>
          </div>
        </li>
      </ul>
      <p class="jh_npMore">没有更多了</p>
    </div>
    <!-- 内容区域结束 -->
  </div>
</template>

<script>
import { xtCollect } from "../../http/api";
export default {
  data() {
    return {
      typeIndex: 0,
      ques_type: "", //题目类型
      mix_keyword: "", //关键字
      quesCollectList: [], //数据列表
      typeList: [
        { cont: "单选", ques_type: 1 },
        { cont: "多选", ques_type: 2 },
        { cont: "判断", ques_type: 3 },
        { cont: "连线", ques_type: 4 }
      ]
    };
  },
  created() {
    this.mix_keyword = this.$route.params.name;
    if (this.mix_keyword == undefined) {
      this.mix_keyword = "";
      this.kShow = false;
    } else {
      this.kShow = true;
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    //   返回
    onClickLeft() {
      this.$router.push("/practise");
    },
    // 搜索
    goSearch() {
      this.$router.push({
        path: "/search",
        query: { name: this.$route.name }
      });
    },
    // 获取数据
    getData() {
      let token = localStorage.getItem("token");
      return xtCollect(token, this.ques_type, this.mix_keyword).then(res => {
        // console.log(res.data.data)
        this.quesCollectList = res.data.data.list;
      });
    },
    checkType({ index, ques_type }) {
      this.typeIndex = index;
      this.ques_type = ques_type;
      this.getData();
      this.$refs.quesiton.toggle();
    },
    look(id) {
      this.$router.push({
        path:'/collectDetail',
        query:{id:id}
      })
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_quesCollect {
  width: 100%;
  height: 100%;
  display: flex;
  background: #fff;
  flex-direction: column;
}
// 导航
.jh_quesCollect_nav /deep/.van-nav-bar,
.van-hairline--bottom {
  height: 87px;
  border-bottom: 3px solid #f6f6f6;
}
.jh_quesCollect_nav /deep/ .van-nav-bar__title,
.van-ellipsis {
  font-size: 36px;
  color: #656464;
}
.jh_quesCollect_nav /deep/.van-icon,
van-icon-search {
  color: #656464;
  font-size: 36px;
}
/deep/.van-dropdown-menu__bar {
  height: 80px;
}
/deep/.van-ellipsis {
  height: 80px;
  font-size: 30px;
  line-height: 80px;
}
/deep/.van-dropdown-menu__title::after {
  border-width: 8px;
  right: -30px;
  top: 40%;
}
/deep/.van-popup,
.van-popup--top,
.van-dropdown-item__content {
  z-index: 99999999999999999999999999999999999 !important;
}
// 导航结束

// 筛选
.jh_filter {
  width: 100%;
  ul {
    width: 100%;
    li,
    .active {
      color: #eb6100;
    }
    li {
      border-bottom: 1px solid #f5f5f5;
    }
    li {
      height: 100px;
      text-align: center;
      line-height: 100px;
      font-size: 30px;
      color: #595959;
    }
  }
}
// 筛选结束

// 内容区域
.jh_quesCollect_content {
  width: 100%;
  flex: 1;
  background: #fff;
  padding: 30px;
  box-sizing: border-box;
  .jh_npMore {
    width: 100%;
    text-align: center;
    line-height: 80px;
    color: #969799;
    font-size: 24px;
  }
  ul {
    width: 100%;
    li {
      padding: 20px 30px;
      box-sizing: border-box;
      width: 690px;
      min-height: 210px;
      border-radius: 10px;
      box-shadow: 0px 0px 20px 0px rgba(10, 0, 24, 0.08);
      margin-bottom: 20px;
      div {
        font-size: 26px;
        color: #262626;
      }
      div:nth-of-type(1) {
        p {
          text-indent: 100px;
          line-height: 40px;
        }
        span:nth-of-type(1) {
          position: absolute;
          width: 100px;
          color: #0097fa;
        }
      }
      div:nth-of-type(2) {
        display: flex;
        justify-content: space-between;
        p {
          width: 150px;
          height: 54px;
          text-align: center;
          line-height: 54px;
          border: 1px solid #eb6100;
          border-radius: 50px;
          font-size: 24px;
          color: #eb6100;
        }
      }
    }
  }
}
// 内容区域结束
// 没有数据
.jh_noContent {
  width: 100%;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 320px;
    height: 320px;
  }
  p {
    font-size: 30px;
    color: #8c8c8c;
    line-height: 90px;
  }
}
// 搜索结果
.jh_keyWords {
  background: #fff;
  font-size: 26px;
  color: #8c8c8c;
  padding: 0px 30px;
  box-sizing: border-box;
  line-height: 60px;
}
</style>
