<template>
  <div class="jh_orderDetail">
    <!-- 导航区域 -->
    <div class="jh_orderDetail_nav">
      <div>
        <img src="@/assets/ret.png" alt @click="goHome" />
        <p>订单详情</p>
        <p></p>
      </div>
      <div>交易完成</div>
    </div>
    <!-- 导航结束 -->
    <div class="jh_orderDetail_cont">
      <div>
        <div>{{detail.shop_name}}</div>
        <div>
          <p>
            <span>订单编号:</span>
            <span>{{detail.order_number}}</span>
          </p>
          <p>
            <span>下单时间:</span>
            <span>{{detail.created_at*1000|timeC}}</span>
          </p>
          <p>
            <span>支付方式:</span>
            <span>虚拟币交易</span>
          </p>
        </div>
        <div>
          <p>
            <span>商品原价:</span>
            <span>
              <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
              <span>{{detail.original_price}}</span>
            </span>
          </p>
          <p>
            <span>优惠券:</span>
            <span>
              <span>–</span>
              <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
              <span>{{detail.refund_price}}</span>
            </span>
          </p>
          <p>
            <span>实付金额:</span>
            <span>
              <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
              <span>{{detail.order_price|toFixPrice}}</span>
            </span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { myOrdeDdetail } from "../../http/api";
export default {
  data() {
    return {
      title: "",
      order_id: "", //订单id
      order_type: "", //订单类型
      detail: {}
    };
  },
  created() {
    this.title = this.$route.meta.title;
    this.order_id = this.$route.query.order_id;
    this.order_type = this.$route.query.order_type;
  },
  mounted() {
    this.myOrdeDdetail();
  },
  methods: {
    //   获取订单详情信息
    myOrdeDdetail() {
      let token = localStorage.getItem("token");
      return myOrdeDdetail(token, this.order_id).then(res => {
        this.detail = res.data.data;
      });
    },
    // 返回
    goHome() {
      console.log(this.order_type)
      if (this.order_type == undefined) {
        this.$router.push({path:"/messageDetail",query:{
          message_classify:'order',
          title:'订单通知'
        }});
      } else {
        this.$router.push({
          path: "/order",
          query: {
            order_type: this.order_type
            
          }
        });
      }
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_orderDetail {
  width: 100%;
}
// 导航区域
.jh_orderDetail_nav {
  width: 100%;
  height: 220px;
  background: linear-gradient(180deg, #63b7fe, #4e7de3);
  padding: 0px 30px;
  box-sizing: border-box;
  div {
    width: 100%;
  }
  div:nth-of-type(1) {
    img {
      width: 24px;
      height: 38px;
    }
    display: flex;
    justify-content: space-between;
    height: 90px;
    align-items: center;
    color: #fff;
  }
  div:nth-of-type(2) {
    height: 130px;
    text-align: center;
    line-height: 130px;
    color: #fff;
    font-size: 30px;
  }
}
// 导航结束
.jh_orderDetail_cont {
  padding: 30px;
  box-sizing: border-box;
  > div {
    width: 100%;
    height: 460px;
    border-radius: 20px;
    background: #fff;
    padding: 0px 30px;
    box-sizing: border-box;
    div {
      width: 100%;
    }
    div:nth-of-type(1) {
      height: 100px;
      font-size: 28px;
      color: #262626;
      line-height: 100px;
    }
    div:nth-of-type(2) {
      height: 170px;
      border-top: 1px solid #f5f5f5;
      border-bottom: 1px solid #f5f5f5;
      display: flex;
      flex-direction: column;
      padding: 20px 0px;
      box-sizing: border-box;
      justify-content: space-between;
      p {
        display: flex;
        justify-content: space-between;
        font-size: 26px;
        color: #8c8c8c;
      }
    }
    div:nth-of-type(3) {
      height: 180px;
      display: flex;
      flex-direction: column;
      padding: 20px 0px;
      box-sizing: border-box;
      justify-content: space-between;
      img {
        width: 20px;
        height: 24px;
        margin: 0px 10px;
      }
      p {
        display: flex;
        justify-content: space-between;
        font-size: 26px;
        color: #8c8c8c;
        align-items: center;
      }
      p:nth-of-type(3) {
        color: #595959;
        font-size: 30px;
        img {
          width: 27px;
          height: 27px;
        }
        span {
          span {
            color: #e02020;
          }
        }
      }
    }
  }
}
</style>
