<template>
  <div class="jh_purchase">
    <!-- 导航 -->
    <div>
      <returnHome :title="title"></returnHome>
    </div>
    <!-- 导航结束 -->
    <!-- 商品信息 -->
    <div class="jh_shop">
      <!-- 标题 -->
      <div class="jh_shop_title">
        <div class="jh_shop_title_cont">
          <p>{{info.title}}</p>
          <p>授课老师:{{info.teacher_name}}</p>
        </div>
      </div>
      <!-- 标题结束 -->

      <!-- 优惠券 -->
      <div class="jh_coupon">
        <p>优惠券</p>
        <p>{{info.has_coupon===0?'无可用':''}}</p>
      </div>
      <!-- 优惠券结束 -->

      <!-- 价格 -->
      <div class="jh_price">
        <div>
          <p>商品金额</p>
          <p>
            <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
            <span>{{info.order_price|toFixPrice}}</span>
          </p>
        </div>

        <div>
          <p>优惠券金额</p>
          <p>
            <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
            <span>{{info.has_coupon}}</span>
          </p>
        </div>

        <div>
          <p>合计</p>
          <p>
            <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
            <span style="color:#e02020">{{info.price|toFixPrice}}</span>
          </p>
        </div>
      </div>
      <!-- 价格结束 -->
    </div>
    <!-- 商品信息结束 -->

    <!-- 提交订单 -->
    <div class="jh_add">
      <p>
        <span>实付价格</span>
        <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
        <span>{{info.price|toFixPrice}}</span>
      </p>
      <p @click="shopPay">提交订单</p>
    </div>
    <!-- 提交订单结束 -->
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { shopInfo, shopPay } from "../../http/api";
import Vue from "vue";
import { Dialog } from "vant";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      id: "", //id
      course_type: "", //类型
      title: "", //标题,
      checked_coupon: {}, //商品价格信息
      info: {} //商品总信息
    };
  },
  created() {
    this.title = this.$route.meta.title;
    this.id = this.$route.query.id;
    this.course_type = this.$route.query.course_type;
  },
  mounted() {
    this.shopInfo();
  },
  methods: {
    // 获取商品支付信息
    shopInfo() {
      let token = localStorage.getItem("token");
      return shopInfo(token, this.id, this.course_type).then(res => {
        this.info = res.data.data.info;
        this.checked_coupon = res.data.data.info.checked_coupon;
      });
    },
    // 支付
    shopPay() {
      let token = localStorage.getItem("token");
      return shopPay(
        token,
        this.id,
        this.course_type,
        this.checked_coupon.coupon_id
      ).then(res => {
        if (res.data.code === 200) {
          Dialog.confirm({
            title: "提示",
            message: "很抱歉，您的学习币余额不足，无法完成支付",
            confirmButtonText	:'去充值'
          })
            .then(() => {
                this.$router.push('/myMoney')
            })
            .catch(() => {
            });
        }
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_shop {
  width: 100%;
}
// 标题
.jh_shop_title {
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  .jh_shop_title_cont {
    border-radius: 10px;
    background: #fff;
    padding: 40px 30px;
    box-sizing: border-box;
    font-size: 30px;
    p {
      line-height: 50px;
    }

    p:nth-last-of-type(1) {
      color: #b7b7b7;
    }
  }
}
// 标题结束

// 优惠券
.jh_coupon {
  display: flex;
  justify-content: space-between;
  height: 95px;
  align-items: center;
  padding: 0px 30px;
  box-sizing: border-box;
  background: #fff;
  font-size: 30px;
  p:nth-last-of-type(1) {
    color: #b7b7b7;
  }
}
// 优惠券结束

// 价格
.jh_price {
  width: 100%;
  background: #fff;
  padding: 30px 0px;
  box-sizing: border-box;
  margin-top: 30px;
  > div {
    display: flex;
    justify-content: space-between;
    font-size: 28px;
    color: #b7b7b7;
    padding: 0px 30px;
    box-sizing: border-box;
    line-height: 40px;

    img {
      width: 25px;
      height: 25px;
      margin-right: 10px;
    }
  }
}
// 价格结束
.jh_add {
  position: fixed;
  bottom: 0px;
  left: 0;
  right: 0;
  height: 110px;
  background: #fff;
  display: flex;
  justify-content: space-between;
  p:nth-of-type(1) {
    width: 60%;
    img {
      width: 21px;
      height: 25px;
      margin: 0px 10px;
    }
    display: flex;
    align-items: center;
    padding: 0px 0px 0px 50px;
    box-sizing: border-box;
    span {
      font-size: 25px;
      color: #b7b7b7;
    }
    span:nth-of-type(2) {
      font-size: 40px;
      color: #e02020;
    }
  }
  p:nth-of-type(2) {
    width: 40%;
    background: #eb6100;
    color: #fff;
    text-align: center;
    line-height: 110px;
  }
}
</style>
