<template>
  <div class="jh_video">
    <div>
      <returnHome :title="title"></returnHome>
    </div>

    <div id="jh_video_play"></div>
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { getPlayToken } from "../../http/api";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      course_id: "",
      video_id: "",
      title: "",
      video_token: ""
    };
  },
  created() {
    this.course_id = this.$route.query.id;
    this.video_id = this.$route.query.video_id;
    this.title = this.$route.query.title;
  },
  mounted() {
      this.getPlayToken();
  },
  methods: {
    getPlayToken() {
      let token = localStorage.getItem("token");
      return getPlayToken(
        token,
        this.video_id,
        this.course_id,
      ).then(res => { 
        var player = new bjcPlayer("#jh_video_play", {
          token: res.data.data.token,
          definition: "low", // hign 高清 low 低清
          media: "video"
        });
        player.play(res.data.data.video_id);


      });
    }
  }
};
</script>

<style scoped lang="scss">
.jh_video {
  width: 100%;
}
#jh_video_play {
  width: 100%;
  height: 600px;
  .bjc-player-wrapper {
    width: 100%;
  }
}
</style>
