<template>
  <div class="jh_search">
    <!-- 头部 -->
    <div class="jh_search_header">
      <img src="@/assets/return.png" alt @click="goHome" />
      <div>
        <input type="text" placeholder="请输入内容" v-model="keywords" @input="keyInput" />
      </div>
      <p @click="goHome" v-if="isShow">取消</p>
      <p class="jh_s" v-else @click="search">搜索</p>
    </div>
    <!-- 头部结束 -->

    <!-- 历史记录去区域 -->
    <div v-if="histShow">
      <div class="jh_history">
        <div class="jh_history_top">
          <p>历史记录</p>
          <img src="@/assets/del.png" alt @click="resetHistory" />
        </div>
      </div>
      <!-- 历史记录去区域结束-->

      <div class="jh_histiry_cont">
        <p v-for="(value,index) in keyList" :key="index" @click="addKeywords(value,index)">{{value}}</p>
      </div>
    </div>

    <div class="jh_search_list">
      <goodClass :goodClass="goodClass"></goodClass>
    </div>
    <div v-if="nosearch" class="nosearch">
      <img src="@/assets/empty.0d284c2e.png" alt />
      <p>暂无搜索结果</p>
    </div>
  </div>
</template>

<script>
import { searchClass, searchotoCourse } from "../../http/api";
import goodClass from "../../components/msmk_index/goodClass";
export default {
  components: {
    goodClass
  },
  data() {
    return {
      name: "",
      nosearch: false,
      histShow: true,
      goodClass: [],
      source: "",
      keywords: "",
      page: 1,
      limit: 10,
      isShow: true,
      course_type: "",
      keyList: JSON.parse(localStorage.getItem("keyList")) || []
    };
  },
  created() {
    this.name = this.$route.query.name;
  },
  mounted() {},
  methods: {
    //   返回
    goHome() {
      this.$router.go(-1);
    },
    // 输入事件
    keyInput() {
      this.isShow = false;
      this.histShow = false;
      if (this.keywords == "") {
        this.isShow = true;
        this.goodClass = [];
        this.histShow = true;
        this.nosearch = false;
      }
    },
    // 搜索
    search() {
      if (this.name == "oto") {
        let token = localStorage.getItem("token");
        return searchotoCourse(
          token,
          this.page,
          this.limit,
          this.keywords
        ).then(res => {
          if (res.data.code == 200) {
            this.$router.push({
              name: "oto",
              params: { data: res.data.data, keywords: this.keywords }
            });
          }
        });
      } else if (this.name == "exam") {
        this.$router.push({
          name: "exam",
          params: { name: this.keywords }
        });
      } else if (this.name == "paperPackage") {
        this.$router.push({
          name: "paperPackage",
          params: { name: this.keywords }
        });
      } else if (this.name == "errorQuestion") {
        this.$router.push({
          name: "errorQuestion",
          params: { name: this.keywords }
        });
      } else if (this.name == "assess") {
        this.$router.push({
          name: "assess",
          params: { name: this.keywords }
        });
      } else if (this.name == "quesCollect") {
        this.$router.push({
          name: "quesCollect",
          params: { name: this.keywords }
        });
      }
      this.histShow = false;
      let token = localStorage.getItem("token");
      this.keyList.unshift(this.keywords);
      if (this.keyList.length > 5) {
        this.keyList.pop(this.keyList.length - 1);
      }
      localStorage.setItem("keyList", JSON.stringify(this.keyList));
      return searchClass(
        token,
        this.limit,
        this.page,
        this.course_type,
        this.keywords
      ).then(res => {
        if (res.data.code == 200) {
          if (res.data.data.list.length > 0) {
            this.goodClass = res.data.data.list;
          } else {
            this.nosearch = true;
          }
        }
      });
    },
    // 清空历史记录
    resetHistory() {
      localStorage.removeItem("keyList");
      this.keyList = [];
    },
    // 历史记录点击
    addKeywords(value, index) {
      this.keywords = value;
      this.keyList.splice(index, 1);
      this.isShow = false;
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_search {
  width: 100%;
  height: 100%;
  background: #fff;
  overflow: auto;
  .nosearch {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    img {
      width: 300px;
      height: 300px;
    }
    p {
      font-size: 30px;
      line-height: 100px;
    }
  }
}

// 头部
.jh_search_header {
  width: 100%;
  height: 94px;
  display: flex;
  justify-content: space-between;
  padding: 0px 40px 0px 50px;
  box-sizing: border-box;
  border-bottom: 1px solid #eaeaea;
  align-items: center;
  img {
    width: 21px;
    height: 32px;
  }
  .jh_s {
    font-size: 28px;
    color: rgb(0, 0, 0);
  }
  p {
    font-size: 28px;
    color: #d2d2d1;
  }
  input {
    width: 480px;
    height: 66px;
    border: none;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    background: #e4e7ed;
  }
  div {
    padding: 0px 0px 0px 56px;
    box-sizing: border-box;
    border-radius: 50px;
    background: #e4e7ed;
  }
  ::placeholder {
    font-size: 33px;
    color: #7b808b;
  }
}
// 头部结束
// 历史记录
.jh_history {
  width: 100%;
  padding: 35px;
  box-sizing: border-box;
  .jh_history_top {
    display: flex;
    justify-content: space-between;
    p {
      color: #333;
      font-weight: 700;
    }
    img {
      width: 30px;
      height: 30px;
    }
  }
}
// 历史记录

.jh_histiry_cont {
  padding: 0px 60px;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  p {
    background: #f7f7f7;
    color: #333;
    border-radius: 40px;
    padding: 14px 17px;
    box-sizing: border-box;
    margin: 20px 10px 0px 20px;
    font-size: 24px;
  }
}
</style>
