<template>
  <div class="jh_studyDetail">
    <!-- 导航区 -->
    <div class="jh_studyDetail_nav">
      <img src="@/assets/return.png" alt @click="goDetail" />
      <p>{{course.title}}</p>
      <img src="@/assets/day.png" alt  @click="goCalendar"/>
    </div>
    <!-- 导航结束 -->
    <!-- 进度区 -->
    <div class="jh_studyDetail_jindu">
      <p>共{{course.section_num}}课时</p>
      <div>
        <van-progress inactive :percentage="progress" pivot-text stroke-width="5px" />
      </div>

      <p>
        已学习
        <span>{{course.progress_rate}}%</span>
      </p>
    </div>
    <!-- 进度区域结束 -->
    <!-- 内容区域 -->
    <div class="jh_studyDetail_content">
      <ul>
        <router-link
          :to="{path:'/video',query:{id:course_id,video_id:value.video_id,title:course.title}}"
          tag="li"
          v-for="(value,index) in periods"
          :key="index"
        >
          <div>
            <i></i>
            <span>[回放]</span>
            <span>{{value.periods_title}}</span>
          </div>
          <div>
            <span>{{course.teacher_name}}</span>
            <span>{{value.start_play}} - {{value.end_play}}</span>
          </div>
          <div>
            <div>
              <van-progress
                inactive
                :percentage="value.progress_rate"
                pivot-text
                stroke-width="5px"
              />
            </div>
            <p>已观看{{value.progress_rate}}%</p>
          </div>
        </router-link>
      </ul>

      <div class="jh_tabbar">
        <div>
          <p v-for="(value,index) in tabbar" :key="index" @click="tabbarC(index)">
            <img :src="value.url" alt />
            <span>{{value.cont}}</span>
          </p>
        </div>
      </div>
    </div>
    <!-- 内容区域结束 -->

    <van-overlay :show="show" @click="show=!show" z-index="1">
      <div class="wrapper" @click.stop="add">
        <div>
          <span>星级:</span>
          <van-rate v-model="value" size="20" />
        </div>
        <div>
          <span>内容:</span>
          <textarea v-model="textValue"></textarea>
        </div>
        <div class="fabu">
          <p @click="addPinglun">发布</p>
        </div>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { myStudy, delStudy, dmyStudyComment } from "../../http/api";
export default {
  data() {
    return {
      textValue: "",
      value: 5,
      show: false,
      course_id: "",
      progress: "", //进度
      course: {}, //总进度信息
      periods: [], //学习视频列表信息,
      tabbar: [
        { cont: "学评论", url: require("../../assets/write.png") },
        { cont: "课程详情", url: require("../../assets/kecheng.png") },
        { cont: "移除列表", url: require("../../assets/del.png") }
      ]
    };
  },
  created() {
    this.course_id = this.$route.query.course_id;
  },
  mounted() {
    this.myStudy();
  },
  methods: {
    add() {},
    goDetail() {
      this.$router.go(-1);
    },
    // 获取数据
    myStudy() {
      let token = localStorage.getItem("token");
      return myStudy(token, this.course_id).then(res => {
        this.progress = res.data.data.course.progress_rate;
        this.course = res.data.data.course;
        this.periods = res.data.data.periods;
      });
    },
goCalendar(){
  this.$router.push('/studyCalendar')
},
    // 底部选择
    tabbarC(index) {
      let token = localStorage.getItem("token");
      if (index == 2) {
        return delStudy(token, this.course_id).then(res => {
          if (res.data.code == 200) {
            this.$toast.success("完成");
            this.$router.push("/myStudy");
          }
        });
      } else if (index == 0) {
        this.show = true;
      } else if (index == 1) {
        this.$router.push({
          path: "/classCont",
          query: {
            id: this.course_id,
            course_type: this.course_type
          }
        });
      }
    },
    // 添加评论
    addPinglun() {
      let token = localStorage.getItem("token");
      return dmyStudyComment(
        token,
        this.course_id,
        this.value,
        this.textValue
      ).then(res => {
        if (res.data.code == 200) {
          this.$toast.success("评论成功");
          this.show = false;
        } else {
          this.$toast(res.data.msg);
        }
      });
    }
  }
};
</script>

<style scoped lang='scss'>
// 弹层
.wrapper {
  position: absolute;
  top: 50%;
  margin-top: -250px;
  left: 10px;
  right: 10px;
  height: 500px;
  border-radius: 10px;
  background: #fff;
  z-index: 333333333333333333333;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  align-items: flex-start;
  padding: 80px 50px;
  box-sizing: border-box;
  .fabu {
    display: flex;
    justify-content: center;
    p {
      width: 200px;
      height: 60px;
      background: #eb6100;
      text-align: center;
      line-height: 60px;
      color: #fff;
      border-radius: 10px;
    }
  }
  div {
    width: 100%;
    display: flex;
    span {
      width: 100px;
    }
    textarea {
      width: 500px;
      height: 150px;
    }
  }
}
.jh_studyDetail /deep/.van-overlay {
  z-index: 1;
}
.jh_studyDetail {
  width: 100%;
  background: #fff;
  height: 100%;
  overflow: auto;
}
// 导航区域
.jh_studyDetail_nav {
  width: 100%;
  height: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 30px;
  box-sizing: border-box;
  border-bottom: 1px solid #f5f5f5;
  p {
    width: 70%;
    color: #595959;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  img:nth-of-type(1) {
    width: 20px;
    height: 34px;
  }
  img:nth-of-type(2) {
    width: 35px;
    height: 35px;
  }
}
// 导航区域结束
// 学习进度
.jh_studyDetail_jindu {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0px 30px;
  height: 110px;
  align-items: center;
  box-sizing: border-box;
  border-bottom: 1px solid #f5f5f5;
  div {
    width: 55%;
  }
  p {
    font-size: 28px;
    color: #595959;
  }
}
.jh_studyDetail_jindu /deep/.van-progress {
  width: 100%;
}

// 学习进度结束
// 内容区域
.jh_studyDetail_content {
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  ul {
    width: 100%;
    font-size: 24px;
    color: #8c8c8c;
    li:not(:last-of-type) {
      margin-bottom: 30px;
    }
    li {
      width: 100%;
      height: 200px;
      border: 4px solid #e9e9e9;
      border-radius: 10px;
      padding: 30px 50px;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      > div:not(:last-of-type) {
        padding-left: 30px;
        box-sizing: border-box;
        span:nth-of-type(1) {
          margin-right: 15px;
        }
      }
      div:nth-of-type(1) {
        position: relative;

        > i {
          position: absolute;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background: #ea7a2f;
          top: 0px;
          left: 0px;
        }
        span:nth-of-type(1) {
          color: #ea7a2f;
        }
        span:nth-of-type(2) {
          color: #595959;
        }
      }
      div:nth-of-type(3) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        > div {
          width: 75%;
        }
      }
    }
  }
}
// 内容区域结束

// 底部
.jh_tabbar {
  border-top: 1px solid #f5f5f5;
  position: fixed;
  right: 0;
  left: 0;
  height: 80px;
  bottom: 0;
  z-index: 9999999999999999;
  background: #fff;
  > div {
    height: 100%;
    display: flex;
    p {
      height: 100%;
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 26px;
        height: 26px;
        margin-right: 10px;
      }
      span {
        font-size: 26px;
        color: #8c8c8c;
      }
    }
  }
}
</style>
