<template>
  <div class="jh_index">
    <!-- 轮播 -->
    <banners :bannerList="bannerList"></banners>
    <!-- 轮播结束 -->

    <!-- 课程模块 -->
    <div class="jh_classNav">
      <ul>
        <router-link to="/course" tag="li">
          <img
            src="data:image/png;base64,"
            alt
          />
          <p>特色课</p>
        </router-link>
        <router-link to="/oto" tag="li">
          <img
            src="data:image/png;base64,"
            alt
          />
          <p>一对一辅导</p>
        </router-link>
        <li @click="goStudyCalendar">
           <img
            src="data:image/png;base64,"
            alt
          />
          <p>学习日历</p>
        </li>
        
      </ul>
    </div>
    <!-- 课程模块结束 -->

    <!-- 名师阵容 -->
    <div>
      <p class="jh_title">
        <span>名师阵容</span>
      </p>
      <goodTeachers :goodTeachers="goodTeachers"></goodTeachers>
    </div>
    <!-- 名师阵容结束 -->

    <!-- 精品课程 -->
    <div>
      <p class="jh_title">
        <span>精品课程</span>
      </p>
      <goodClass :goodClass="goodClass"></goodClass>
    </div>
    <!-- 精品课程结束 -->

    <!-- 推荐课程 -->
    <div>
      <p class="jh_title">
        <span>推荐课程</span>
      </p>
      <recommendClass :recommendClass="recommendClass"></recommendClass>
    </div>
    <!-- 推荐课程结束 -->

    <!-- 明星讲师 -->
    <div style="margin-bottom: 50px;">
      <p class="jh_title">
        <span>明星讲师</span>
      </p>
      <starTeachers :starTeachers="starTeachers"></starTeachers>
    </div>

    <!-- 明星讲师结束 -->
  </div>
</template>

<script>
import { getBanners, getData } from "../../http/api";
import banners from "../../components/msmk_index/banners";
import goodTeachers from "../../components/msmk_index/goodTeachers";
import goodClass from "../../components/msmk_index/goodClass";
import recommendClass from "../../components/msmk_index/recommendClass";
import starTeachers from "../../components/msmk_index/starTeachers";
export default {
  data() {
    return {
      bannerList: [],
      goodTeachers: [],
      goodClass: [],
      recommendClass: [],
      starTeachers: []
    };
  },
  //注册组件
  components: {
    banners,
    goodTeachers,
    goodClass,
    starTeachers,
    recommendClass
  },
  created() {
     // 获取数据
    this.getData();
  },
  mounted() {
    // 获取轮播图
    this.getBanners();
   
  },
  methods: {
    goStudyCalendar(){
        if(localStorage.getItem('token')===null){
          this.$router.push('/regLogin')
        }else{
          this.$router.push('/studyCalendar')
          
        }
    },
    /*     
      //    \\
        *心* 
        *好*
        *累*
      \\    //
    */
    // 获取轮播图
    getBanners() {
      return getBanners().then(res => {
        this.bannerList = res.data.data;
      });
    },
    // 获取数据
    getData() {
      let token=localStorage.getItem('token')
      return getData(token).then(res => {
        this.goodTeachers=res.data.data[0].list
        this.goodClass = res.data.data[1].list; //精品课程
        this.recommendClass = res.data.data[2].list; //推荐课程
        this.starTeachers = res.data.data[3].list; //推荐课程
      });
    }
  }
};
</script>

<style scoped lang="scss">
.jh_index {
  width:100%;
  height: 100%;
}
// title样式
.jh_title {
  width: 100%;
  font-size: 30px;
  color: #595959;
  padding: 15px 30px;
  box-sizing: border-box;
  span {
    text-indent: 20px;
    display: block;
    // padding-right: 5px;
    border-left: 5px solid #eb6100;
    box-sizing: border-box;
  }
}

// 导航
.jh_classNav {
  position: relative;
  z-index: 99999999999;
  margin-top: -30px;
  width: 100%;
  height: 200px;
  margin-bottom: 20px;
  ul {
    @extend .jh_classNav;
    display: flex;
    justify-content: space-around;
    li {
      width: 200px;
      height: 200px;
      font-size: 16px;
      color: #8c8c8c;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      box-sizing: border-box;
      padding: 40px 0px;
      background: #fff;
      border-radius: 20px;
      img {
        width: 56px;
        height: 56px;
      }
    }
  }
}
// 导航结束

</style>
