<template>
  <div class="jh_course">
    <div class="jh_course_top">
      <div>
        <div class="jh_tsk">
          <p></p>
          <p>特色课</p>
          <router-link
            to="/search"
            tag="img"
            src="data:image/png;base64,"
          ></router-link>
        </div>
      </div>
      <!-- 排序筛选 -->
      <div class="jh_sort">
        <van-dropdown-menu>
          <van-dropdown-item title="分类" ref="attrclassify">
            <div v-if="isShow">
              <ul class="jh_sort_ul_first">
                <li v-for="(item,index) in attrclassify" :key="index">
                  <p>{{item.name}}</p>
                  <ul class="jh_sort_ul_second">
                    <li
                      v-for="(val,info) in item.child"
                      :key="info"
                      @click="attrclassifyToggle(val.id)"
                      :class="classIndex==val.id-1?'jh_commonClass':'null'"
                    >{{val.name}}</li>
                  </ul>
                </li>
              </ul>
              <div class="jh_sort_btn">
                <p @click="jh_reset">重置</p>
                <p @click="jh_sure">确定</p>
              </div>
            </div>
          </van-dropdown-item>
          <van-dropdown-item title="排序" ref="sortList">
            <ul class="jh_sort_sort">
              <li
                v-for="(value,index) in sortList"
                :key="index"
                @click="sortToggle({order_by:value.order_by,index:index})"
                :class="sortIndex==index?'jh_commonSort':'null'"
              >{{value.cont}}</li>
            </ul>
          </van-dropdown-item>
          <van-dropdown-item title="筛选" ref="screenList">
            <ul class="jh_sort_screenList">
              <li
                v-for="(value,index) in screenList"
                :key="index"
                @click="screenToggle({course_type:value.course_type,is_vip:value.is_vip,index:index})"
                :class="screenIndex==index?'jh_commonClass':'null'"
              >{{value.cont}}</li>
            </ul>
          </van-dropdown-item>
        </van-dropdown-menu>
      </div>
      <!-- 排序筛选结束 -->
    </div>
    <!-- 内容区域 -->
    <div class="jh_contList">
      <goodClass :goodClass="goodClass"></goodClass>
    </div>

    <!-- 内容区域结束 -->
  </div>
</template>

<script>
import goodClass from "../../components/msmk_index/goodClass";
import { getClass, getClassData } from "../../http/api";
export default {
  components: {
    goodClass
  },
  data() {
    return {
      isShow: true, //控制下拉菜单
      attrclassify: [], //课程分类,
      classIndex: 0,
      sortIndex: 0,
      screenIndex: 0,
      //排序列表
      sortList: [
        { cont: "综合排序", order_by: 0 },
        { cont: "最新", order_by: 1 },
        { cont: "最热", order_by: 2 },
        { cont: "价格从低到低", order_by: 3 },
        { cont: "价格从高到低", order_by: 4 }
      ],
      // 筛选列表
      screenList: [
        { cont: "全部", course_type: 0, is_vip: 0 },
        { cont: "大课班", course_type: 2, is_vip: 0 },
        { cont: "小课班", course_type: 3, is_vip: 0 },
        { cont: "公开课", course_type: 4, is_vip: 0 },
        { cont: "点播课", course_type: 5, is_vip: 0 },
        { cont: "面授课", course_type: 7, is_vip: 0 },
        { cont: "音频课", course_type: 8, is_vip: 0 },
        { cont: "系统课", course_type: 9, is_vip: 0 },
        { cont: "图文课", course_type: 0, is_vip: 1 }
      ],
      goodClass: [], //特色课
      attr_val_id: "", //年级分类参数
      order_by: "", //排序
      course_type: "", //筛选课程种类
      is_vip: "", //是否是会员
      page: 1, //页====数
      limit: 10, //每页数据条数
      flag: true //开关
    };
  },
  created() {},
  mounted() {
    this.getClass();
    this.getClassData();
  },
  beforeRouteEnter: (to, from, next) => {
    next(vm => {
      window.addEventListener("scroll", vm.getMore);
    });
  },
  beforeRouteLeave(to, from, next) {
    window.removeEventListener("scroll", this.getMore);
    next();
  },
  methods: {
    // 年级分类
    attrclassifyToggle(id) {
      this.classIndex = id - 1;
      this.attr_val_id = id; //将id保存
    },
    // 年级分类结束

    // 确定
    jh_sure() {
      this.page = 1;
      this.limit = 10;
      this.getClassData(
        this.attr_val_id,
        this.order_by,
        this.course_type,
        this.is_vip
      ); //获取数据
      this.$refs.attrclassify.toggle(); //下拉菜单开关
    },
    // 确定结束

    // 重置
    jh_reset() {
      this.attr_val_id = ""; //清空id重置
      this.page = 1;
      this.limit = 10;
      this.getClassData(
        this.attr_val_id,
        this.order_by,
        this.course_type,
        this.is_vip
      ); //获取数据
      this.$refs.attrclassify.toggle(); //下拉菜单开关
    },
    // 重置结束

    // 排序
    sortToggle({ order_by, index }) {
      this.sortIndex = index; //样式切换
      this.order_by = order_by; //保存数据
      this.page = 1;
      this.limit = 10;
      let token = localStorage.getItem("token");
      this.getClassData(
        this.attr_val_id,
        this.order_by,
        this.course_type,
        this.is_vip,
        this.page,
        this.limit,
        token
      ); //获取数据
      this.$refs.sortList.toggle(); //下拉菜单开关
    },
    // 排序结束

    // 筛选
    screenToggle({ course_type, is_vip, index }) {
      this.screenIndex = index;
      this.course_type = course_type; //保存数据
      this.is_vip = is_vip; //保存数据
      this.page = 1;
      this.limit = 10;
      let token = localStorage.getItem("token");
      this.getClassData(
        this.attr_val_id,
        this.order_by,
        this.course_type,
        this.is_vip,
        token,
        this.page,
        this.limit,
        token
      ); //获取数据
      this.$refs.screenList.toggle(); //下拉菜单开关
    },
    // 筛选结束

    // 获取数据
    getClassData() {
      let token = localStorage.getItem("token");
      return getClassData(
        this.attr_val_id,
        this.order_by,
        this.course_type,
        this.is_vip,
        token,
        this.page,
        this.limit
      ).then(res => {
        if (res.data.code == 200) {
          this.goodClass = res.data.data.list;
        }
      });
    },
    // 获取数据结束

    //下拉加载
    getMore() {
      // 判断开关的属性,如果为false 则return false
      if (this.flag == false) {
        return false;
      }
      let clientHeight = document.documentElement.clientHeight; //获取html的可视高度
      let scrollTop = document.documentElement.scrollTop; //获取元素的滚动高度
      let scrollHeight = document.documentElement.scrollHeight; //获取文档的实际高度
      // console.log(clientHeight, scrollTop, scrollHeight);

      if (clientHeight + scrollTop + 170 > scrollHeight) {
        this.flag = false;
        let token = localStorage.getItem("token");
        getClassData(
          this.attr_val_id,
          this.order_by,
          this.course_type,
          this.is_vip,
          token,
          this.page,
          this.limi,
          token
        ).then(res => {
          if (res.data.code == 200) {
            this.goodClass.push(...res.data.data.list);
            this.page++;
            this.flag = true;
          }
        });
      }
    },
    // 下拉加载结束

    // 获取课程分类
    getClass() {
      getClass().then(res => {
        this.attrclassify = res.data.data.attrclassify;
      });
    }
    // 获取课程分类结束
  }
};
</script>

<style scoped lang="scss">
.jh_commonClass {
  color: #eb6100 !important;
  background: #ebeefe !important;
}
.jh_commonSort {
  color: #eb6100 !important;
}

.jh_course {
  width: 100%;
  height: 100%;
  position: relative;
  background: #f0f2f5;
}
// 头部
.jh_course_top {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 222222;
  background: #fff;
  .jh_tsk {
    width: 100%;
    display: flex;
    top: 0;
    justify-content: space-between;
    height: 90px;
    align-items: center;
    img {
      width: 40px;
      height: 40px;
    }
    color: #595959;
    font-size: 35px;
    padding: 0 20px;
    box-sizing: border-box;
  }
  .jh_tsk,
  .jh_border {
    border-bottom: 2px solid #f5f5f5;
  }
}
// 头部结束
// 排序
.jh_sort {
  height: 80px;
}
// /deep/+第三方组件的类名
/deep/.van-dropdown-menu__bar {
  height: 80px;
}
/deep/.van-ellipsis {
  height: 80px;
  font-size: 30px;
  line-height: 80px;
}
/deep/.van-dropdown-menu__title::after {
  border-width: 8px;
  right: -30px;
  top: 40%;
}
/deep/.van-popup,
.van-popup--top,
.van-dropdown-item__content {
  z-index: 99999999999999999999999999999999999 !important;
}
// 排序结束

// 内容
.jh_goodClass {
  width: 100%;
  margin-top: 170px;
  margin-bottom: 100px;

  .jh_money_img {
    width: 32px;
    height: 32px;
    border-radius: 0px;
  }
  width: 100%;
  padding: 15px 30px;
  box-sizing: border-box;
}
// 内容结束

// 课程分类
.jh_sort_ul_first {
  width: 100%;
  height: 100%;
  padding: 20px 30px 0px 20px;
  box-sizing: border-box;
  li {
    padding: 10px 0px;
    box-sizing: border-box;
  }

  p {
    font-size: 25px;
    color: #595959;
  }
  .jh_sort_ul_second {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    border-bottom: 1px solid #f5f5f5;
    padding-bottom: 15px;
    box-sizing: border-box;
    li {
      margin: 20px 10px;
      padding: 15px 5px;
      box-sizing: border-box;
      width: 22%;
      background: #f5f5f5;
      text-align: center;
      font-size: 25px;
      color: #646464;
      border-radius: 10px;
    }
  }
}
.jh_sort_btn {
  width: 100%;
  display: flex;
  padding: 10px 30px 30px 30px;
  box-sizing: border-box;
  justify-content: space-between;
  p {
    width: 48%;
    text-align: center;
    padding: 15px 0px;
    border-radius: 10px;
    border: 1px solid #f1f1f1;
    font-size: 25px;
  }
  p:nth-of-type(2) {
    border: 1px solid #eb6100;
    background: #eb6100;
    color: white;
  }
}
// 课程分类结束

// 排序
.jh_sort_sort {
  width: 100%;
  li {
    height: 100px;
    text-align: center;
    line-height: 100px;
    border-bottom: 1px solid #f5f5f5;
    font-size: 25px;
    color: #595959;
  }
}
//排序结束

// 筛选
.jh_sort_screenList {
  width: 100%;
  padding: 20px 15px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  box-sizing: border-box;
  li {
    margin: 20px 18px;
    padding: 15px 5px;
    box-sizing: border-box;
    width: 20%;
    background: #f5f5f5;
    text-align: center;
    font-size: 25px;
    color: #646464;
    border-radius: 10px;
  }
}
// 筛选结束

// 内容区域

// 内容区域结束
</style>
