<template>
  <div class="jh_error">
    <!-- 导航 -->
    <div class="jh_exam_nav">
      <van-nav-bar title="错题记录" left-arrow @click-left="onClickLeft">
        <template #right>
          <van-icon name="search" size="18" @click="goSearch" />
        </template>
      </van-nav-bar>
    </div>
    <!-- 导航结束 -->

    <!-- 筛选功能区域 -->
    <div class="jh_filter">
      <div>
        <div>
          <van-dropdown-menu>
            <van-dropdown-item title="类型不限" ref="exam_type">
              <ul class="jh_filter_list">
                <li
                  v-for="(value,index) in filterList"
                  :key="index"
                  :class="value.ques_source==ques_source?'active':''"
                  @click="filter(value.ques_source)"
                >{{value.cont}}</li>
              </ul>
            </van-dropdown-item>
          </van-dropdown-menu>
        </div>
        <router-link to="/errorList" tag="div">
          <img src="@/assets/gongu.png" alt />
          <p>巩固全部错题</p>
        </router-link>
        <div>
          <img src="@/assets/yichu.png" alt />
          <p :class="switchNum==1?'active':''" @click="removeError" v-if="switchNum==1">取消移除错题</p>
          <p @click="removeError" v-else>移除错题</p>
        </div>
      </div>
    </div>
    <!-- 筛选功能区域结束 -->

    <!-- 无数据 -->
    <div class="jh_keyWords" v-if="kShow">"{{keywords}}"的搜索结果</div>
    <div class="jh_noContent" v-if="dataList.length===0">
      <img src="@/assets/empty.0d284c2e.png" alt />
      <p>暂无错题记录</p>
    </div>
    <!-- 无数据 -->

    <!-- 数据区域 -->
    <div v-else>
      <ul class="jh_errdata">
        <li
          v-for="(value,index) in dataList"
          :key="index"
          @click="gongguerr({id:value.paper_id,time:value.created_at})"
        >
          <p>
            <span>{{value.name}}</span>
            <span>{{value.error_count}}道错题</span>
          </p>
          <p>提交时间：{{value.created_at}}</p>
          <p>
            <span></span>
            <span class="jh_gongguerr">巩固错题</span>
          </p>
        </li>
      </ul>
      <div class="jh_nomore">没有更多了</div>
    </div>
    <!-- 数据区域结束 -->
  </div>
</template>

<script>
import { errorRecord, autoDetach, unautoDetach } from "../../http/api";
import Vue from "vue";
import { Dialog } from "vant";
export default {
  data() {
    return {
      ques_source: "-1",
      keywords: "",
      filterList: [
        { cont: "全部", ques_source: "-1" },
        { cont: "套卷练习", ques_source: "3" },
        { cont: "考点专题", ques_source: "4" },
        { cont: "仿真模考试", ques_source: "5" }
      ],
      dataList: [],
      switchNum: 0
    };
  },
  created() {
    this.keywords = this.$route.params.name;
    if (this.keywords == undefined) {
      this.keywords = "";
      this.kShow = false;
    } else {
      this.kShow = true;
    }
  },
  mounted() {
    this.getErrorRecord();
  },
  methods: {
    // 去巩固
    gongguerr({ id, time }) {
      let timer = Date.parse(time) / 1000;
      this.$router.push({
        path: "/errorList",
        query: {
          id: id,
          timer: timer,
          switchNum: this.switchNum
        }
      });
    },
    //   返回
    onClickLeft() {
      this.$router.push("/practise");
    },
    // 搜索
    goSearch() {
      this.$router.push({
        path: "/search",
        query: { name: this.$route.name }
      });
    },
    // 获取错题
    getErrorRecord() {
      let token = localStorage.getItem("token");
      return errorRecord(token, this.keywords, this.ques_source).then(res => {
        this.switchNum = res.data.data.switch;
        this.dataList = res.data.data.list;
      });
    },

    // 筛选
    filter(ques_source) {
      this.ques_source = ques_source;
      this.$refs.exam_type.toggle();
      this.getErrorRecord();
    },
    // 移除错题
    removeError() {
      let token = localStorage.getItem("token");
      Dialog.confirm({
        message: "是否做对题目后自动移除该错题"
      })
        .then(() => {
          if (this.switchNum == 0) {
            return autoDetach(token).then(res => {
              if (res.data.code == 200) {
                this.$toast.success({
                    message:'已开启'
                })
                this.getErrorRecord();
              }
            });
          } else {
            return unautoDetach(token).then(res => {
              if (res.data.code == 200) {
                this.$toast.fail({
                  icon:"cross",
                  message:"已关闭"
                })
                this.getErrorRecord();
              }
            });
          }
        })
        .catch(() => {
          // on cancel
        });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_error {
  width: 100%;
  height: 100%;
  overflow: auto;
  background: #fff;
}
// 导航区域
.jh_exam_nav /deep/.van-nav-bar,
.van-hairline--bottom {
  height: 87px;
  border-bottom: 3px solid #f6f6f6;
}
.jh_exam_nav /deep/ .van-nav-bar__title,
.van-ellipsis {
  font-size: 36px;
  color: #656464;
}
.jh_exam_nav /deep/.van-icon,
van-icon-search {
  color: #656464;
  font-size: 36px;
}
// 导航结束

// 筛选区域
.jh_filter {
  width: 100%;
  > div {
    display: flex;
    height: 70px;
    > div:nth-of-type(3) {
      .active {
        color: #eb6100;
      }
    }
    > div {
      flex: 1;
      display: flex;
      align-items: center;
      font-size: 30px;
      justify-content: center;
      > div:nth-of-type(1) {
        width: 100%;
        height: 100%;
      }

      img {
        width: 30px;
        height: 30px;
      }
    }
  }
}
.jh_filter /deep/ .van-dropdown-menu__bar {
  width: 100%;
  height: 100%;
  box-shadow: 0px 0px 0px;
}
.jh_filter /deep/ .van-dropdown-menu__title {
  font-size: 30px;
  height: 100%;
  line-height: 70px;
}
.jh_filter /deep/ .van-dropdown-menu__title::after {
  border-width: 8px;
  right: -30px;
  top: 40%;
}
.jh_filter_list {
  .active {
    color: #eb6100;
  }
  li {
    height: 100px;
    text-align: center;
    line-height: 100px;
    color: #595959;
    border-bottom: 1px solid #f5f5f5;
  }
}
// 筛选区域结束

// 没有数据
.jh_noContent {
  width: 100%;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 320px;
    height: 320px;
  }
  p {
    font-size: 30px;
    color: #8c8c8c;
    line-height: 90px;
  }
}
// 搜索结果
// 数据区域
.jh_errdata {
  padding: 30px 30px 0px 30px;
  box-sizing: border-box;
  li {
    padding: 30px;
    box-sizing: border-box;
    box-shadow: 0 0 20px 0 rgba(10, 0, 24, 0.08);
    margin-bottom: 20px;
    font-size: 24px;
    color: #8c8c8c;
    > p {
      display: flex;
      justify-content: space-between;
      line-height: 60px;
    }
    .jh_gongguerr {
      width: 150px;
      height: 55px;
      border: 1px solid #4a70ff;
      border-radius: 30px;
      text-align: center;
      line-height: 55px;
      color: #4a70ff;
    }
    p:nth-of-type(1) {
      color: #262626;
      font-size: 30px;
      span:nth-of-type(2) {
        color: #4a70ff;
        font-size: 26px;
      }
    }
  }
}
// 数据区域结束
// 搜索结果
.jh_keyWords {
  font-size: 26px;
  color: #8c8c8c;
  padding: 0px 30px;
  box-sizing: border-box;
  line-height: 30px;
}
// 搜索结果结束
.jh_nomore {
  width: 100%;
  font-size: 30px;
  color: #8c8c8c;
  text-align: center;
}
</style>
