import Vue from "vue";
import Vuex from "vuex";
import createPersistedState from "vuex-persistedstate";
Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    activeIndex: 0,
    href: "",
    isShow: false
  },
  mutations: {
    indexToggle(state, payload) {
      state.activeIndex = payload;
    },
    activeIndex(state, payload) {
      state.activeIndex = payload;
    },
    share(state, { location, isShow }) {
      state.href = location;
      state.isShow = isShow;
    },
    unshow(state, payload) {
      state.isShow = payload;
    }
  },
  actions: {},
  modules: {},
  // 数据持久化
  plugins: [createPersistedState()]
});
