import request from "./request";
//封装业务的各种接口
export function getBanners() {
  return request({
    url: "/banner",
    method: "GET"
  });
}

// 获取首页数据
export function getData(token) {
  return request({
    url: "recommend/appIndex",
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      DeviceType: "H5",
      Authorization: `Bearer ${token}`
    }
  });
}

// 获取课程分类
export function getClass() {
  return request({
    url: "courseClassify",
    method: "GET"
  });
}

// 获取课程

export function getClassData(
  attr_val_id,
  order_by,
  course_type,
  is_vip,
  token,
  page,
  limit
) {
  return request({
    url: "/courseBasis?",
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      DeviceType: "H5",
      Authorization: `Bearer ${token}`
    },
    params: {
      page,
      limit,
      attr_val_id,
      order_by,
      course_type,
      is_vip
    }
  });
}

