<template>
  <div class="jh_myMoney">
    <!-- 头部导航 -->
    <div class="jh_myMoney_nav">
      <img src="@/assets/return.png" alt @click="goHome" />
      <p>{{$route.meta.title}}</p>
      <p @click="coinList" v-if="coinShow">余额明细</p>
      <p v-else></p>
    </div>
    <!-- 头部导航结束 -->
    <div v-if="coinShow">
      <div class="jh_myMoney_topbar">
        <p>我的余额</p>
        <p>{{balance}} 学习金币</p>
      </div>
      <!-- 充值列表 -->
      <div class="jh_myMoney_pay">
        <ul>
          <li
            v-for="(value,index) in list"
            :key="index"
            :class="activeIndex==index?'active':'null'"
            @click="pay({coin_id:value.id,index:index})"
          >
            <p>{{value.coin|toFixPrice}}学习币</p>
            <p>￥{{value.rmb|toFixPrice}}</p>
          </li>
        </ul>
      </div>
      <!-- 充值列表结束 -->

      <!-- 充值说明 -->
      <div class="jh_pay_say">
        <p>充值说明</p>
        <p>{{coinInfo.agreement}}</p>
      </div>
      <!-- 充值说明结束 -->

      <!-- 立即充值 -->
      <div class="jh_nowPay_null" v-if="activeIndex=='null'">立即充值</div>
      <div class="jh_nowPay_ok" v-else @click="pay_online">立即充值</div>

      <!-- 立即充值 -->
      <van-popup v-model="show" position="bottom" :style="{ height: '30%' }">
        <div class="jh_pay_online">
          <p>支付方式</p>
          <div>
            <div>
              <p>
                <input type="radio" name="pay" v-model="type" value="wx" />
              </p>
              <img src="@/assets/wechat.png" alt />
              <p>微信</p>
            </div>
            <div>
              <p>
                <input type="radio" name="pay" v-model="type" value="ali" />
              </p>
              <img src="@/assets/pay.png" alt />
              <p>支付宝</p>
            </div>
          </div>
        </div>
      </van-popup>
    </div>

    <!-- 余额明细 -->
    <div class="jh_myMoney_list" v-else>
      <ul>
        <li v-for="(value,index) in CoinList" :key="index">
          <p>
            <span>{{value.shop_name}}</span>
            <span>-{{value.change_price}}</span>
          </p>
          <p>
            <span>{{value.remaining_sum}}</span>
            <span>{{value.created_at*1000|timeB}}</span>
          </p>
        </li>
      </ul>
      <p>没有更多了</p>
    </div>
    <!-- 余额明细 -->
  </div>
</template>

<script>
import { coinBalance, coinRank, payOline, coinList } from "../../http/api";
export default {
  data() {
    return {
      coinShow: true,
      balance: "", //余额
      list: [], //充值列表
      activeIndex: "null",
      coin_id: "", //充值id
      coinInfo: {},
      show: false,
      type: "",
      page: 1,
      CoinList: [] //明细
    };
  },
  created() {},
  mounted() {
    this.coinBalance();
    this.coinRank();
  },
  methods: {
    goHome() {
      if (this.coinShow == false) {
        this.coinShow = true;
      } else {
        this.$router.push("/login");
      }
    },
    // 获取余额
    coinBalance() {
      let token = localStorage.getItem("token");
      return coinBalance(token).then(res => {
        this.balance = res.data.data.balance;
      });
    },
    // 充值列表
    coinRank() {
      let token = localStorage.getItem("token");
      return coinRank(token).then(res => {
        this.list = res.data.data.list;
        this.coinInfo = res.data.data.coinInfo;
      });
    },
    // 选择支付
    pay({ coin_id, index }) {
      this.activeIndex = index;
      this.coin_id = coin_id;
    },
    // 在线支付
    pay_online() {
      if (this.Cpay != "" && (this.coin_id != "") & (this.show == true)) {
        let token = localStorage.getItem("token");
        return payOline(token, this.coin_id, this.type).then(res => {
          if (res.data.code == 200) {
            this.$toast.success("支付成功");
            this.show = false;
          } else {
            this.$toast.fail(res.data.msg);
            this.show = false;
          }
        });
      }
      this.show = true;
    },

    // 获取明细
    coinList() {
      this.coinShow = false;
      let token = localStorage.getItem("token");
      return coinList(token, this.page).then(res => {
        console.log(res.data.data.list);
        this.CoinList = res.data.data.list;
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_myMoney {
  width: 100%;
  height: 100%;
  // 导航
  .jh_myMoney_nav {
    width: 100%;
    height: 90px;
    display: flex;
    align-items: center;
    padding: 0px 30px;
    box-sizing: border-box;
    justify-content: space-between;
    border-bottom: 1px solid #eee;
    color: #595959;
    background: #fff;
    font-size: 32px;
    img {
      width: 23px;
      height: 30px;
    }
  }
  // 导航结束
  // 学习币
  .jh_myMoney_topbar {
    padding: 0px 30px;
    box-sizing: border-box;
    background: #fff;
    height: 90px;
    width: 100%;
    display: flex;
    align-items: center;
    font-size: 30px;
    p:nth-of-type(2) {
      color: #dc692a;
      margin-left: 20px;
    }
  }
  // 学习比结束
}
//充值列表
.jh_myMoney_pay {
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  ul {
    width: 100%;
    li:not(:last-of-type) {
      margin-bottom: 30px;
    }
    li.active {
      background: #fcede2;
      border: 1px solid #eb6100;
      box-shadow: 0 6px 6px 0 rgba(235, 97, 0, 0.75);
    }
    li {
      display: flex;
      width: 100%;
      height: 110px;
      border-radius: 10px;
      background: #fff;
      justify-content: space-between;
      align-items: center;
      padding: 0px 40px;
      box-sizing: border-box;
      font-size: 32px;
      color: #333;
      p:nth-of-type(2) {
        font-weight: 500;
        color: #e90000;
      }
    }
  }
}
// 充值列表结束

// 充值说明
.jh_pay_say {
  width: 100%;
  background: #fff;
  color: #666;
  font-size: 24px;
  padding: 30px;
  box-sizing: border-box;
  margin-bottom: 100px;
  p:nth-of-type(1) {
    font-weight: bold;
    font-size: 36px;
    color: #333;
  }
}

// 立即充值
.jh_nowPay_null {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100px;
  background: #cccccc;
  color: #fff;
  text-align: center;
  line-height: 100px;
}
.jh_nowPay_ok {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100px;
  background: #eb6100;
  color: #fff;
  text-align: center;
  line-height: 100px;
  z-index: 9999999999999999999999999999;
}
// 立即充值结束
// 在线支付
.jh_pay_online {
  width: 100%;
  height: 300px;
  background: #fff;
  padding: 30px;
  box-sizing: border-box;

  margin-bottom: 60px;
  img {
    width: 40px;
    height: 40px;
    margin: 0px 20px 0px 20px;
  }
  > p {
    font-size: 35px;
    font-weight: bold;
    line-height: 60px;
  }
  > div {
    display: flex;
    flex-direction: column;
    div {
      height: 60px;
      display: flex;
      align-items: center;
    }
  }
}
// 在线支付结束

// 明细
.jh_myMoney_list {
  width: 100%;
  background: #fff;
  > p {
    text-align: center;
    font-size: 26px;
    color: #969799;
    line-height: 50px;
  }
  ul {
    width: 100%;
    padding: 0px 10px;
    box-sizing: border-box;
    li {
      width: 100%;
      height: 110px;
      border-bottom: 1px solid #eee;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 15px 0px;
      box-sizing: border-box;
      p {
        display: flex;
        justify-content: space-between;
        padding: 0px 45px;
        box-sizing: border-box;
        font-size: 30px;
        color: #333;
        span:nth-of-type(1) {
          width: 70%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
      p:nth-of-type(2) {
        color: #595959;
        font-size: 24px;
      }
    }
  }
}
// 明细结束
</style>
