<template>
  <div class="jh_questionDetail">
    <!-- 答题卡 -->
    <div class="jh_dtkcont" v-if="dtkShow">
      <!-- 导航 -->
      <div class="jh_datika_nav">
        <img src="@/assets/return.png" alt @click="dtkUshow" />
        <div>答题卡</div>
        <p></p>
      </div>
      <!-- 导航结束 -->
      <!-- 页数 -->
      <div class="jh_page">
        <p>
          <span>{{title}}</span>
        </p>
        <p>
          <span>{{page}}</span>
          <span>/{{errorList.length}}</span>
        </p>
      </div>
      <!-- 页数结束 -->

      <!-- 显示答题数量 -->
      <div class="jh_datika_cont">
        <ul>
          <li
            @click="questionToggle(index)"
            v-for="(value,index) in errorList"
            :key="index"
          >{{index+1}}</li>
        </ul>
      </div>
      <!-- 显示答题数量 -->

      <div class="jh_addquestion" @click="stop">结束巩固</div>
    </div>
    <!-- 答题卡结束 -->

    <!-- 答题内容 -->
    <div class="jh_dt">
      <!-- 导航 -->
      <div class="jh_quesDetail_nav">
        <img src="@/assets/return.png" alt @click="goHome" />
        <div>
          <img src="@/assets/datika.png" alt @click="showDatika" />
          <van-icon
            name="star"
            color="#fc5500"
            size="16px"
            @click="removecollect"
            v-if="isCollect"
          />
          <van-icon name="star-o" size="16px" @click="addcollect" v-else />
          <img src="@/assets/ji.png" alt @click="fkshow(1)" />
        </div>
      </div>
      <!-- 导航结束 -->
      <!-- 页数 -->
      <div class="jh_page">
        <p>
          <span>{{title}}</span>
        </p>
        <p>
          <span>{{page}}</span>
          <span>/{{errorList.length}}</span>
        </p>
      </div>
      <!-- 页数结束 -->
      <div class="jh_question_item">
        <div>
          <!-- 考试题 -->
          <div v-for="(value,index) in errorList" :key="index" class="jh_content">
            <div v-if="index==page-1">
              <div class="jh_top">
                <p>{{value.ques_type==1?'单选':'多选'}}</p>
                <p>{{index+1}}</p>
                <p v-html="value.ques_stem" class="jh_img"></p>
              </div>
              <ul class="jh_section">
                <li v-for="(val,info) in value.ques_option" :key="info">
                  <van-radio-group v-model="radio">
                    <van-radio :name="info*1+1" @click="addErr({id:val.id,rmid:value.id})">
                      <p>{{val.id|abcd}}丶</p>
                      <p v-html="val.content"></p>
                    </van-radio>
                  </van-radio-group>
                </li>
              </ul>
            </div>
          </div>
          <!-- 考试题结束 -->
          <!-- 切换 -->
          <div class="jh_downUp">
            <p @click="duToggle(0)">上一题</p>
            <p @click="duToggle(1)">下一题</p>
          </div>
          <!-- 切换结束 -->
          <div class="jh_downUp">
            <p @click="remove">移除错题</p>
            <p @click="looksns">查看答案</p>
          </div>
          <!-- 解析 -->
          <div v-if="asShow">
            <div class="jh_jx">
              <div>
                <p>
                  <span>正确答案:</span>
                  <span>{{right|ABCD}}</span>
                </p>
                <p>
                  <span>您的答案</span>
                  <span>{{radio|abcd}}</span>
                </p>
              </div>
              <div>
                <p>考点</p>
                <p>{{title}}</p>
              </div>
              <div>
                <p>解析</p>
                <p>暂无解析</p>
              </div>
            </div>
          </div>

          <!-- 解析结束 -->
        </div>
      </div>
    </div>
    <!-- 答题内容结束 -->

    <!-- 问题反馈 -->
    <div class="jh_questionFk" v-show="questionShow">
      <div class="jh_questionFk_nav">
        <img src="@/assets/return.png" alt @click="fkshow(2)" />
        <p>题目纠错反馈</p>
        <p></p>
      </div>
      <div>
        <p>纠结错误</p>
        <div>
          <van-radio-group v-model="question" direction="horizontal">
            <van-radio name="1">错别字</van-radio>
            <van-radio name="2">答案解析不匹配</van-radio>
            <van-radio name="3">解析错误</van-radio>
            <van-radio name="4">其他</van-radio>
          </van-radio-group>
        </div>
      </div>
      <div>
        <textarea placeholder="请输入题目纠错信息，最多不超过500个字" v-model="questionFk"></textarea>
        <p @click="correction">提交</p>
      </div>
    </div>
    <!-- 问题反馈结束 -->
  </div>
</template>

<script>
import Vue from "vue";
import { Dialog } from "vant";
import {
  collect,
  collectCancel,
  analysis,
  correction,
  errorRecordList,
  detach,
  thisErrorRecord,
  nowRemove
} from "../../http/api";
export default {
  data() {
    return {
      questionShow: false,
      questionFk: "",
      question: "",
      radio: undefined,
      page: 1,
      radioList: [],
      isCollect: false, //是否收藏
      id: "", //id
      number: "", //
      type: "", //
      count: 0,
      dtkShow: false,
      collectId: "", //收藏id
      report_id: "", //解析id
      errorList: [],
      right: "", //正确答案
      title: "", //
      asShow: false, //答案显示,
      rmid: ""
    };
  },
  created() {
    this.switchNum = this.$route.query.switchNum;
  },
  mounted() {
    this.errorRecordList();
  },
  watch: {},
  methods: {
    //   移除错题
    remove() {
      Dialog.confirm({
        message: "确定要移除本错题"
      })
        .then(() => {
          let token = localStorage.getItem("token");
          return detach(token, this.errorList[this.page - 1].id).then(res => {
            if (res.data.code == 200) {
              this.$toast.success("移除成功");
              this.errorRecordList();
            } else {
              this.$toast(res.msg);
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    // 移除错题
    looksns() {
      this.asShow = !this.asShow;
    },
    addErr({ id, rmid }) {
      this.rmid = rmid;
      if (this.switchNum == 1) {
        this.radio = id;
        this.getright();
        this.asShow = true;
        if (this.right * 1 + 1 == this.radio) {
          this.nowRemove();
        }
      } else {
        this.radio = id;
        this.getright();
        this.asShow = true;
      }
    },

    // 正确移除错题
    nowRemove() {
      let token = localStorage.getItem("token");
      return nowRemove(token, this.rmid).then(res => {
        if (res.data.code == 200) {
          this.$toast.success("答案正确,已移除");
          this.radio = "";
          this.errorRecordList();
          this.asShow = false;
        } else {
          this.$toast(res.msg);
        }
      });
    },
    // 正确移除错题
    // 上一页下一页
    duToggle(i) {
      if (i == 0) {
        if (this.page == 1) {
          this.$toast("已是最后 一页了");
          return false;
        }
        this.radio = "";
        this.asShow = false;
        this.page--;
        this.getright();
        this.collectTest();
      } else if (i == 1) {
        if (this.page > this.errorList.length - 1) {
          this.dtkShow = true;
          return false;
        } else {
          this.radio = "";
          this.asShow = false;
          this.page++;
          this.collectTest();
          this.getright();
        }
      }
    },
    // 上一页下一页

    // get正确答案
    getright() {
      this.title = this.errorList[this.page - 1].title;
      let index = this.errorList[this.page - 1].ques_option.findIndex(
        (val, info) => {
          return val.is_right == 1;
        }
      );
      this.right = index;
    },
    // get正确答案
    //   返回
    goHome() {
      this.$router.push("/errorQuestion");
    },
    // 返回结束
    // 获取错题
    errorRecordList() {
      let token = localStorage.getItem("token");
      if (this.$route.query.id == undefined) {
        return errorRecordList(token).then(res => {
          this.errorList = res.data.data;
          if (this.errorList.length == 0) {
            this.$router.push("/errorQuestion");
          }
          this.collectTest();
          this.getright();
        });
      } else {
        return thisErrorRecord(
          token,
          this.$route.query.id,
          this.$route.query.timer
        ).then(res => {
          this.errorList = res.data.data;
          if (this.errorList.length == 0) {
            this.$router.push("/errorQuestion");
          }
          this.collectTest();
          this.getright();
        });
      }
    },
    // 答题显示卡
    showDatika() {
      this.dtkShow = true;
    },
    // 答题卡隐藏
    dtkUshow() {
      this.dtkShow = false;
    },
    // 答题卡切换
    questionToggle(index) {
      this.dtkShow = false;
      this.page = index + 1;
      this.radio = this.errorList[this.page - 1];
    },

    // 检测收藏
    collectTest() {
      if (this.errorList[this.page - 1].collect_id == 0) {
        this.isCollect = false;
      } else {
        this.isCollect = true;
      }
    },
    // 检测收藏

    // 习题收藏取消收藏
    addcollect() {
      let token = localStorage.getItem("token");
      return collect(token, this.errorList[this.page - 1].ques_id).then(res => {
        if (res.data.code == 200) {
          this.$toast.success({
            message: "收藏成功"
          });
          this.errorRecordList();
        }
      });
    },
    removecollect() {
      let token = localStorage.getItem("token");
      return collectCancel(
        token,
        this.errorList[this.page - 1].collect_id
      ).then(res => {
        if (res.data.code == 200) {
          this.$toast({
            message: "取消收藏"
          });
          this.errorRecordList();
        }
      });
    },
    // 习题收藏取消收藏结束

    // 答题解析
    analysis() {
      let token = localStorage.getItem("token");
      return analysis(token, this.report_id).then(res => {
        this.analysisList = res.data.data;
      });
    },
    // 答案解析结束

    // 显示隐藏反馈卡
    fkshow(i) {
      if (i == 1) {
        this.questionShow = true;
      } else {
        this.questionShow = false;
      }
    },
    // 显示隐藏反馈卡结束
    // 问题反馈
    correction() {
      let token = localStorage.getItem("token");

      return correction(
        token,
        this.questionFk,
        this.errorList[this.page - 1].id,
        this.question
      ).then(res => {
        if (res.data.code == 200) {
          this.$toast("反馈成功");
          this.questionShow = false;
        }
      });
    },
    // 问题反馈结束
    // 巩固结束
    stop() {
      Dialog.confirm({
        title: "还有题目未做完，是否结束巩固？"
      })
        .then(() => {
          this.$router.push("/errorList");
        })
        .catch(() => {});
    }
  }
};
</script>

<style  lang="scss" scoped>
.jh_questionDetail {
  width: 100%;
  height: 100%;
  background: #fff;
  overflow: auto;
}

// 导航
.jh_quesDetail_nav {
  font-size: 26px;
  color: #8c8c8c;
  width: 100%;
  display: flex;
  align-items: center;
  align-items: center;
  height: 89px;
  border-bottom: 1px solid #f0f2f5;
  padding: 0px 40px;
  box-sizing: border-box;
  justify-content: space-between;
  > div {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    p {
      display: flex;
      align-items: center;
      line-height: 30px;
    }
  }
  > img:nth-of-type(1) {
    width: 20px;
  }
  img {
    width: 30px;
    height: 30px;
  }
}
// 导航结束
// 页数
.jh_page {
  width: 100%;
  height: 80px;
  border-bottom: 1px solid #f0f2f5;
  display: flex;
  justify-content: space-between;
  padding: 0px 30px;
  box-sizing: border-box;
  align-items: center;
  color: #595959;
  font-size: 30px;
  p:nth-of-type(2) {
    span:nth-of-type(1) {
      font-size: 40px;
      color: #0097fa;
    }
  }
}
// 页数结束

// 题
.jh_question_item {
  width: 100%;
  padding: 30px 30px 0px 30px;
  box-sizing: border-box;
  .jh_content {
    width: 100%;
    .jh_top {
      position: relative;

      > p:nth-of-type(2) {
        position: absolute;
        top: 0px;
        left: 100px;
      }
      > p:nth-of-type(1) {
        position: absolute;
        width: 85px;
        height: 40px;
        background: #0097fa;
        text-align: center;
        font-size: 24px;
        line-height: 40px;
        border-radius: 20px 0 20px 0;
        color: #fff;
        margin-right: 10px;
      }
      > p:nth-of-type(3) {
        text-indent: 150px;
      }
    }

    .jh_section {
      li {
        width: 100%;
      }
    }
  }
}
.jh_section /deep/.van-radio {
  display: flex;
  padding: 30px 0px;
  box-sizing: border-box;
  height: 100%;
}
.jh_section /deep/ .van-radio__label {
  display: flex;
  align-items: center;
}
// 题结束
.jh_downUp {
  margin: 50px 0px;
  display: flex;
  justify-content: space-between;
  p {
    width: 180px;
    height: 70px;
    border-radius: 10px;
    border: 1px solid #d9d9d9;
    text-align: center;
    line-height: 70px;
    color: #595959;
    font-size: 26px;
  }
}

// 答题卡
.jh_dtkcont {
  width: 100%;
  height: 100%;
  position: relative;
  .jh_addquestion {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100px;
    text-align: center;
    line-height: 100px;
    background-color: #0097fa;
    color: #fff;
  }
  .jh_datika_nav {
    display: flex;
    justify-content: space-between;
    padding: 0px 30px;
    box-sizing: border-box;
    height: 90px;
    border-bottom: 1px solid #f0f2f5;
    align-items: center;
    img {
      width: 22px;
      height: 35px;
    }
  }
  .jh_datika_cont {
    ul {
      padding-top: 30px;
      box-sizing: border-box;
      display: flex;
      width: 100%;
      flex-wrap: wrap;
      .noUndefinded {
        background-color: #0097fa;
        border: 1px solid #0097fa;
        color: #fff;
      }

      li {
        margin: 0px 25px 30px 25px;
        width: 90px;
        height: 90px;
        border: 1px solid #eaeaea;
        border-radius: 50%;
        text-align: center;
        line-height: 90px;
      }
    }
  }
}
// 答题卡结束
// 解析
.jh_jx {
  border-top: 5px solid #f0f2f5;
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  font-size: 30px;
  color: #666;
  div {
    display: flex;
    height: 100px;
    align-items: center;
  }
  div:nth-of-type(1) {
    justify-content: space-between;
    p {
      span {
        margin-right: 15px;
      }
      span:nth-of-type(2) {
        color: #22bfa7;
      }
    }
  }
  div:nth-of-type(2) {
    p:nth-of-type(2) {
      background: linear-gradient(30deg, #0097fa, #0096f9);
      border-radius: 20px;
      padding: 5px 10px;
      font-size: 25px;
      color: #fff;
      margin-left: 10px;
    }
  }
  div:nth-of-type(3) {
    p:nth-of-type(2) {
      font-size: 25px;
      margin-left: 10px;
    }
  }
}
// 解析结束

// 问题反馈
.jh_questionFk {
  background: #fff;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999999999999999999999999999999999999999999999999999999999999999999;
  .jh_questionFk_nav {
    display: flex;
    height: 90px;
    align-items: center;
    justify-content: space-between;
    padding: 0px 30px;
    box-sizing: border-box;
    border-bottom: 1px solid #f0f2f5;
    img {
      width: 20px;
    }
  }
  > div:nth-of-type(2) {
    padding: 30px;
    box-sizing: border-box;
    font-size: 30px;
    color: #595959;
    border-bottom: 20px solid #f0f2f5;
  }
  > div:nth-of-type(3) {
    padding: 30px;
    box-sizing: border-box;
    textarea {
      width: 100%;
      height: 400px;
      border: 1px solid #d9d9d9;
      border-radius: 15px;
      font-size: 30px;
      padding: 30px;
      box-sizing: border-box;
      margin-bottom: 260px;
    }
    p {
      width: 100%;
      height: 90px;
      text-align: center;
      line-height: 90px;
      border: 1px solid #0097fa;
      color: #0097fa;
    }
  }
}

.jh_questionFk /deep/.van-radio {
  display: flex;
  padding: 30px 0px;
  box-sizing: border-box;
  height: 100%;
}
.jh_questionFk /deep/ .van-radio__label {
  display: flex;
  align-items: center;
}
.jh_questionFk /deep/.van-radio--horizontal {
  margin-right: 50px;
}

// 问题反馈结束
</style>
