<template>
  <div class="jh_oto_plan">
    <!-- 头部 -->
    <div class="jh_teacherCont_header">
      <div>
        <p @click="onClickLeft">
          <img src="../../assets/ret.png" alt />
        </p>
        <p>{{this.$route.meta.title}}</p>
        <p></p>
      </div>
    </div>
    <!-- 头部结束 -->

    <!-- 讲师信息 -->
    <div class="jh_goodTeachers">
      <ul>
        <li v-for="(value,index) in teachersCont" :key="index">
          <div>
            <div>
              <img :src="value.avatar" alt />
              <p>
                <span>
                  {{value.teacher_name}}
                  <span class="jh_level">{{value.level_name}}</span>
                </span>
                <span>{{value.sex==0?'男':'女'}} {{value.teach_age}}年教龄</span>
              </p>
              <router-link :to="{path:'/teacherCont',query:{id:`${value.id}`}}" tag="p">查看详情</router-link>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <!-- 讲师信息结束 -->
    <!-- 选择时间 -->
    <div class="jh_ckecktime">
      <p></p>
      <p>选择时间</p>
      <p>(北京时间)</p>
    </div>
    <!-- 选择时间结束 -->

    <div>
      <van-tabs>
        <van-tab v-for="(value,index) in weekdayList" :key="index">
          <template #title>
            <p>{{value}}</p>
            <p>{{weekDateList[index]}}</p>
          </template>
          <!-- 预约列表 -->
          <div v-if="otoCourseList.length>0">没找到数据</div>
          <!-- 预约列表结束 -->
          <!-- 没预约 -->
          <div class="jh_noMiss" v-else>
            <img src="@/assets/empty.0d284c2e.png" alt />
            <p>暂无信息</p>
          </div>
          <!-- 没预约结束 -->
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import { getTeachers, otoTime, otoTimeNext } from "../../http/api";
export default {
  data() {
    return {
      id: "",
      teachersCont: [],
      weekDateList: [], //时间表
      otoCourseList: [], //课程表
      weekdayList: [
        "周一",
        "周二",
        "周三",
        "周四",
        "周五",
        "周六",
        "周日",
        "周一",
        "周二",
        "周三",
        "周四",
        "周五",
        "周六",
        "周日"
      ]
    };
  },
  created() {
    this.id = this.$route.query.id;
  },
  mounted() {
    this.getTeachers();
    this.otoTime();
  },
  methods: {
    //   返回
    onClickLeft() {
      this.$router.go(-1);
    },

    // 获取教师信息
    getTeachers() {
      return getTeachers(this.id).then(res => {
        this.teachersCont.push(res.data.data.teacher);
      });
    },

    // 获取时间表
    otoTime() {
      let token = localStorage.getItem("token");
      let toDay = new Date();
      let n = this.weekdayList[toDay.getDay()];
      return otoTime(token, this.id).then(res => {
        this.weekDateList = res.data.data.weekDateList;
        this.otoCourseList = res.data.data.otoCourseList;

        return otoTimeNext(token, this.id).then(res => {
          if (res.data.code == 200) {
            this.weekDateList.push(...res.data.data.weekDateList);
            let arr = [];
            this.weekDateList.forEach(value => {
              arr.push(value.replace("月", "/", "").replace("日", ""));
            });
            this.weekDateList = arr;
          } else {
            let arr = [];
            this.weekDateList.forEach(value => {
              console.log(value);
              arr.push(value.replace("月", "/", "").replace("日", ""));
            });
            this.weekDateList = arr;
          }
        });

      });
    }
  }
};
</script>

<style scoped lang='scss'>
