  p {
    font-size: 30px;
    color: #595959;
    line-height: 100px;
  }

  img {
    width: 280px;
    height: 300px;
  }
}
.jh_oto_plan {
  width: 100%;
}
// 头部
.jh_teacherCont_header {
  width: 100%;
  height: 200px;
  background: linear-gradient(180deg, #63b7fe, #4e7de3);
}
// 导航
.jh_teacherCont_header > div {
  width: 100%;
  height: 85px;
  display: flex;
  justify-content: space-between;
  padding: 0px 30px;
  box-sizing: border-box;
