  display: flex;
  flex-direction: column;
  margin-top: -125px;
  > ul {
    width: 100%;
    > li {
      > div {
        background: #fff;
        display: flex;
        justify-content: space-between;
        padding: 0px 30px 20px 30px;
        box-sizing: border-box;
        border-radius: 20px;
        flex-direction: column;
        ul {
          display: flex;
          li {
            background: #ffe4d3;
            color: #ea7a2f;
            font-family: PingFangSC-Regular;
            text-align: center;
            width: 145px;
            height: 50px;
            line-height: 50px;
            border-radius: 30px;
            font-size: 24px;
            margin: 0px 20px 20px 0px;
          }
        }
        div {
          height: 140px;
          display: flex;
          align-items: center;

          img {
            width: 85px;
            height: 85px;
            border-radius: 50%;
          }
          p:nth-of-type(2) {
            width: 150px;
            height: 60px;
            border-radius: 50px;
            font-size: 24px;
            font-family: PingFangSC-Regular;
            font-weight: 400;
            color: #eb6100;
            text-align: center;
            line-height: 60px;
            border: 1px solid #eb6100;
          }
          p:nth-of-type(1) {
