        }
      }
    }
  }
}
// 教师信息结束

// 选择时间
.jh_ckecktime {
  width: 100%;
  height: 100px;
  padding: 0px 20px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  p {
    color: #595959;
  }
  p:nth-of-type(1) {
    width: 6px;
    height: 36px;
