  }
  p:nth-of-type(2) {
    font-size: 32px;
    margin: 0px 25px;
  }
  p:nth-of-type(3) {
    font-size: 28px;
  }
}
// 选择时间结束

.jh_oto_plan /deep/ .van-tabs__wrap--scrollable {
  width: 95%;
  height: 145px;
  border-radius: 10px;
  margin: 0 auto;
}
.jh_oto_plan /deep/ .van-tab__text {
