<template>
  <div class="jh_exam">
    <!-- 导航 -->
    <div class="jh_exam_nav">
      <van-nav-bar title="仿真考试" left-arrow @click-left="onClickLeft">
        <template #right>
          <van-icon name="search" size="18" @click="goSearch" />
        </template>
      </van-nav-bar>
    </div>
    <!-- 导航结束 -->
    <!-- 筛选 -->
    <div class="jh_filter">
      <van-dropdown-menu>
        <van-dropdown-item title="考试类型" ref="exam_type">
          <ul>
            <li
              v-for="(value,index) in exam_typeList"
              :key="index"
              :class="examIndex==index?'active':'null'"
              @click="exam({index:index,exam_type:value.exam_type})"
            >{{value.cont}}</li>
          </ul>
        </van-dropdown-item>
        <van-dropdown-item title="考试状态" ref="start_status">
          <ul>
            <li
              v-for="(value,index) in start_statusList"
              :key="index"
              :class="startIndex==index?'active':'null'"
              @click="start({index:index,start_status:value.start_status})"
            >{{value.cont}}</li>
          </ul>
        </van-dropdown-item>
        <van-dropdown-item title="参考状态" ref="done_status">
          <ul>
            <li
              v-for="(value,index) in done_statusList"
              :key="index"
              :class="doneIndex==index?'active':'null'"
              @click="done({index:index,done_status:value.done_status})"
            >{{value.cont}}</li>
          </ul>
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>
    <!-- 筛选结束 -->
    <div class="jh_keyWords"  v-if="kShow">"{{name}}"的搜索结果</div>
    <div class="jh_noContent" v-if="list.length===0">
        <img src="@/assets/empty.0d284c2e.png" alt="">
        <p>请稍候，模考正在赶来的路上啦</p>
    </div>
  </div>
</template>

<script>
import { packageSimulation } from "../../http/api";
export default {
  data() {
    return {
        kShow:false,
      //考试类型
      exam_typeList: [
        { cont: "全部", exam_type: 0 },
        { cont: "模拟考试", exam_type: 1 },
        { cont: "正式考试", exam_type: 2 }
      ],
      examIndex: 0,
      exam_type: 0,
      // 考试状态
      start_statusList: [
        { cont: "全部", start_status: 0 },
        { cont: "未开始", start_status: 2 },
        { cont: "进行中", start_status: 1 },
        { cont: "已结束", start_status: 3 }
      ],
      startIndex: 0,
      start_status: 0,
      //   参考状态
      done_statusList: [
        { cont: "全部", done_status: 0 },
        { cont: "已做", done_status: 1 },
        { cont: "未作", done_status: 2 }
      ],
      doneIndex: 0,
      done_status: 0,
      name: "",
      list: [] //数据列表
    };
  },
  created() {
      
      this.name=this.$route.params.name;
      if(this.name==undefined){
        this.name=''
          this.kShow=false
      }else{
          this.kShow=true
      }
  },
  mounted() {
      this.getData()
  },
  methods: {
    //   返回
    onClickLeft() {
      this.$router.push('/practise');
    },
    // 搜索
    goSearch() {
      this.$router.push({
        path: "/search",
        query: { name: this.$route.name }
      });
    },
    // 初始数据
    getData(){
        let token=localStorage.getItem('token')
        return packageSimulation(
        token,
        this.exam_type,
        this.done_status,
        this.start_status,
        this.name
      ).then(res => {
        if (res.data.code === 200) {
          this.list = res.data.data.list;
        }
      });
    },
    // 考试类型
    exam({ index, exam_type }) {
      let token = localStorage.getItem("token");
      this.examIndex = index;
      this.exam_type = exam_type;
      return packageSimulation(
        token,
        this.exam_type,
        this.done_status,
        this.start_status,
        this.name
      ).then(res => {
        if (res.data.code === 200) {
          this.list = res.data.data.list;
          this.$refs.exam_type.toggle();
        }
      });
    },
    // 开始状态
    start({ index, start_status }) {
      let token = localStorage.getItem("token");
      this.startIndex = index;
      this.start_status = start_status;
      return packageSimulation(
        token,
        this.exam_type,
        this.done_status,
        this.start_status,
        this.name
      ).then(res => {
        if (res.data.code === 200) {
          this.list = res.data.data.list;
       
          this.$refs.start_status.toggle();

        }
      });
    },
    // 参考状态
    done({ index, done_status }) {
      let token = localStorage.getItem("token");
      this.doneIndex = index;
      this.done_status = done_status;
      return packageSimulation(
        token,
        this.exam_type,
        this.done_status,
        this.start_status,
        this.name
      ).then(res => {
        if (res.data.code === 200) {
          this.list = res.data.data.list;
          this.$refs.done_status.toggle();
        }
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_exam{
  width:100%;
  height:100%;
  background: #fff;
}
// 导航区域
.jh_exam_nav /deep/.van-nav-bar,
.van-hairline--bottom {
  height: 87px;
  border-bottom: 3px solid #f6f6f6;
}
.jh_exam_nav /deep/ .van-nav-bar__title,
.van-ellipsis {
  font-size: 36px;
  color: #656464;
}
.jh_exam_nav /deep/.van-icon,
van-icon-search {
  color: #656464;
  font-size: 36px;
}
// 导航结束
/deep/.van-dropdown-menu__bar {
  height: 80px;
}
/deep/.van-ellipsis {
  height: 80px;
  font-size: 30px;
  line-height: 80px;
}
/deep/.van-dropdown-menu__title::after {
  border-width: 8px;
  right: -30px;
  top: 40%;
}
/deep/.van-popup,
.van-popup--top,
.van-dropdown-item__content {
  z-index: 99999999999999999999999999999999999 !important;
}
// 筛选
.jh_filter {
  width: 100%;
  ul {
    width: 100%;
    li,
    .active {
      color: #eb6100;
    }
    li {
      border-bottom: 1px solid #f5f5f5;
    }
    li {
      height: 100px;
      text-align: center;
      line-height: 100px;
      font-size: 30px;
      color: #595959;
    }
  }
}
// 筛选结束

// 没有数据
.jh_noContent{
    width:100%;
    height:400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    img{
        width:320px;
        height:320px;
    }
    p{
        font-size: 30px;
        color:#8c8c8c;
        line-height: 90px;
    }
}
// 搜索结果
.jh_keyWords{
    font-size: 26px;
    color:#8c8c8c;
    padding:0px 30px;
    box-sizing: border-box;
    line-height: 60px;

}
</style>
