<template>
  <div class="jh_record">
    <!-- 头部 -->
    <div class="jh_record_header">
      <div class="jh_record_title">约课记录</div>
      <div class="jh_record_tab">
        <div
          v-for="(value,index) in tab"
          :key="index"
          :class="activeIndex==index?'active':''"
          @click="classToggle(index)"
        >
          {{value.name}}
          <span v-show="activeIndex==index"></span>
        </div>
      </div>
    </div>
    <!-- 头部结束 -->
    <!-- 内容区域 -->
    <div class="jh_record_content">
      <div>
        <div>
          <img src="../../assets/no-message.8d3ca5af.png" alt />
          <p>请登入后查看相关内容</p>
          <p @click="goregLogin" v-if="isShow">登入/注册</p>
          <p v-if="!isShow" @click="nowYuyue">立即约课</p>
        </div>
      </div>
    </div>
    <!-- 内容区域结束 -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: "",//约课/\注册显示切换
      activeIndex: 0,
      tab: [{ name: "待上课" }, { name: "已上课" }, { name: "已取消" }]
    };
  },
  created() {},
  mounted() {
    this.getToken();
  },
  methods: {
    //   切换
    classToggle(index) {
      this.activeIndex = index;
    },
    // 登入
    goregLogin() {
      this.$router.push("/regLogin");
    },
    // 获取token
    getToken() {
      let token = localStorage.getItem("token");
      if (token == null) {
        this.isShow = true;
      } else {
        this.isShow = false;
      }
    },
    // 立即约课
    nowYuyue(){
      this.$router.push('/oto')
    },
  }
};
</script>

<style scoped lang='scss'>
.jh_record {
  height: 100%;
  background: #f0f2f5;
  display: flex;
  flex-direction: column;
}
// 头部
.jh_record_header {
  background: #fff;
  > .jh_record_title {
    height: 80px;
    text-align: center;
    line-height: 80px;
    font-size: 35px;
    color: #595959;
    border-bottom: 1px solid #f5f5f5;
  }
  > .jh_record_tab {
    height: 80px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    > .active {
      color: #eb6100;
    }
    > div {
      height: 100%;
      font-size: 30px;
      color: #7d7e80;
      line-height: 80px;
      flex: 1;
      text-align: center;
      position: relative;
      > span {
        position: absolute;
        width: 20%;
        border-bottom: 1px solid #eb6100;
        height: 0px;
        bottom: 0;
        left: 50%;
        margin-left: -10%;
      }
    }
  }
}
// 头部结束

// 内容
.jh_record_content {
  flex: 1;
  //   background: gold;
  > div {
    height: 100%;
    > div {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 180px;
      justify-content: space-between;
      height: 630px;
      box-sizing: border-box;
      img {
        width: 370px;
        height: 270px;
      }
      p:nth-of-type(1) {
        color: #b7b7b7;
        font-size: 25px;
      }
      p:nth-of-type(2) {
        color: #fff;
        font-size: 28px;
        background: #eb6100;
        padding: 5px 25px;
        box-sizing: border-box;
      }
    }
  }
}
// 内容结束
</style>
