<template>
  <div class="jh_teacherCont">
    <!-- 头部 -->
    <div class="jh_teacherCont_header">
      <div>
        <p @click="onClickLeft">
          <img src="../../assets/ret.png" alt />
        </p>
        <p>{{this.$route.meta.title}}</p>
        <p></p>
      </div>
    </div>
    <!-- 头部结束 -->

    <!-- 讲师信息 -->
    <div class="jh_goodTeachers">
      <ul>
        <li v-for="(value,index) in teachersCont" :key="index">
          <div>
            <div>
              <img :src="value.avatar" alt />
              <p>
                <span>
                  {{value.teacher_name}}
                  <span class="jh_level">{{value.level_name}}</span>
                </span>
                <span>{{value.sex==0?'男':'女'}} {{value.teach_age}}年教龄</span>
              </p>
              <p @click="collect" v-if="flag==2">关注</p>
              <p @click="collect" v-else>已关注</p>
            </div>
            <ul>
              <li v-for="(val,info) in value.tag_content" :key="info">{{val}}</li>
            </ul>
          </div>
        </li>
      </ul>
    </div>
    <!-- 讲师信息结束 -->

    <!-- 具体信息 -->
    <div class="jh_teacherCont_cont">
      <van-tabs v-model="active">
        <van-tab title="讲师介绍">
          <ul class="jh_teacher_jieshao">
            <li>
              <p v-for="(value,index) in teacher" :key="index">
                <span>{{value.attr_name}}</span>
                <span v-for="(val,info) in value.attr_value" :key="info">{{val.attr_value_name}}</span>
              </p>
              <p>
                <span>老师介绍</span>
                <span>{{teacherInfo_intro}}</span>
              </p>
            </li>
          </ul>
        </van-tab>
        <van-tab title="主讲课程">
          <div>
            <goodClass :goodClass="goodClass"></goodClass>
          </div>
        </van-tab>
        <van-tab title="学员评价">
          <div v-if="studentTag.length>0">
            <div class="jh_tagCont">
              <ul :class="isShow==true?'jh_tag_small':'jh_tag'" class="jh_tag">
                <P v-if="isShow" @click="down">﹀</P>
                <P v-else @click="up">︿</P>
                <li
                  v-for="(value,index) in studentTag"
                  :key="index"
                >{{value.title}}({{value.count}})</li>
              </ul>
            </div>
            <div class="jh_studentInfo">
              <ul>
                <li v-for="(value,index) in studentInfo" :key="index">
                  <div>
                    <img :src="value.avatar" alt />
                    <van-icon
                      color="#ea7a2f"
                      :name="value.grade>info?'star':'star-o'"
                      v-for="(val,info) in 5"
                      :key="info"
                      size="13px"
                    />
                  </div>
                  <div>
                    <ul class="jh_staudent_tag">
                      <li v-for="(value,index) in studentTag" :key="index">{{value.title}}</li>
                    </ul>
                  </div>
                  <div>{{value.content}}</div>
                </li>
              </ul>
            </div>
          </div>

          <div class="jh_noTag" v-else>
            <img src="../../assets/empty.0d284c2e.png" alt />
            <p>暂无评价</p>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <!-- 具体信息结束 -->
    <!-- 立即预约 -->
    <div class="jh_appointment" @click="nowOrder">立即预约</div>
    <!-- 立即预约结束 -->
  </div>
</template>

<script>
import { Collect, teacherCollect } from "../../http/api";
import goodClass from "../../components/msmk_index/goodClass";
export default {
  components: {
    goodClass
  },
  props: [
    "teachersCont",
    "goodClass",
    "studentTag",
    "studentInfo",
    "teacherInfo_intro",
    "teacher",
    "id"
  ],
  data() {
    return {
      cIsshow: true,
      isShow: true,
      active: 0,
      flag: "",
      page: 1, //
      limit: 10 //
    };
  },
  created() {},
  mounted() {
    this.findCollect();
  },
  methods: {
    up() {
      this.isShow = true;
    },
    down() {
      this.isShow = false;
    },
    //   返回
    onClickLeft() {
      this.$router.go(-1);
    },
    // 关注
    collect() {
      let token = localStorage.getItem("token");
      return teacherCollect(token, this.id).then(res => {
        if (res.data.code == 200) {
          this.findCollect();
          if (this.flag == 2) {
            this.$toast.success({
              message: "关注成功"
            });
          } else {
            this.$toast.success({
              message: "已取消"
            });
          }
        }
      });
    },
    // 查看状态
    findCollect() {
      let token = localStorage.getItem("token");
      return Collect(token, this.id).then(res => {
        // console.log(res.data.data.flag);
        this.flag = res.data.data.flag; //1已关注 2取消关注
      });
    },
    // 立即预约
    nowOrder() {
      this.$router.push({
        path: "/otoPlan",
        query: {
          id: this.id
        }
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_teacherCont {
  width: 100%;
  height: 100%;
}
// 头部
.jh_teacherCont_header {
  width: 100%;
