  background: linear-gradient(180deg, #63b7fe, #4e7de3);
}
// 导航
.jh_teacherCont_header > div {
  width: 100%;
  height: 85px;
  display: flex;
  justify-content: space-between;
  padding: 0px 30px;
  box-sizing: border-box;
  font-size: 40px;
  color: #fff;
  align-items: center;
}
// 导航结束

// 头部结束
// 教师信息
.jh_goodTeachers {
  width: 100%;
  padding: 15px 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin-top: -125px;
  > ul {
    width: 100%;
    margin-bottom: 30px;

    > li {
      > div {
        background: #fff;
        display: flex;
        justify-content: space-between;
        padding: 0px 30px 20px 30px;
        box-sizing: border-box;
