            font-family: PingFangSC-Regular;
            text-align: center;
            width: 145px;
            height: 50px;
            line-height: 50px;
            border-radius: 30px;
            font-size: 24px;
            margin: 0px 20px 20px 0px;
          }
        }
        div {
          height: 140px;
          display: flex;
          align-items: center;

          img {
            width: 85px;
            height: 85px;
            border-radius: 50%;
          }
          p:nth-of-type(2) {
            width: 130px;
            height: 60px;
            background: #ebeefe;
            border-radius: 50px;
            font-size: 24px;
            font-family: PingFangSC-Regular;
            font-weight: 400;
            color: #eb6100;
            text-align: center;
            line-height: 60px;
          }
          p:nth-of-type(1) {
            width: 60%;
            flex: 1;
            display: flex;
            padding: 0 0 0 20px;
            box-sizing: border-box;
            flex-direction: column;
            .jh_level {
              font-size: 20px;
              color: #ea7a2f;
            }
            span:nth-of-type(1) {
              margin-bottom: 10px;
            }
            span:nth-of-type(2) {
              color: #b7b7b7;
              font-size: 24px;
            }
          }
        }
      }
    }
  }
}
// 教师信息结束

// 具体信息
.jh_teacherCont_cont {
  background: #fff;
  width: 100%;
}
.jh_teacherCont_cont /deep/.van-tabs__wrap {
  height: 88px;
}
.jh_teacherCont_cont /deep/.van-tabs__nav--line {
