  margin-bottom: 100px;
  li {
    p {
      display: flex;
      justify-content: space-between;
      margin-top: 25px;
      line-height: 80px;
      span:nth-of-type(1) {
        width: 25%;
        text-align: center;
      }
      span:nth-of-type(2) {
        width: 75%;
        text-align: left;
        padding: 0px 20px 0px 0px;
        box-sizing: border-box;
      }
    }
    p:nth-last-of-type(1) {
      line-height: 65px;
    }
  }
}
// 具体信息结束
