}
// 立即预约
// 暂无评价
.jh_noTag {
  width: 100%;
  height: 425px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 30px 0px;
  box-sizing: border-box;
  img {
    width: 300px;
  }
  p {
    font-size: 28px;
    color: #8c8c8c;
  }
}
// 暂无评价结束
</style>
