<template>
  <div class="jh_exam">
    <!-- 导航 -->
    <div class="jh_exam_nav">
      <van-nav-bar title="测评记录" left-arrow @click-left="onClickLeft">
        <template #right>
          <van-icon name="search" size="18" @click="goSearch" />
        </template>
      </van-nav-bar>
    </div>
    <!-- 导航结束 -->
    <!-- 筛选 -->
    <div class="jh_filter">
      <van-dropdown-menu>
        <van-dropdown-item title="类型不限" ref="exam_type">
          <ul>
            <li
              v-for="(value,index) in filterList"
              :key="index"
              :class="type==value.type?'active':''"
              @click="Toggle({i:0,type:value.type})"
            >{{value.cont}}</li>
          </ul>
        </van-dropdown-item>
        <van-dropdown-item title="时间" ref="start_status">
          <ul>
            <li
              v-for="(value,index) in timeList"
              :key="index"
              :class="search_type==value.search_type?'active':''"
              @click="Toggle({i:1,type:value.search_type})"
            >{{value.cont}}</li>
          </ul>
        </van-dropdown-item>
      </van-dropdown-menu>
    </div>
    <!-- 筛选结束 -->
    <div class="jh_keyWords" v-if="kShow">"{{name}}"的搜索结果</div>
    <!-- 数据区域 -->
    <div>
      <ul class="jh_data">
        <li v-for="(value,index) in dataList" :key="index">
          <p>
            <span>{{value.package_name}}</span>
            <span>{{value.wrong_count}}道错题</span>
          </p>
          <p>
            <span>答题时长0分{{value.report_time_long}}秒</span>
            <span>正确率{{value.correct_rate}}%</span>
          </p>
          <p>提交时间：{{value.created_at*1000|timeC}}</p>
          <p>
            <span
              class="jh_gongguerr"
              style="margin-right:10px;"
              @click="jiexi({id:value.package_id,report_id:value.report_id})"
            >查看分析</span>
            <span class="jh_gongguerr" @click="testAgain(value.package_id)">重新练习</span>
          </p>
        </li>
      </ul>
    </div>
    <!-- 数据区域结束 -->

    <div class="jh_noContent" v-if="dataList.length==0">
      <img src="@/assets/empty.0d284c2e.png" alt />
      <p>该时段您没有留下测评记录哦！</p>
    </div>
  </div>
</template>

<script>
import { examStatistics } from "../../http/api";
export default {
  data() {
    return {
      kShow: false,
      name: "",
      filterList: [
        { cont: "全部", type: "all" },
        { cont: "套卷练习", type: "practice" },
        { cont: "考点专题", type: "simulation" },
        { cont: "考点专练", type: "point" }
      ],
      timeList: [
        { cont: "全部", search_type: "all" },
        { cont: "今天", search_type: "today" },
        { cont: "七天", search_type: "week" }
      ],
      type: "all",
      search_type: "all",
      dataList: []
    };
  },
  created() {
    this.name = this.$route.params.name;
    if (this.name == undefined) {
      this.name = "";
      this.kShow = false;
    } else {
      this.kShow = true;
    }
  },
  mounted() {
    this.getAssess();
  },
  methods: {
    //   返回
    onClickLeft() {
      this.$router.push("/practise");
    },
    // 搜索
    goSearch() {
      this.$router.push({
        path: "/search",
        query: { name: this.$route.name }
      });
    },
    Toggle({ i, type }) {
      if (i == 0) {
        this.type = type;
        this.$refs.exam_type.toggle();
        this.getAssess();
      } else if (i == 1) {
        this.search_type = type;
        this.$refs.start_status.toggle();
        this.getAssess();
      }
    },

    // 获取测评数据
    getAssess() {
      let token = localStorage.getItem("token");
      return examStatistics(token, this.type, this.search_type, this.name).then(
        res => {
          this.dataList = res.data.data.list;
          // console.log(this.dataList);
        }
      );
    },
    // 重新练习
    testAgain(id) {
      this.$router.push({
        path: "/questionDetail",
        query: {
          id: id,
          number: 8,
          type: "Point"
        }
      });
    },
    jiexi({id, report_id}) {
      this.$router.push({
        path: "/fenxi",
        query: {
          id: id,
          report_id: report_id,
          number: 15,
          type: "Point"
        }
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_exam {
  width: 100%;
  height: 100%;
  background: #fff;
  overflow: auto;
}
.jh_exam {
  width: 100%;
  height: 100%;
  background: #fff;
}
// 导航区域
.jh_exam_nav /deep/.van-nav-bar,
.van-hairline--bottom {
  height: 87px;
  border-bottom: 3px solid #f6f6f6;
}
.jh_exam_nav /deep/ .van-nav-bar__title,
.van-ellipsis {
  font-size: 36px;
  color: #656464;
}
.jh_exam_nav /deep/.van-icon,
van-icon-search {
  color: #656464;
  font-size: 36px;
}
// 导航结束
/deep/.van-dropdown-menu__bar {
  height: 80px;
}
/deep/.van-ellipsis {
  height: 80px;
  font-size: 30px;
  line-height: 80px;
}
/deep/.van-dropdown-menu__title::after {
  border-width: 8px;
  right: -30px;
  top: 40%;
}
/deep/.van-popup,
.van-popup--top,
.van-dropdown-item__content {
  z-index: 99999999999999999999999999999999999 !important;
}
// 筛选
.jh_filter {
  width: 100%;
  ul {
    width: 100%;
    li,
    .active {
      color: #eb6100;
    }
    li {
      border-bottom: 1px solid #f5f5f5;
    }
    li {
      height: 100px;
      text-align: center;
      line-height: 100px;
      font-size: 30px;
      color: #595959;
    }
  }
}
// 筛选结束

// 没有数据
.jh_noContent {
  width: 100%;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 320px;
    height: 320px;
  }
  p {
    font-size: 30px;
    color: #8c8c8c;
    line-height: 90px;
  }
}
// 搜索结果
.jh_keyWords {
  font-size: 26px;
  color: #8c8c8c;
  padding: 0px 30px;
  box-sizing: border-box;
  line-height: 60px;
}
// 数据区域
.jh_data {
  padding: 30px 30px 0px 30px;
  box-sizing: border-box;
  li {
    padding: 30px;
    box-sizing: border-box;
    box-shadow: 0 0 20px 0 rgba(10, 0, 24, 0.08);
    margin-bottom: 20px;
    font-size: 24px;
    color: #8c8c8c;
    > p {
      display: flex;
      justify-content: space-between;
      line-height: 60px;
    }
    .jh_gongguerr {
      width: 150px;
      height: 55px;
      border: 1px solid #4a70ff;
      border-radius: 30px;
      text-align: center;
      line-height: 55px;
      color: #4a70ff;
    }
    p:nth-of-type(1) {
      color: #262626;
      font-size: 30px;
      span:nth-of-type(2) {
        color: #4a70ff;
        font-size: 26px;
      }
    }
    p:nth-of-type(4) {
      justify-content: flex-end;
    }
  }
}
</style>
