<template>
  <div class="jh_feedback">
    <!-- 导航 -->
    <div>
      <returnHome :title="title"></returnHome>
    </div>
    <div class="jh_feedback_cont">
      <textarea class="jh_cont" v-model="feedBack" :maxlength="num" placeholder="请输入您的意见"></textarea>
      <p class="jh_max">{{feedBack.length}}/500</p>
    </div>
    <div class="add" @click="addFeed">提交</div>
    <!-- 导航结束 -->
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { addfeedback } from "../../http/api";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      title: "",
      feedBack: "", //意见
      num: 500
    };
  },
  created() {
    this.title = this.$route.meta.title;
  },
  mounted() {},
  methods: {
    addFeed() {
      let token = localStorage.getItem("token");
      return addfeedback(token, this.feedBack).then(res => {
        if (res.data.code === 200) {
          this.$toast("反馈成功");
          setTimeout(() => {
            this.$router.push("/login");
          }, 2000);
        }
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_feedback {
  width: 100%;
  height: 100%;
  background: #fff;
}
.jh_feedback_cont {
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  .jh_cont {
    width: 100%;
    height: 330px;
    padding: 20px;
    box-sizing: border-box;
    font-size: 26px;
  }
  p {
    font-size: 28px;
    color: #b7b7b7;
    text-align: right;
  }
}
.add {
  position: fixed;
  bottom: 480px;
  left: 30px;
  right: 30px;
  height: 90px;
  border-radius: 10px;
  background: #eb6100;
  line-height: 90px;
  text-align: center;
  color: #fff;
}
</style>
