<template>
  <div class="jh_options">
    <div class="jh_nav">
      <returnHome :title="title"></returnHome>
    </div>
    <router-link to="/setPass" tag="div" class="jh_setPass">
      <p>设置密码</p>
      <p>></p>
    </router-link>
    <div class="jh_outLogin">
      <p @click="outLogin">退出登入</p>
    </div>
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
export default {
  components: {
    returnHome
  },
  data() {
    return {};
  },
  created() {
    this.title = this.$route.meta.title;
  },
  mounted() {},
  methods: {
    // 退出登入
    outLogin() {
      localStorage.removeItem("token");
      if (localStorage.getItem("token") == null) {
        this.$router.push("/index");
      }
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_options {
  width: 100%;
  .jh_setPass {
    height: 130px;
    align-items: center;
    width: 100%;
    background: #fff;
    display: flex;
    justify-content: space-between;
    padding: 0px 30px;
    box-sizing: border-box;
    margin-top: 20px;
    font-size: 28px;
    color: #595959;
    p:nth-of-type(2) {
      font-size: 40px;
      color: #b7b7b7;
    }
  }
  .jh_outLogin {
    width: 100%;
    background: #fff;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    p {
      width: 690px;
      height: 90px;
      background: #eb6100;
      color: #fff;
      border-radius: 10px;
      text-align: center;
      line-height: 90px;
      font-size: 30px;
    }
  }
}
</style>
