<template>
  <div class="jh_message">
    <!-- 导航 -->
    <div>
      <returnHome :title="title"></returnHome>
    </div>
    <!-- 导航结束 -->
    <div class="jh_message_cont">
      <ul>
        <router-link
          :to="{path:'/messageDetail',query:{message_classify:value.message_classify,title:value.title}}"
          v-for="(value,index) in list"
          :key="index"
          tag="li"
        >
          <div>
            <img :src="value.url" alt />
          </div>
          <div>
            <p>{{value.title}}</p>
            <p>{{value.content==''?'暂无消息':value.content}}</p>
          </div>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { classifyMessage } from "../../http/api";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      title: "消息通知",
      list: [
        {
          title: "课程通知",
          url: require("../../assets/a.png"),
          message_classify: "course",
          content: ""
        },
        {
          title: "系统通知",
          url: require("@/assets/b.png"),
          message_classify: "system",
          content: ""
        },
        {
          title: "订单通知",
          url: require("@/assets/c.png"),
          message_classify: "order",
          content: ""
        },
        {
          title: "约课通知",
          url: require("@/assets/a.png"),
          message_classify: "oto",
          content: ""
        },
        {
          title: "考试通知",
          url: require("@/assets/e.png"),
          message_classify: "exam",
          content: ""
        }
      ],
      messageList: []
    };
  },
  created() {},
  mounted() {
    this.classifyMessage();
  },
  methods: {
    //   获取消息
    classifyMessage() {
      let token = localStorage.getItem("token");
      return classifyMessage(token).then(res => {
        this.messageList = res.data.data;
        this.list.forEach((value, index) => {
          this.messageList.forEach((val, info) => {
            if (value.message_classify === val.message_classify) {
              value.content = val.content;
            }
          });
        });
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_message {
  width: 100%;
  height: 100%;
  background: #fff;
}
.jh_message_cont {
  width: 100%;
  ul {
    width: 100%;
    padding: 0px 30px;
    box-sizing: border-box;
    li {
      display: flex;
      align-items: center;
      padding: 30px 0px;
      box-sizing: border-box;
      border-bottom: 1px solid #f5f5f5;
      color: #595959;
      font-size: 26px;
      div {
        img {
          width: 85px;
          height: 85px;
        }
        margin-right: 30px;
        p:nth-of-type(1) {
          margin-bottom: 20px;
          font-size: 30px;
        }
      }
    }
  }
}
</style>
