<template>
  <div class="jh_coupon">
    <div>
      <returnHome :title="title"></returnHome>
    </div>
    <div class="jh_coupon_nav">
      <van-tabs>
        <van-tab :title="value.name" v-for="(value,index) in nav" :key="index">
          <div slot="title" @click="navToggle(value.status)">{{value.cont}}</div>
          <div class="jh_order_cont"  v-if="couponList.length>0"></div>
          <div class="jh_noOrder" v-else>
            <img src="@/assets/empty.0d284c2e.png" alt />
            <p>暂无优惠券</p>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { getCoupon } from "../../http/api";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      status: 0,
      title: "我的优惠券",
      nav: [
        { cont: "未使用", status: "0" },
        { cont: "已经使用", status: "1" },
        { cont: "已过期", status: "2" }
      ],
      couponList:[],//优惠券数据
    };
  },
  created() {},
  mounted() {
      this.getCoupon()
  },
  methods: {
    navToggle(status) {
      this.status = status;
      this.getCoupon()
    },
    getCoupon(){
        let token=localStorage.getItem('token')
        return getCoupon(token,this.status).then(res=>{
            this.couponList=res.data.data
        })
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_coupon{
    width:100%;
    height:100%;
}
// 导航区域
.jh_coupon_nav {
  width: 100%;
  height:100%;
  background: #fff;
}
.jh_coupon_nav /deep/.van-tab--active {
  color: #eb6100;
}
.jh_coupon_nav /deep/.van-tabs__wrap,
.van-tabs__wrap--scrollable {
  height: 90px;
  border-bottom: 3px solid #f5f5f5;
  box-sizing: border-box;
}
.jh_coupon_nav /deep/.van-tabs__wrap--scrollable .van-tabs__nav--complete {
  padding: 0px;
}
.jh_coupon_nav /deep/.van-tab__text {
  font-size: 32px;
  height: 90px;
  line-height: 90px;
}
// 导航区域结束
.jh_noOrder {
  width: 100%;
  height: 100%;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 300px;
    height: 300px;
  }
  p {
    font-size: 30px;
    color: #8c8c8c;
    line-height: 80px;
  }
}
</style>
