<template>
  <div class="jh_info">
    <!-- 导航区 -->
    <div>
      <returnHome :title="title"></returnHome>
    </div>
    <!-- 导航区结束 -->

    <div class="jh_user_cont">
      <ul>
        <!-- 头像 -->
        <li @click="toggle(1)">
          <p>头像</p>
          <p>
            <img :src="user.avatar" alt />
            <span class="jh_right">></span>
          </p>
        </li>
        <!-- 头像结束 -->

        <!-- 昵称 -->
        <li @click="toggle(7)">
          <p>昵称</p>
          <p>
            <span>{{user.nickname}}</span>
            <span class="jh_right">></span>
          </p>
        </li>
        <!-- 昵称结束 -->

        <!-- 手机号 -->
        <li>
          <p>手机号</p>
          <p>
            <span>{{user.mobile}}</span>
            <span class="jh_right">></span>
          </p>
        </li>
        <!-- 手机号结束 -->
        <!-- 性别 -->
        <li @click="toggle(6)">
          <p>性别</p>
          <p>
            <span>{{user.sex===0?'男':'女'}}</span>
            <span class="jh_right">></span>
          </p>
        </li>
        <!-- 性别结束 -->

        <!-- 出生日期 -->
        <li @click="toggle(2)">
          <p>出生日期</p>
          <p>
            <span>{{user.birthday==='0'?'请选择':user.birthday}}</span>
            <span class="jh_right">></span>
          </p>
        </li>
        <!-- 出生日期结束 -->

        <!-- 所在城市 -->
        <li @click="toggle(3)">
          <p>所在城市</p>
          <p>
            <span>{{user.province_name===''?'请选择':`${user.province_name}, ${user.city_name}, ${user.district_name}`}}</span>
            <span class="jh_right">></span>
          </p>
        </li>
        <!-- 所在城市结束 -->

        <!-- 学科 -->
        <li @click="toggle(5)">
          <p>学科</p>
          <p>
            <span>{{xk}}</span>
            <span class="jh_right">></span>
          </p>
        </li>
        <!-- 学科结束 -->

        <!-- 年级 -->
        <li @click="toggle(4)">
          <p>年级</p>
          <p>
            <span>{{nj}}</span>
            <span class="jh_right">></span>
          </p>
        </li>
        <!-- 年级结束 -->
      </ul>
      <!-- 头像弹出 -->
      <van-popup v-model="show" position="bottom">
        <!-- 拍照 -->
        <ul class="photo" v-if="showIndex==1">
          <li>
            拍照
            <input
              type="file"
              accept="image/*"
              capture="camera"
              id="takePhoto"
              @change="takePhoto"
            />
          </li>
          <li>
            从手机相册中选择
            <input type="file" accept="image/*" id="takePhoto" @change="takePhoto" />
          </li>
          <li @click="show=!show">取消</li>
        </ul>
        <!-- 拍照结束 -->

        <!-- 地址 -->
        <div v-else-if="showIndex==3">
          <van-area
            title="标题"
            :area-list="areaList"
            @change="change"
            @confirm="sure"
            @cancel="cancel"
          />
        </div>

        <!-- 地址结束 -->

        <!-- 时间 -->
        <div v-else-if="showIndex==2">
          <van-datetime-picker
            v-model="currentDate"
            type="date"
            title="选择年月日"
            :min-date="minDate"
            :max-date="maxDate"
            @confirm="sure"
            @cancel="cancel"
          />
        </div>

        <div v-else-if="showIndex==4">
          <van-picker
            show-toolbar
            :columns="njList"
            @confirm="sure"
            @cancel="cancel"
            value-key="name"
          />
        </div>
        <div v-else-if="showIndex==5">
          <van-picker
            show-toolbar
            :columns="list"
            @confirm="sure"
            @cancel="cancel"
            value-key="attr_name"
          />
        </div>
        <div v-else-if="showIndex==6">
          <van-picker
            show-toolbar
            :columns="sexList"
            @confirm="sure"
            @cancel="cancel"
            value-key="name"
          />
        </div>
        <div v-else-if="showIndex==7">
          <!-- <van-field v-model="name" label="文本" /> -->
          <van-form @submit="sure">
            <van-field
              v-model="name"
              label="用户名"
              placeholder="用户名"
              :rules="[{ required: true, message: '请填写用户名' }]"
            />
            <div style="margin: 16px;">
              <van-button round block type="info" native-type="submit">提交</van-button>
            </div>
          </van-form>
        </div>

        <!-- 时间结束 -->
      </van-popup>
      <!-- 头像弹出结束 -->
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import returnHome from "../../components/return/return";
import {
  userInfo,
  userXue,
  takePhoto,
  pushPhoto,
  province,
  city,
  district,
  updateUser,
  updateBirthday,
  updateNJ,
  updateXK,
  updateSex,
  updateNickname
} from "../../http/api";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      name: "",
      njid: "",
      xkid: "",
      showIndex: 1, //显示id
      show: false,
      title: "", //标题,
      user: {}, //个人信息,
      niList: [], //年纪列表
      xkList: [], //学科列表
      nj: "", //年级
      xk: "请选择", //学科,
      path: "请选择",
      provinceList: [], //省地址
      provinceId: "110000", //省id
      //   cityList: [], //市地址
      cityId: "110100", //市id，
      //   districtList: [], //区地址
      minDate: new Date(1960, 0, 1),
      maxDate: new Date(2020, 10, 1),
      currentDate: new Date(),
      birthday: "",
      // 城市列表
      areaList: {
        province_list: {},
        city_list: {},
        county_list: {}
      },
      // 学科
      list: [
        {
          attr_id: 2,
          attr_val_id: 7,
          attr_name: "语文"
        },
        {
          attr_id: 2,
          attr_val_id: 8,
          attr_name: "数学"
        },
        {
          attr_id: 2,
          attr_val_id: 9,
          attr_name: "英语"
        },
        {
          attr_id: 2,
          attr_val_id: 12,
          attr_name: "物理"
        },
        {
          attr_id: 2,
          attr_val_id: 13,
          attr_name: "化学"
        },
        {
          attr_id: 2,
          attr_val_id: 24,
          attr_name: "生物"
        },
        {
          attr_id: 2,
          attr_val_id: 25,
          attr_name: "政治"
        },
        {
          attr_id: 2,
          attr_val_id: 26,
          attr_name: "信息技术"
        }
      ],
      sexList: [
        { name: "男", sex: "0" },
        { name: "女", sex: "1" }
      ]
    };
  },
  created() {
    this.title = this.$route.meta.title;
  },
  mounted() {
    this.userInfo();
    this.province();
    this.userXl();
  },
  methods: {
    // 获取个人信息
    userInfo() {
      let token = localStorage.getItem("token");
      return userInfo(token).then(res => {
        this.user = res.data.data;
        let info = res.data.data.attr.findIndex((value, index) => {
          return value.attr == "年级";
        });
        this.nj = res.data.data.attr[info].attr_value;
        this.njid = res.data.data.attr[info].attr_val_id;
        let inf = res.data.data.attr.findIndex((value, index) => {
          return value.attr == "学科";
        });
        this.xk = res.data.data.attr[inf].attr_value;
        this.xkid = res.data.data.attr[inf].attr_val_id;
      });
    },
    // 学历信息
    userXl() {
      let token = localStorage.getItem("token");
      return userXue(token).then(res => {
        this.njList = res.data.data[0].value;
        this.xkList = res.data.data[1].vaule;
      });
    },
    // 拍照
    takePhoto(e) {
      let formdata = new FormData();
      formdata.append("file", e.target.files[0]);
      let token = localStorage.getItem("token");
      return takePhoto(token, formdata).then(res => {
        if (res.data.code != 200) {
          this.$toast(res.data.msg);
          this.show = false;
          return false;
        } else {
          this.path = res.data.data.path;
          return pushPhoto(token, this.path).then(res => {
            this.userInfo();
            this.show = false;
          });
        }
      });
    },
    // 获取地址
    province() {
      let token = localStorage.getItem("token");
      return province(token).then(res => {
        let obj = {};
        res.data.data.forEach(res => {
          obj[res.id] = res.area_name;
        });
        this.areaList.province_list = obj;

        return city(token, this.provinceId).then(res => {
          let obj1 = {};
          res.data.data.forEach(res => {
            obj1[res.id] = res.area_name;
          });
          this.areaList.city_list = obj1;

          return district(token, this.cityId).then(res => {
            let obj3 = {};
            res.data.data.forEach(res => {
              obj3[res.id] = res.area_name;
            });
            this.areaList.county_list = obj3;
          });
        });
      });
    },

    // 地址滑动
    change(e, value, index) {
      let token = localStorage.getItem("token");
      if (index == 0) {
        return city(token, value[index].code).then(res => {
          let obj1 = {};
          res.data.data.forEach(res => {
            obj1[res.id] = res.area_name;
          });
          this.areaList.city_list = obj1;
          return district(token, res.data.data[0].id).then(res => {
            let obj3 = {};
            res.data.data.forEach(res => {
              obj3[res.id] = res.area_name;
            });
            this.areaList.county_list = obj3;
          });
        });
      } else if (index == 1) {
        return district(token, value[1].code).then(res => {
          let obj3 = {};
          res.data.data.forEach(res => {
            obj3[res.id] = res.area_name;
          });
          this.areaList.county_list = obj3;
        });
      }
    },

    // 切换
    toggle(index) {
      this.show = true;
      this.showIndex = index;
    },
    // 确定
    sure(e) {
      let token = localStorage.getItem("token");
      if (this.showIndex === 2) {
        let data = new Date(e);
        this.birthday =
          data.getFullYear() +
          "-" +
          (data.getMonth() + 1) +
          "-" +
          data.getDate();

        return updateBirthday(token, this.birthday).then(res => {
          if (res.data.code == 200) {
            this.userInfo();
            this.show = false;
          }
        });
      } else if (this.showIndex == 3) {
        return updateUser(token, e[0].code, e[1].code, e[2].code).then(res => {
          if (res.data.code == 200) {
            this.userInfo();
            this.show = false;
          }
        });
      } else if (this.showIndex === 4) {
        return updateNJ(token, e.id, this.xkid).then(res => {
          if (res.data.code == 200) {
            this.userInfo();
            this.show = false;
          }
        });
      } else if (this.showIndex === 5) {
        return updateXK(token, this.njid, e.attr_val_id).then(res => {
          if (res.data.code == 200) {
            this.userInfo();
            this.show = false;
          }
        });
      } else if (this.showIndex === 6) {
        return updateSex(token, e.sex).then(res => {
          if (res.data.code == 200) {
            this.userInfo();
            this.show = false;
          }
        });
      } else if (this.showIndex === 7) {
        return updateNickname(token, this.name).then(res => {
          if (res.data.code === 200) {
            this.userInfo();
            this.show = false;
          }
        });
      }
    },
    // 取消
    cancel() {
      this.show = false;
    }
  }
};
</script>

<style scoped lang='scss'>
// 内容区域
.jh_user_cont {
  margin-top: 20px;
  background: #fff;
  width: 100%;
  ul {
    width: 100%;
    padding: 0px 30px;
    box-sizing: border-box;
    li {
      width: 100%;
      height: 110px;
      border-bottom: 1px solid #f5f5f5;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 30px;
      color: #595959;
      p:nth-of-type(2) {
        color: #8c8c8c;
        height: 100%;
        display: flex;
        align-items: center;
      }
      img {
        width: 75px;
        height: 75px;
        border-radius: 50%;
      }
      .jh_right {
        font-size: 34px;
        color: #b7b7b7;
        margin-left: 40px;
      }
    }
  }
}
// 内容区域结束
.jh_user_cont /deep/.van-ellipsis {
  font-size: 30px;
}
.jh_user_cont /deep/.van-picker__toolbar {
  height: 90px;
}
.jh_user_cont /deep/.van-picker__confirm,
.jh_user_cont /deep/.van-picker__cancel {
  font-size: 30px;
}
.jh_user_cont /deep/.van-field {
  margin: 150px;
}
.jh_user_cont /deep/.van-picker__title {
  font-size: 30px;
  height: 90px;
  line-height: 90px;
}
.jh_user_cont /deep/ .van-field {
  margin: 0px;
  margin: 100px 0px;
  height: 90px;
}
.jh_user_cont /deep/ .van-field__label {
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 100px;
}
.jh_user_cont /deep/ .van-field__control {
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
}
.jh_user_cont /deep/.van-button--round {
  height: 80px;
  font-size: 30px;
}
.jh_user_cont .photo {
  width: 100%;
  height: 300px;
  background: #fff;
  margin-bottom: 40px;
  li {
    position: relative;
    width: 100%;
    display: block;
    text-align: center;
    line-height: 100px;
    input {
      position: absolute;
      opacity: 0;
      left: 0;
      top: 0;
    }
  }
}
</style>
