<template>
  <div class="jh_goodTeachers">
    <ul>
      <li v-for="(value,index) in goodTeachers" :key="index" @click="goDetail(value.teacher_id)">
        <img :src="value.teacher_avatar" alt />
        <p>
          <span>{{value.teacher_name}}</span>
          <span>{{value.introduction}}</span>
        </p>
      </li>
    </ul>
    <van-popup v-model="show">
      <div class="jh_login">
        <img src="@/assets/x.png" alt class="jh_x" @click="xxx" />
        <img src="@/assets/feiji.decaf161.png" alt class="jh_imgLogin" />
        <p>赶紧登录一下吧</p>
        <p>立即预约一对一辅导，浏览更多视频课程~</p>
        <p @click="goLogin">立即登录</p>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  props: ["goodTeachers"],
  data() {
    return {
      show: false
    };
  },
  created() {},
  mounted() {},
  methods: {
    xxx() {
      this.show = false;
    },
    goDetail(id) {
      if (localStorage.getItem("token") === null) {
        this.show = true;
      } else {
        this.$router.push({
          path: "/teacherCont",
          query: { id,}
        });
      }
    },

    goLogin(){
      this.$router.push('/regLogin')
    }
  }
};
</script>

<style scoped lang='scss'>
