  ul {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    li:nth-last-of-type(1) {
      margin-bottom: 0px;
    }
    li {
      background: #fff;
      height: 150px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0px 10px;
      box-sizing: border-box;
      margin-bottom: 30px;
      border-radius: 20px;
      img {
        width: 100px;
        height: 100px;
        border-radius: 50%;
      }
      p {
        width: 60%;
        flex: 1;
        display: flex;
        padding: 0 0 0 20px;
        box-sizing: border-box;
        flex-direction: column;
        span {
          overflow: hidden; /*超出部分隐藏*/
