          white-space: nowrap;
          color: #595959;
          font-size: 30px;
        }
        span:nth-of-type(2) {
          color: #b7b7b7;
          font-size: 20px;
        }
      }
    }
  }
}
// 名师 明星讲师结束

// 登入验证
.jh_login {
  width: 100%;
  height: 700px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  .jh_x {
    position: absolute;
