<template>
  <div class="jh_pointSearch">
    <div class="jh_search">
      <input type="text" placeholder="请输入题库名称关键词" v-model="bank" @input="goSearch" />
      <img src="@/assets/csdn-sou.png" alt @click="goSearch" />
      <router-link to="/point" tag="p">取消</router-link>
    </div>

    <div class="jh_noSearch" v-if="list.length==0">
      <img src="@/assets/empty@2x.a2fa524d.png" alt />
      <p>没有搜索到相关内容</p>
    </div>
    <div class="jh_pointSearch_list" v-else>
      <ul>
        <li v-for="(value,index) in list" :key="index" @click="goPoint(value.id)">{{value.name}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import { searchBank } from "../../http/api";
export default {
  data() {
    return {
      bank: "",
      list: []
    };
  },
  created() {
    this.bank = this.$route.params.keyWords;
  },

  mounted() {
      this.goSearch()
  },
  methods: {
    goSearch() {
      if (this.bank === "") {
        this.list = [];
      } else {
        let token = localStorage.getItem("token");
        return searchBank(token, this.bank).then(res => {
          this.list = res.data.data;
        });
      }
    },
    goPoint(id) {
      this.$router.push({
        path: "/point",
        query: { classifyId: 0, classify_id: id }
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_pointSearch {
  width: 100%;
  height: 100%;
  background: #fff;
}

// 搜索区域
.jh_search {
  width: 100%;
  height: 130px;
  border-bottom: 1px solid #eaeaea;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  position: relative;
  > p {
    font-size: 28px;
    color: #323233;
  }
  input {
    width: 600px;
    height: 90px;
    border-radius: 50px;
    border: none;
    background: #f0f2f5;
    text-indent: 30px;
    color: #b7b7b7;
    font-size: 26px;
    margin-right: 10px;
  }

  img {
    position: absolute;
    width: 50px;
    height: 50px;
    right: 130px;
    top: 40px;
  }
  ::placeholder {
    color: #b7b7b7;
    font-size: 26px;
  }
}
// 搜索区域结束

// 列表内容渲染
.jh_pointSearch_list {
  width: 100%;
  ul {
    width: 100%;
    li {
      width: 100%;
      height: 75px;
      text-indent: 30px;
      border-bottom: 1px solid #eaeaea;
      color: #595959;
      line-height: 75px;
      font-size: 30px;
    }
  }
}
// 列表内容结束
.jh_noSearch {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-top: 190px;
  align-items: center;
  img {
    width: 140px;
    height: 190px;
    margin-bottom: 10px;
  }
  p {
    font-size: 30px;
    color: #999;
  }
}
</style>
