<template>
  <div class="jh_questionDetail">
    <!-- 答题卡 -->
    <div class="jh_dtkcont" v-if="dtkShow">
      <!-- 导航 -->
      <div class="jh_datika_nav">
        <img src="@/assets/return.png" alt @click="dtkUshow" />
        <div>答题卡</div>
        <p></p>
      </div>
      <!-- 导航结束 -->
      <!-- 页数 -->
      <div class="jh_page">
        <p>
          <span v-for="(value,index) in testTypeList" :key="index">{{value}}</span>
        </p>
        <p>
          <span>{{questionList.length}}</span>
          <span>/{{questionList.length}}</span>
        </p>
      </div>
      <!-- 页数结束 -->
      <!-- 显示答题数量 -->
      <div class="jh_datika_cont">
        <ul>
          <li
            @click="questionToggle(index)"
            v-for="(value,index) in analysisList"
            :key="index"
            :class="value.is_right==1?'unRight':'Ringht'"
          >{{index+1}}</li>
        </ul>
      </div>
      <!-- 显示答题数量 -->
    </div>
    <!-- 答题卡结束 -->

    <!-- 答题内容 -->
    <div class="jh_dt" v-else>
      <!-- 导航 -->
      <div class="jh_quesDetail_nav">
        <img src="@/assets/return.png" alt @click="goHome" />
        <div>
          <p>
            <span></span>
            <span></span>
          </p>

          <img src="@/assets/datika.png" alt @click="showDatika" />
          <van-icon
            name="star"
            color="#fc5500"
            size="16px"
            @click="removecollect"
            v-if="isCollect"
          />
          <van-icon name="star-o" size="16px" @click="addcollect" v-else />
          <img src="@/assets/ji.png" alt @click="fkshow(1)" />
        </div>
      </div>
      <!-- 导航结束 -->
      <!-- 页数 -->
      <div class="jh_page">
        <p>
          <span v-for="(value,index) in testTypeList" :key="index">{{value}}</span>
        </p>
        <p>
          <span>{{page}}</span>
          <span>/{{questionList.length}}</span>
        </p>
      </div>
      <!-- 页数结束 -->
      <div class="jh_question_item">
        <div>
          <!-- 考试题 -->
          <div v-for="(value,index) in questionList" :key="index" class="jh_content">
            <div v-if="page==index+1">
              <div class="jh_top">
                <p>{{value.ques_type==1?'单选':'多选'}}</p>
                <p>{{page}}丶</p>
                <p v-html="value.ques_stem" class="jh_img"></p>
              </div>

              <ul class="jh_section">
                <li v-for="(val,info) in value.ques_option" :key="info">
                  <van-radio-group v-model="radio">
                    <van-radio :name="info*1+1" @click="radio=val.id">
                      <p>{{val.id|abcd}}丶</p>
                      <p v-html="val.content"></p>
                    </van-radio>
                  </van-radio-group>
                </li>
              </ul>
            </div>
          </div>
          <!-- 考试题结束 -->
          <!-- 切换 -->
          <div class="jh_downUp">
            <p @click="duToggle(0)">上一题</p>
            <p @click="duToggle(1)">下一题</p>
          </div>
          <!-- 切换结束 -->
        </div>
      </div>
    </div>
    <!-- 答题内容结束 -->

    <!-- 问题反馈 -->
    <div class="jh_questionFk" v-show="questionShow">
      <div class="jh_questionFk_nav">
        <img src="@/assets/return.png" alt @click="fkshow(2)" />
        <p>题目纠错反馈</p>
        <p></p>
      </div>
      <div>
        <p>纠结错误</p>
        <div>
          <van-radio-group v-model="question" direction="horizontal">
            <van-radio name="1">错别字</van-radio>
            <van-radio name="2">答案解析不匹配</van-radio>
            <van-radio name="3">解析错误</van-radio>
            <van-radio name="4">其他</van-radio>
          </van-radio-group>
        </div>
      </div>
      <div>
        <textarea placeholder="请输入题目纠错信息，最多不超过500个字" v-model="questionFk"></textarea>
        <p @click="correction">提交</p>
      </div>
    </div>
    <!-- 问题反馈结束 -->

    <!-- 解析 -->

    <div v-show="number==15" v-for="(value,index) in analysisList" :key="index">
      <div class="jh_jx" v-show="page==index+1">
        <div>
          <p>
            <span>正确答案:</span>
            <span v-for="(val,info) in value.right_answer" :key="info">{{val.id|abcd}}</span>
          </p>
          <p>
            <span>您的答案</span>
            <span v-for="(val,info) in value.user_answer" :key="info">{{val.id|abcd}}</span>
          </p>
        </div>
        <div>
          <p>考点</p>
          <p v-for="(value,index) in testTypeList" :key="index">{{value}}</p>
        </div>
        <div>
          <p>解析</p>
          <p>暂无解析</p>
        </div>
      </div>
    </div>

    <!-- 解析结束 -->
  </div>
</template>

<script>
import {
  pointInfo,
  examPointQ,
  storeReport,
  collect,
  collectCancel,
  analysis,
  correction
} from "../../http/api";
export default {
  data() {
    return {
      questionShow: false,
      questionFk: "",
      question: "",
      radio: undefined,
      page: 1,
      radioList: [],
      isCollect: false, //是否收藏
      id: "", //id
      number: "", //
      type: "", //
      testTypeList: [],
      questionList: [],
      dtkShow: false,
      report_id: "", // 考试结果,
      collectId: "", //收藏id
      report_id: "", //解析id
      analysisList: [] //解析列表
    };
  },
  created() {
    this.id = this.$route.query.id;
    this.number = this.$route.query.number || "";
    this.report_id = this.$route.query.report_id || "";
    this.type = this.$route.query.type;
  },
  mounted() {
    this.testType();
    if (this.report_id != "") {
      this.analysis();
    }
    this.geTexamPointQ();
  },
  watch: {},
  methods: {
    // 返回
    goHome() {
      this.$router.push("/assess");
    },
    // 返回结束

    //   考试科目
    testType() {
      let token = localStorage.getItem("token");
      return pointInfo(token, this.id).then(res => {
        this.testTypeList = res.data.data;
      });
    },
    //   考试题内容
    geTexamPointQ() {
      let token = localStorage.getItem("token");
      return examPointQ(token, this.id, this.number).then(res => {
        this.questionList = res.data.data;
        this.collectTest();
      });
    },
    // 显示正确答案
    rightIndex() {
      
      if (this.analysisList[this.page - 1].user_answer==null) {
        this.radio = null;
      } else {
        this.radio = this.analysisList[this.page - 1].user_answer[0].id;
      }
    },
    // 显示正确答案
    // 上下页切换
    duToggle(page) {
      if (page == 1) {
        if (this.page > this.questionList.length) {
          this.$toast("已是最后 一页了");
          return false;
        }
        if (this.page < this.questionList.length) {
          this.page++;
          this.collectTest();
          this.rightIndex();
        }
      } else {
        if (this.page == 1) {
          this.$toast("已经是第一页了");
          return false;
        }
        this.page--;
        this.collectTest();
        this.rightIndex();
      }
    },

    // 答题显示卡
    showDatika() {
      this.number = 14;
      this.radioList.length = this.questionList.length;
      this.dtkShow = true;
      let sum = 0;
      this.radioList.forEach((value, index) => {
        if (value != undefined) {
          sum++;
        }
      });
      this.sum = sum;
    },
    // 答题卡隐藏
    dtkUshow() {
      this.number = 15;
      this.dtkShow = false;
    },
    // 答题卡切换
    questionToggle(index) {
      this.number = 14;
      this.dtkShow = false;
      this.page = index + 1;
      this.radio = this.radioList[this.page - 1];
    },

    // 检测收藏
    collectTest() {
      if (this.questionList[this.page - 1].collect_id == null) {
        this.isCollect = false;
      } else {
        this.isCollect = true;
      }
    },
    // 检测收藏

    // 习题收藏取消收藏
    addcollect() {
      let token = localStorage.getItem("token");
      return collect(token, this.questionList[this.page - 1].id).then(res => {
        if (res.data.code == 200) {
          this.$toast.success({
            message:"收藏成功"
          })
          this.geTexamPointQ();
        }
      });
    },
    removecollect() {
      let token = localStorage.getItem("token");
      return collectCancel(
        token,
        this.questionList[this.page - 1].collect_id
      ).then(res => {
        if (res.data.code == 200) {
          this.$toast({
            message:"取消收藏"
          })
          this.geTexamPointQ();
        }
      });
    },
    // 习题收藏取消收藏结束

    // 答题解析
    analysis() {
      let token = localStorage.getItem("token");
      return analysis(token, this.report_id).then(res => {
        this.analysisList = res.data.data;
        this.rightIndex();
      });
    },
    // 答案解析结束

    // 显示隐藏反馈卡
    fkshow(i) {
      if (i == 1) {
        this.questionShow = true;
      } else {
        this.questionShow = false;
      }
    },
    // 显示隐藏反馈卡结束
    // 问题反馈
    correction() {
      let token = localStorage.getItem("token");

      return correction(
        token,
        this.questionFk,
        this.questionList[this.page - 1].id,
        this.question
      ).then(res => {
        if (res.data.code == 200) {
          this.$toast("反馈成功");
          this.questionShow = false;
        }
      });
    }
    // 问题反馈结束
  }
};
</script>

<style  lang="scss" scoped>
.jh_questionDetail {
  width: 100%;
  height: 100%;
  background: #fff;
  overflow: auto;
}

// 导航
.jh_quesDetail_nav {
  font-size: 26px;
  color: #8c8c8c;
  width: 100%;
  display: flex;
  align-items: center;
  height: 89px;
  border-bottom: 1px solid #f0f2f5;
  padding: 0px 40px;
  box-sizing: border-box;
  justify-content: space-between;
  > div {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    p {
      display: flex;
      align-items: center;
      line-height: 30px;
    }
  }
  > img:nth-of-type(1) {
    width: 20px;
  }
  img {
    width: 30px;
    height: 30px;
  }
}
// 导航结束
// 页数
.jh_page {
  width: 100%;
  height: 80px;
  border-bottom: 1px solid #f0f2f5;
  display: flex;
  justify-content: space-between;
  padding: 0px 30px;
  box-sizing: border-box;
  align-items: center;
  color: #595959;
  font-size: 30px;
  p:nth-of-type(2) {
    span:nth-of-type(1) {
      font-size: 40px;
      color: #0097fa;
    }
  }
}
// 页数结束

// 题
.jh_question_item {
  width: 100%;
  padding: 30px 30px 0px 30px;
  box-sizing: border-box;
  .jh_content {
    width: 100%;
    .jh_top {
      position: relative;

      > p:nth-of-type(2) {
        position: absolute;
        top: 0px;
        left: 100px;
      }
      > p:nth-of-type(1) {
        position: absolute;
        width: 85px;
        height: 40px;
        background: #0097fa;
        text-align: center;
        font-size: 24px;
        line-height: 40px;
        border-radius: 20px 0 20px 0;
        color: #fff;
        margin-right: 10px;
      }
      > p:nth-of-type(3) {
        text-indent: 150px;
      }
    }

    .jh_section {
      li {
        width: 100%;
      }
    }
  }
}
.jh_section /deep/.van-radio {
  display: flex;
  padding: 30px 0px;
  box-sizing: border-box;
  height: 100%;
}
.jh_section /deep/ .van-radio__label {
  display: flex;
  align-items: center;
}
// 题结束
.jh_downUp {
  margin: 50px 0px;
  display: flex;
  justify-content: space-between;
  p {
    width: 180px;
    height: 70px;
    border-radius: 10px;
    border: 1px solid #d9d9d9;
    text-align: center;
    line-height: 70px;
    color: #595959;
    font-size: 26px;
  }
}

// 答题卡
.jh_dtkcont {
  width: 100%;
  min-height: 100%;
  position: relative;
  .jh_addquestion {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100px;
    text-align: center;
    line-height: 100px;
    background-color: #eb6100;
    color: #fff;
  }
  .jh_datika_nav {
    display: flex;
    justify-content: space-between;
    padding: 0px 30px;
    box-sizing: border-box;
    height: 90px;
    border-bottom: 1px solid #f0f2f5;
    align-items: center;
    img {
      width: 22px;
      height: 35px;
    }
  }
  .jh_datika_cont {
    ul {
      padding-top: 30px;
      box-sizing: border-box;
      display: flex;
      width: 100%;
      flex-wrap: wrap;
      .unRight {
        background: rgb(71, 173, 118);
        border: 1px solid rgb(71, 173, 118);
        color: #fff;
      }
      .Ringht {
        background: rgb(240, 96, 0);
        border: 1px solid rgb(240, 96, 0);
        color: #fff;
      }

      li {
        margin: 0px 25px 30px 25px;
        width: 90px;
        height: 90px;
        border: 1px solid #eaeaea;
        border-radius: 50%;
        text-align: center;
        line-height: 90px;
      }
    }
  }
}
// 答题卡结束
// 解析
.jh_jx {
  border-top: 5px solid #f0f2f5;
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  font-size: 30px;
  color: #666;
  div {
    display: flex;
    height: 100px;
    align-items: center;
  }
  div:nth-of-type(1) {
    justify-content: space-between;
    p {
      span {
        margin-right: 15px;
      }
      span:nth-of-type(2) {
        color: #22bfa7;
      }
    }
  }
  div:nth-of-type(2) {
    p:nth-of-type(2) {
      background: linear-gradient(30deg, #0097fa, #0096f9);
      border-radius: 20px;
      padding: 5px 10px;
      font-size: 25px;
      color: #fff;
      margin-left: 10px;
    }
  }
  div:nth-of-type(3) {
    p:nth-of-type(2) {
      font-size: 25px;
      margin-left: 10px;
    }
  }
}
// 解析结束

// 问题反馈
.jh_questionFk {
  background: #fff;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999999999999999999999999999999999999999999999999999999999999999999;
  .jh_questionFk_nav {
    display: flex;
    height: 90px;
    align-items: center;
    justify-content: space-between;
    padding: 0px 30px;
    box-sizing: border-box;
    border-bottom: 1px solid #f0f2f5;
    img {
      width: 20px;
    }
  }
  > div:nth-of-type(2) {
    padding: 30px;
    box-sizing: border-box;
    font-size: 30px;
    color: #595959;
    border-bottom: 20px solid #f0f2f5;
  }
  > div:nth-of-type(3) {
    padding: 30px;
    box-sizing: border-box;
    textarea {
      width: 100%;
      height: 400px;
      border: 1px solid #d9d9d9;
      border-radius: 15px;
      font-size: 30px;
      padding: 30px;
      box-sizing: border-box;
      margin-bottom: 260px;
    }
    p {
      width: 100%;
      height: 90px;
      text-align: center;
      line-height: 90px;
      border: 1px solid #0097fa;
      color: #0097fa;
    }
  }
}

.jh_questionFk /deep/.van-radio {
  display: flex;
  padding: 30px 0px;
  box-sizing: border-box;
  height: 100%;
}
.jh_questionFk /deep/ .van-radio__label {
  padding: 0px;
  display: flex;
  align-items: center;
  line-height: 30px;
}
.jh_questionFk /deep/.van-radio--horizontal {
  margin-right: 50px;
}

// 问题反馈结束
</style>
