<template>
  <div class="jh_concern">
    <div>
      <returnHome :title="title"></returnHome>
    </div>

    <!-- 讲师信息 -->
    <div class="jh_goodTeachers" v-if="concern.length>0">
      <ul>
        <router-link :to="{path:'/teacherCont',query:{id:`${value.teacher_id }`}}" tag="li" v-for="(value,index) in concern" :key="index">
         <div>
            <div>
              <img :src="value.avatar" alt />
              <p>
                <span>
                  {{value.teacher_name}}
                  <span class="jh_level">{{value.level_name}}</span>
                </span>
                <span>{{value.sex==0?'男':'女'}} {{value.teach_age}}年教龄</span>
              </p>
              <p @click.stop="collect(value.teacher_id)" class="jh_noConcerb">取消关注</p>
            </div>
            <ul>
              <li v-for="(val,info) in value.tag_content" :key="info">{{val}}</li>
            </ul>
          </div>
        </router-link>
      </ul>
    </div>
    <!-- 讲师信息借宿 -->

     <div class="jh_noCollect" v-else>
          <img src="@/assets/empty.0d284c2e.png" alt="">
          <p>暂无关注</p>
    </div>
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { teacherScollectList,teacherCollect } from "../../http/api";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      title: "", //网页标题,
      page: 1,
      limit: 10,
      concern: []
    };
  },
  created() {
    this.title = this.$route.meta.title;
  },
  mounted() {
    this.getCTeachersList();
  },
  methods: {
    // 获取关注教师的数据
    getCTeachersList() {
      let token = localStorage.getItem("token");
      return teacherScollectList(this.limit, this.page, token).then(res => {
          // console.log(res.data.data.list)
        this.concern = res.data.data.list;
      });
    },
    // 取消关注
    collect(teacher_id){
        let token=localStorage.getItem('token')
        return teacherCollect(token,teacher_id).then(res=>{
            console.log(res)
            if(res.data.code==200){
                this.$toast.success({
                  message:"已取消"
                })
                this.getCTeachersList()
            }
        })
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_concern {
  width: 100%;
}
// 教师信息
.jh_goodTeachers {
  width: 100%;
  padding: 15px 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  position: relative;
  > ul {
    width: 100%;
    margin-bottom: 30px;

    > li {
      > div {
        background: #fff;
        display: flex;
        justify-content: space-between;
        padding: 0px 30px 20px 30px;
        box-sizing: border-box;
        border-radius: 20px;
        flex-direction: column;
        margin-bottom: 20px;
        ul {
          display: flex;
          li {
            background: #ffe4d3;
            color: #ea7a2f;
            font-family: PingFangSC-Regular;
            text-align: center;
            width: 145px;
            height: 50px;
            line-height: 50px;
            border-radius: 30px;
            font-size: 24px;
            margin: 0px 20px 20px 0px;
          }
        }
        div {
          height: 140px;
          display: flex;
          align-items: center;

          img {
            width: 85px;
            height: 85px;
            border-radius: 50%;
          }
          p:nth-of-type(2) {
            width: 130px;
            height: 60px;
            background: #ebeefe;
            border-radius: 50px;
            font-size: 24px;
            font-family: PingFangSC-Regular;
            font-weight: 400;
            color: #eb6100;
            text-align: center;
            line-height: 60px;
          }
          p:nth-of-type(1) {
            width: 60%;
            flex: 1;
            display: flex;
            padding: 0 0 0 20px;
            box-sizing: border-box;
            flex-direction: column;
            .jh_level {
              font-size: 20px;
              color: #ea7a2f;
            }
            span:nth-of-type(1) {
              margin-bottom: 10px;
            }
            span:nth-of-type(2) {
              color: #b7b7b7;
              font-size: 24px;
            }
          }
        }
      }
    }
  }
}
// 教师信息结束

// 暂无关注
.jh_noCollect{
  width:100%;
  height:100%;
  img{
    width:300px;
    height:300px;
  }
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top:150px;
  p{
    font-size: 30px;
    color:#8c8c8c;
    line-height: 100px;
  }

}
</style>
