import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
// 将axios在原型上定义
import axios from "axios";
Vue.prototype.$axios = axios;
import Vant from "vant";
import { Toast } from "vant";
import { Picker } from "vant";
Vue.use(Picker);
// 全局注册
Vue.use(Toast);
import "vant/lib/index.css";
import common from "./common/filters/index";

Object.keys(common).forEach(key => {
  Vue.filter(key, common[key]);
});
Vue.use(Vant);
// console.log(process.env);
Vue.config.productionTip = false;
// 全区路由首位
router.beforeEach((to, from, next) => {
  const routerList = ["/index", "/course", "/record", "/practise", "/login"];
  let index = routerList.findIndex(value => {
    return to.path == value;
  });
  store.commit("activeIndex", index);
  // 判断是to.meta.title是否为空,设置成页面title
  if (to.meta.title != "") {
    document.title = to.meta.title;
  }
  next();
});

new Vue({
  router,
  store,
  axios,
  render: h => h(App)
}).$mount("#app");
