<template>
  <div class="jh_oto">
    <!-- 头部导航区 -->
    <div class="jh_oto_nav">
      <van-nav-bar title="一对一辅导" left-arrow @click-left="onClickLeft">
        <template #right>
          <van-icon name="search" size="18" @click="goSearch" />
        </template>
      </van-nav-bar>
    </div>
    <!-- 头部导航区结束-->
    <!-- ======================================================================================================================= -->
    <!-- 下拉菜单区域 -->
    <div class="jh_oto_menu">
      <van-dropdown-menu>
        <!-- 左侧 -->
        <van-dropdown-item title="选择上课时间" ref="checkTime">
          <div class="jh_shangketime">
            <div class="jh_date">
              <p>日期</p>
              <ul>
                <li v-for="(value,index) in weekdayList" :key="index">
                  <span>{{value}}</span>
                </li>
              </ul>
              <ul>
                <li
                  v-for="(value,index) in weekDateList"
                  :key="index"
                  :class="activeIndex==index?'active':''"
                  @click="indexToggle(index)"
                >
                  <span>{{value}}</span>
                </li>
              </ul>
            </div>
            <div class="jh_time">
              <p>时段</p>
              <div>
                <div style="margin-right:20px;" @click="xzztime(1)">
                  <p>{{startT}}</p>
                  <img src="@/assets/clock.png" alt />
                </div>
                <div @click="xzztime(2)">
                  <p>{{endT}}</p>
                  <img src="@/assets/clock.png" alt />
                </div>
              </div>
            </div>
            <div class="jh_btn">
              <p @click="timeReset">重置</p>
              <p @click="timeSure">确定</p>
            </div>
          </div>
        </van-dropdown-item>
        <!-- 左侧结束 -->
        <!-- 右侧 -->
        <van-dropdown-item title="选额老师条件" ref="checkTeachers">
          <div class="jh_teacher-module">
            <!-- M1 -->
            <div>
              <p>老师类型</p>
              <ul>
                <li
                  v-for="(value,index) in otoTeacherLevel"
                  :key="index"
                  @click="add_level_id({level_id:value.level_id,index:index})"
                  :class="level_id_index==index?'active':'null'"
                >{{value.level_name}}</li>
              </ul>
            </div>
            <!-- M1 -->
            <!-- 只看,性别 -->
            <div>
              <p>只看</p>
              <ul>
                <li>
                  <input type="checkbox" v-model="collect_bool" />
                  <span>已关注</span>
                </li>
                <li>
                  <input type="checkbox" v-model="attended_bool" />
                  <span>上过课的</span>
                </li>
              </ul>
            </div>
            <div>
              <p>性别</p>
              <ul>
                <li
                  v-for="(value,index) in sexList"
                  :key="index"
                  :class="sex_index==index?'active':'null'"
                  @click="add_sex({sex:value.sex,index:index})"
                >{{value.name}}</li>
              </ul>
            </div>
            <!-- 只看,性别结束 -->
            <!-- 年级 -->
            <div v-for="(value,index) in attrList" :key="index">
              <p>{{value.attr_name}}</p>
              <ul>
                <li
                  v-for="(val,info) in value.child"
                  :key="info"
                  @click="addattr({attr_val_id:val.attr_val_id,info:info})"
                  :class="attr_val_id_inedx==info?'active':'null'"
                >{{val.attr_val_name}}</li>
              </ul>
            </div>

            <div v-for="(value,index) in attrList2" :key="'value'+index">
              <p>{{value.attr_name}}</p>
              <ul>
                <li
                  v-for="(val,info) in value.child"
                  :key="info"
                  @click="addattr1({attr_val_id:val.attr_val_id,info:info})"
                  :class="attr_val_id_inedx1==info?'active':'null'"
                >{{val.attr_val_name}}</li>
              </ul>
            </div>
            <!-- 年级结束 -->
          </div>
          <div class="jh_btn">
            <p @click="teachersReset">重置</p>
            <p @click="teacherSure">确定</p>
          </div>
        </van-dropdown-item>
        <!-- 右侧结束 -->
      </van-dropdown-menu>
    </div>
    <!-- 下拉菜单区域结束 -->
    <!-- 老师列表 -->
    <div class="jh_otoTeachers">
      <p v-if="searchTitleS" class="jh_searchTitle">"{{keywords}}"搜索结果</p>
      <ul>
        <li v-for="(value,index) in otoTeachers" :key="index" @click="goDetail(value.teacher_id)">
          <img :src="value.avatar" alt />
          <p>
            <span>
              {{value.real_name}}
              <span class="jh_is_collect" v-if="value.is_collect==1">已关注</span>
            </span>
            <span>{{value.sex==0?'男':'女'}} &nbsp;&nbsp;{{value.teach_age}}年教授</span>
          </p>
          <p class="jh_oto_yuyue">预约</p>
        </li>
      </ul>
      <div class="no_list">
        <p>没有更多了</p>
      </div>
    </div>
    <!-- 老师列表结束 -->
  </div>
</template>

<script>
import calendars from "../../components/msmk_index/calendars";
import {
  otoCourseOptions,
  otoCourse,
  otoTime,
  otoTimeNext
} from "../../http/api";
export default {
  components: {
    calendars
  },
  data() {
    return {
      currentTime: "",
      startT: "开始时间",
      endT: "结束时间",
      activeIndex: 0,
      show: true,
      id: 3,
      searchTitleS: false,
      keywords: "",
      collect_bool: false,
      attended_bool: false,
      sex_index: "null",
      level_id_index: "null",
      attr_val_id_inedx: "null",
      attr_val_id_inedx1: "null",
      sexList: [
        { name: "男", sex: "0" },
        { name: "女", sex: "1" }
      ],
      otoTeachers: [], //一对一教师列表数据
      page: 1,
      limit: 10,
      attrList: [], //年级学科
      attrList2: [], //年级学科
      otoTeacherLevel: [], //等级,
      page: 1,
      limit: 10,
      start_time: "", //开始时间
      end_time: "", //结束时间
      level_id: "", //M1
      is_collect: "", //收藏过的1
      is_attended: "", //上过课的1
      sex: "", //性别 男0 女1
      attr_val_id: "", //年级和学科
      attr_val_id1: "", //年级和学科
      weekdayList: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
      weekDateList: [] //时间表
    };
  },
  created() {
    this.keywords = this.$route.params.keywords;
    if (this.keywords == undefined) {
      this.getOtoCourse();

      this.searchTitleS = false;
    }
    if (this.keywords != undefined) {
      this.otoTeachers = this.$route.params.data;
      this.searchTitleS = true;
    }
    this.otoCourseOptions();
  },
  mounted() {
    this.getTime();
  },
  methods: {
    goDetail(id) {
      if (localStorage.getItem("token") === null) {
        this.$router.push("/regLogin");
      } else {
        this.$router.push({ path: "/otoPlan", query: { id: id } });
      }
    },
    xzztime(index) {
      this.currentTime = true;
    },
    indexToggle(index) {
      this.activeIndex = index;
    },
    timeSure() {
      this.$refs.checkTime.toggle();
    },
    timeReset() {
      this.$refs.checkTime.toggle();
    },
    // 老师类型
    add_level_id({ level_id, index }) {
      this.level_id_index = index;
      this.level_id = level_id;
    },
    // 性别
    add_sex({ sex, index }) {
      this.sex_index = index;
      this.sex = sex;
    },
    // 年级
    addattr({ attr_val_id, info }) {
      this.attr_val_id = attr_val_id;
      this.attr_val_id_inedx = info;
    },
    // 科目
    addattr1({ attr_val_id, info }) {
      this.attr_val_id1 = attr_val_id;
      this.attr_val_id_inedx1 = info;
    },
    // 教师筛选确定
    teacherSure() {
      let token = localStorage.getItem("token");
      if (this.attr_val_id == "") {
        this.attr_val_id = this.attr_val_id1;
      } else if (this.attr_val_id1 == "") {
        this.attr_val_id = this.attr_val_id;
      } else if (this.attr_val_id != "" && this.attr_val_id1 != "") {
        this.attr_val_id = `${this.attr_val_id},${this.attr_val_id1}`;
      }
      if (this.collect_bool == true) {
        this.is_collect = 1;
      } else {
        this.is_collect = 0;
      }
      if (this.attended_bool == true) {
        this.is_attended = 1;
      } else {
        this.is_attended = 0;
      }
      return otoCourse(
        token,
        this.page,
        this.limit,
        this.start_time,
        this.end_time,
        this.level_id,
        this.is_collect,
        this.is_attended,
        this.sex,
        this.attr_val_id
      ).then(res => {
        if (res.data.code == 200) {
          this.otoTeachers = res.data.data;
          this.$refs.checkTeachers.toggle();
        }
      });
    },
    // 教师筛选重置
    teachersReset() {
      let token = localStorage.getItem("token");
      (this.start_time = ""),
        (this.end_time = ""),
        (this.level_id = ""),
        (this.is_collect = ""),
        (this.is_attended = ""),
        (this.sex = ""),
        (this.attr_val_id = "");
      return otoCourse(
        token,
        this.page,
        this.limit,
        this.start_time,
        this.end_time,
        this.level_id,
        this.is_collect,
        this.is_attended,
        this.sex,
        this.attr_val_id
      ).then(res => {
        if (res.data.code == 200) {
          this.otoTeachers = res.data.data;
          this.$refs.checkTeachers.toggle();
        }
      });
    },
    //   返回
    onClickLeft() {
      this.$router.push("/index");
    },
    goSearch() {
      this.$router.push({
        path: "/search",
        query: { name: this.$route.name }
      });
    },
    //   获取一对一老师详情
    getOtoCourse() {
      let token = localStorage.getItem("token");
      this.$axios({
        url: `https://www.365msmk.com/api/app/otoCourse`,
        method: "get",
        headers: {
          "Content-Type": "application/json",
          DeviceType: "H5",
          Authorization: `Bearer ${token}`
        },
        params: {
          page: this.page,
          limit: this.limit
        }
      }).then(res => {
        this.otoTeachers = res.data.data;
      });
    },
    // 获取一对一老师筛选条件
    otoCourseOptions() {
      return otoCourseOptions().then(res => {
        this.attrList.push(res.data.data.attrList[0]);
        this.attrList2.push(res.data.data.attrList[1]);
        this.otoTeacherLevel = res.data.data.otoTeacherLevel;
      });
    },

    // 获取时间表
    getTime() {
      let token = localStorage.getItem("token");
      return otoTime(token, this.id).then(res => {
        this.weekDateList = res.data.data.weekDateList;
        return otoTimeNext(token, this.id).then(res => {
          if(res.data.code==200){
             this.weekDateList.push(...res.data.data.weekDateList);
            let arr = [];
            // console.log(this.weekDateList);
            this.weekDateList.forEach(value => {
              arr.push(value.substring(3, 5));
            });
            this.weekDateList = arr;
          }else{
              let arr = [];
               this.weekDateList.forEach(value => {
              arr.push(value.substring(3, 5));
            });
            this.weekDateList = arr;
          }
         
        });
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_oto {
  width: 100%;
  height: 100%;
}
.jh_searchTitle {
  padding: 0px 30px;
  box-sizing: border-box;
  font-size: 30px;
  color: #8d8d90;
}
.jh_oto_nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99999999999999999999999999;
}
// 导航区域
.jh_oto /deep/.van-nav-bar,
.van-hairline--bottom {
  height: 87px;
  border-bottom: 3px solid #f6f6f6;
}
.jh_oto /deep/ .van-nav-bar__title,
.van-ellipsis {
  font-size: 36px;
  color: #656464;
}
.jh_oto /deep/.van-icon,
van-icon-search {
  color: #656464;
  font-size: 36px;
}
// 导航区域结束
// 下拉菜单
.jh_oto_menu {
  margin-top: 90px;
}
.jh_oto /deep/.van-dropdown-menu__bar {
  height: 87px;
}
.jh_oto /deep/ .van-dropdown-item__content {
  min-height: 100%;
}
.jh_oto /deep/.van-dropdown-menu__title {
  height: 87px;
  font-size: 28px;
  line-height: 87px;
}
.jh_oto /deep/.van-dropdown-menu__title {
  color: #8c8c8c;
}
.jh_oto /deep/ .van-dropdown-menu__title--active {
  color: #eb6100;
}
/deep/.van-dropdown-menu__title::after {
  border-width: 8px;
  right: -30px;
  top: 45%;
}
// 下拉菜单结束
// oto教师
.jh_otoTeachers {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  .no_list {
    width: 100%;
    height: 100%;
    font-size: 30px;
    color: #7b7b7b;
    text-align: center;
    line-height: 50px;
    p {
      width: 100%;
    }
  }
  ul {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    li:nth-last-of-type(1) {
      margin-bottom: 0px;
    }
    li {
      background: #fff;
      height: 160px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 30px;
      box-sizing: border-box;
      margin-bottom: 30px;
      border-radius: 10px;
      img {
        width: 100px;
        height: 100px;
        border-radius: 50%;
      }
      .jh_oto_yuyue {
        flex: none;
        width: 140px;
        height: 60px;
        line-height: 60px;
        background: #ebeefe;
        border-radius: 50px;
        font-size: 28px;
        font-family: PingFangSC-Regular;
        color: #eb6100;
        text-align: center;
      }
      p {
        width: 60%;
        flex: 1;
        display: flex;
        padding: 0 0 0 20px;
        line-height: 50px;
        box-sizing: border-box;
        flex-direction: column;
        .jh_is_collect {
          font-size: 20px;
          color: #ea7a2f;
        }
        span {
          overflow: hidden; /*超出部分隐藏*/
          text-overflow: ellipsis; /* 超出部分显示省略号 */
          white-space: nowrap;
          color: #595959;
          font-size: 30px;
        }
        span:nth-of-type(2) {
          color: #b7b7b7;
          font-size: 24px;
        }
      }
    }
  }
}
// oto教师结束

// 筛选列表
.jh_teacher-module {
  height: 100%;
  width: 100%;
  padding: 0px 30px;
  box-sizing: border-box;
  background: #fff;
  // m1
  > div > p {
    font-size: 24px;
    line-height: 80px;
    color: #595959;
  }
  > div > ul {
    display: flex;
  }
  > div:not(n-1) {
    border-bottom: 1px solid #f1f1f1;
  }
  div:nth-of-type(1),
  div:nth-of-type(4),
  div:nth-of-type(5) {
    width: 100%;

    ul {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      li,
      .active {
        color: #eb6100;
        background: #ebeefe;
      }
      li {
        width: 140px;
        height: 70px;
        background: #f5f5f5;
        border-radius: 10px;
        margin: 0px 35px 20px 0px;
        text-align: center;
        line-height: 70px;
        color: #646464;
        font-size: 22px;
      }
      li:nth-child(4n + 0) {
        margin-right: 0px;
      }
    }
  }
  // m1结束
  // 只看
  div:nth-of-type(2) {
    ul {
      align-items: center;
      li {
        height: 60px;
        font-size: 26px;
        color: #9a9b9b;
        display: flex;
        align-items: center;
        padding: 0px 20px 0px 0px;
        box-sizing: border-box;
        input {
          margin-right: 10px;
        }
      }
    }
  }
  // 只看结束
  // 性别
  div:nth-of-type(3) {
    ul {
      li,
      .active {
        color: #eb6100;
        background: #ebeefe;
      }
      li {
        width: 140px;
        height: 70px;
        background: #f5f5f5;
        border-radius: 10px;
        text-align: center;
        line-height: 70px;
        color: #646464;
        font-size: 22px;
        margin: 0px 45px 36px 0px;
      }
    }
  }
  div:nth-of-type(5) {
    margin-bottom: 80px;
  }
  // 性别结束
}
// 筛选列表结束
// 按钮
.jh_btn {
  width: 100%;
  position: fixed;
  bottom: 0px;
  left: 0px;
  left: 0px;
  height: 88px;
  display: flex;
  p {
    flex: 1;
    text-align: center;
    line-height: 88px;
    font-size: 30px;
  }
  p:nth-of-type(1) {
    color: #eb6100;
    background: #fff;
  }
  p:nth-of-type(2) {
    background: #eb6100;
    color: #fff;
  }
}
// 按钮结束
.jh_oto /deep/.van-dropdown-item__content {
  background: #f0f2f5;
}
// 上课时间
.jh_shangketime {
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  .jh_date {
    width: 100%;
    height: 380px;
    background: #fff;
    p {
      font-size: 26px;
      padding: 0px 0px 0px 15px;
      box-sizing: border-box;
      line-height: 60px;
    }
    ul {
      display: flex;
      flex-wrap: wrap;

      .active {
        color: rgb(255, 255, 255);
        background: rgb(235, 97, 0);
      }
      li {
        width: 14%;
        text-align: center;
        line-height: 100px;
        border-radius: 50%;
      }
    }
  }
}
.jh_time {
  margin-top: 30px;
  width: 100%;
  height: 180px;
  background: #fff;
  border-radius: 10px;
  > p {
    font-size: 26px;
    padding: 0px 0px 0px 30px;
    box-sizing: border-box;
    line-height: 60px;
  }
  > div {
    display: flex;
    padding: 0px 30px;
    box-sizing: border-box;
    div {
      width: 300px;
      height: 70px;
      border: 1px solid rgb(191, 191, 191);
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-radius: 10px;
      p {
        width: 60%;
        background: rgb(235, 235, 228);
        height: 100%;
        text-align: center;
        line-height: 70px;
        color: rgb(84, 84, 84);
        font-size: 30px;
      }
      img {
        width: 25px;
        height: 25px;
      }
    }
  }
}
</style>
