<template>
  <div class="jh_set">
    <div>
      <returnHome :title="title"></returnHome>
    </div>
    <div class="jh_setPass">
      <div>
        <input type="text" id placeholder="请输入手机号码" v-model="mobile" />
        <p @click="get_sms_code" v-if="show">获取验证码</p>
        <p v-else>({{num}})重新获取</p>
      </div>
      <div>
        <input type="text" placeholder="请输入验证码" v-model="sms_code" />
      </div>
      <div>
        <input type="password" placeholder="请输入密码" v-model="password" />
      </div>
    </div>
    <div class="jh_btn">
      <p @click="jh_set">确定</p>
    </div>
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { getsms_code, password } from "../../http/api";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      title: "",
      mobile: "",
      sms_code: "",
      password: "",
      num:60,
      show:true
    };
  },
  created() {
    this.title = this.$route.meta.title;
  },
  mounted() {},
  methods: {
    //   倒计时
    countsecond() {
      let time = 60;
      let timer = setInterval(() => {
        if (time < 1) {
          clearInterval(timer);
          this.show = true;
        }
        this.num = time;
        time--;
      }, 1000);
    },
    //   获取验证码
    get_sms_code() {
      let token = localStorage.getItem("token");
      return getsms_code(token, this.mobile).then(res => {
        if (res.data.code != 200) {
          this.$toast(res.data.msg);
        }else{
          this.$toast.success({
            message:"发送成功"
          })
            this.show=false
            this.countsecond()
        }
      });
    },
    //   确定
    jh_set() {
      let token = localStorage.getItem("token");
      return password(token, this.mobile, this.password, this.sms_code).then(
        res => {
          if (res.data.data == true) {
            this.$toast.success({
            message:"修改秘密成功"
          })
            this.$router.push("/options");
          }
        }
      );
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_set {
  width: 100%;
  height: 100%;
  background: #fff;
}
.jh_setPass {
  width: 100%;
  padding: 0px 30px;
  box-sizing: border-box;

  > div {
    display: flex;
    justify-content: space-between;
    height: 110px;
    border-bottom: 1px solid #f1f1f1;
    align-items: center;
    p {
      color: #eb6100;
      font-size: 26px;
    }
    input {
      border: none;
    }
    ::placeholder {
      font-size: 30px;
      color: #7d7e80;
    }
  }
}
.jh_btn {
  width: 100%;
  height: 330px;
  display: flex;
  justify-content: center;
  align-items: center;
  p {
    width: 620px;
    background: linear-gradient(90deg, #ff944b, #fc5500);
    height: 90px;
    border-radius: 50px;
    text-align: center;
    line-height: 90px;
    color: #fff;
    font-size: 30px;
  }
}
</style>
