<template>
  <div class="jh_callectDetail">
    <div>
      <!-- 导航 -->
      <div class="jh_callectDetail_nav">
        <img src="@/assets/return.png" alt @click="goHome" style="width:10px" />
        <p>{{title}}</p>
        <div>
          <img src="@/assets/datika.png" alt />
          <van-icon
            name="star"
            color="#fc5500"
            size="16px"
            @click="removecollect"
            v-if="isCollect"
          />
          <van-icon name="star-o" size="16px" @click="addcollect" v-else />
          <img src="@/assets/ji.png" alt @click="fkshow(1)" />
        </div>
      </div>
      <!-- 导航结束 -->

      <!-- 题目内容 -->
      <div class="jh_question_item">
        <div>
          <!-- 考试题 -->
          <div class="jh_content">
            <div class="jh_top">
              <p>{{collectList.ques_type==1?'单选':'多选'}}</p>
              <p v-html="collectList.ques_stem" class="jh_img"></p>
            </div>
            <ul class="jh_section">
              <li v-for="(val,info) in collectList.ques_option" :key="info">
                <van-radio-group v-model="radio">
                  <van-radio :name="info*1+1" @click="radio=val.id">
                    <p>{{val.id|abcd}}丶</p>
                    <p v-html="val.content"></p>
                  </van-radio>
                </van-radio-group>
              </li>
            </ul>
          </div>
          <!-- 考试题结束 -->
        </div>
      </div>
      <!-- 题目内容结束 -->

      <!-- 解析 -->
      <div class="jh_jx">
        <div>
          <p>
            <span>正确答案:</span>
            <span>{{right|abcd}}</span>
          </p>
          <p>
            <span></span>
            <span></span>
          </p>
        </div>
        <div>
          <p>考点</p>
          <p>{{collectList.point_name}}</p>
        </div>
        <div>
          <p>解析</p>
          <p>暂无解析</p>
        </div>
      </div>
      <!-- 解析结束 -->
    </div>

    <!-- 问题反馈 -->
    <div class="jh_questionFk" v-show="questionShow">
      <div class="jh_questionFk_nav">
        <img src="@/assets/return.png" alt @click="fkshow(2)" />
        <p>题目纠错反馈</p>
        <p></p>
      </div>
      <div>
        <p>纠结错误</p>
        <div>
          <van-radio-group v-model="question" direction="horizontal">
            <van-radio name="1">错别字</van-radio>
            <van-radio name="2">答案解析不匹配</van-radio>
            <van-radio name="3">解析错误</van-radio>
            <van-radio name="4">其他</van-radio>
          </van-radio-group>
        </div>
      </div>
      <div>
        <textarea placeholder="请输入题目纠错信息，最多不超过500个字" v-model="questionFk"></textarea>
        <p @click="correction">提交</p>
      </div>
    </div>
    <!-- 问题反馈结束 -->
  </div>
</template>

<script>
import {
  myCollectDetail,
  analysis,
  collect,
  collectCancel,
  correction
} from "../../http/api";
export default {
  data() {
    return {
      questionShow: false,
      question: "",
      questionFk: "",
      radio: "",
      id: "",
      collect_id: "", //收藏id
      isCollect: false,
      title: "",
      collectList: [],
      testTypeList: [],
      right: ""
    };
  },
  created() {
    this.id = this.$route.query.id;
    this.title = this.$route.meta.title;
  },
  mounted() {
    this.myCollectDetail();
  },
  methods: {
    goHome() {
      this.$router.push("/quesCollect");
    },

    // 获取收藏
    myCollectDetail() {
      let token = localStorage.getItem("token");
      return myCollectDetail(token, this.id).then(res => {
        // console.log(res.data.data.collect_id);
        let info = res.data.data.ques_option.findIndex((value, index) => {
          return value.is_right == 1;
        });
        this.right = info * 1 + 1;
        this.collectList = res.data.data;
        this.collect_id = res.data.data.collect_id;
        this.collectTest();
      });
    },
    // 获取收藏结束

    // 习题收藏取消收藏
    addcollect() {
      let token = localStorage.getItem("token");
      return collect(token, this.id).then(res => {
        if (res.data.code == 200) {
          this.$toast.success({
            message: "收藏成功"
          });
          // console.log(res.data.data);
          // console.log("添加成功");
          this.myCollectDetail();
        }
      });
    },
    removecollect() {
      let token = localStorage.getItem("token");
      return collectCancel(token, this.collect_id).then(res => {
        if (res.data.code == 200) {
          this.$toast({
            message: "取消收藏"
          });
          // console.log(res.data.data);
          // console.log("取消成功");
          this.myCollectDetail();
        }
      });
    },

    // 习题收藏取消收藏结束

    // 检测收藏
    collectTest() {
      if (this.collectList.collect_id == 0) {
        this.isCollect = false;
      } else {
        this.isCollect = true;
      }
    },
    // 检测收藏

    // 显示隐藏反馈卡
    fkshow(i) {
      if (i == 1) {
        this.questionShow = true;
      } else {
        this.questionShow = false;
      }
    },
    // 显示隐藏反馈卡结束
    // 问题反馈

    correction() {
      let token = localStorage.getItem("token");
      return correction(token, this.questionFk, this.id, this.question).then(
        res => {
          if (res.data.code == 200) {
            this.$toast("反馈成功");
            this.questionShow = false;
          }
        }
      );
    }
    // 问题反馈结束
  }
};
</script>

<style scoped lang='scss'>
.jh_callectDetail {
  width: 100%;
  height: 100%;
  background: #fff;
  overflow: auto;
}

// 导航
.jh_callectDetail_nav {
  font-size: 30px;
  color: #8c8c8c;
  width: 100%;
  display: flex;
  align-items: center;
  height: 89px;
  border-bottom: 1px solid #f0f2f5;
  padding: 0px 40px;
  box-sizing: border-box;
  justify-content: space-between;
  > div {
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    p {
      display: flex;
      align-items: center;
      line-height: 30px;
    }
  }
  img {
    width: 30px;
    height: 30px;
  }
}
// 导航结束

.jh_question_item {
  width: 100%;
  padding: 30px 30px 0px 30px;
  box-sizing: border-box;
  .jh_content {
    width: 100%;
    .jh_top {
      position: relative;
      height: 50px;
      > p:nth-of-type(2) {
        position: absolute;
        top: 0px;
        left: 100px;
      }
      > p:nth-of-type(1) {
        position: absolute;
        width: 85px;
        height: 40px;
        background: #0097fa;
        text-align: center;
        font-size: 24px;
        line-height: 40px;
        border-radius: 20px 0 20px 0;
        color: #fff;
        margin-right: 10px;
      }
      > p:nth-of-type(3) {
        text-indent: 150px;
      }
    }

    .jh_section {
      li {
        width: 100%;
      }
    }
  }
}
.jh_section /deep/.van-radio {
  display: flex;
  padding: 30px 0px;
  box-sizing: border-box;
  height: 100%;
}
.jh_section /deep/ .van-radio__label {
  display: flex;
  align-items: center;
}

// 解析
.jh_jx {
  border-top: 5px solid #f0f2f5;
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  font-size: 30px;
  color: #666;
  div {
    display: flex;
    height: 100px;
    align-items: center;
  }
  div:nth-of-type(1) {
    justify-content: space-between;
    p {
      span {
        margin-right: 15px;
      }
      span:nth-of-type(2) {
        color: #22bfa7;
      }
    }
  }
  div:nth-of-type(2) {
    p:nth-of-type(2) {
      background: linear-gradient(30deg, #0097fa, #0096f9);
      border-radius: 20px;
      padding: 5px 10px;
      font-size: 25px;
      color: #fff;
      margin-left: 10px;
    }
  }
  div:nth-of-type(3) {
    p:nth-of-type(2) {
      font-size: 25px;
      margin-left: 10px;
    }
  }
}
// 解析结束

// 问题反馈
.jh_questionFk {
  background: #fff;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999999999999999999999999999999999999999999999999999999999999999999;
  .jh_questionFk_nav {
    display: flex;
    height: 90px;
    align-items: center;
    justify-content: space-between;
    padding: 0px 30px;
    box-sizing: border-box;
    border-bottom: 1px solid #f0f2f5;
    img {
      width: 20px;
    }
  }
  > div:nth-of-type(2) {
    padding: 30px;
    box-sizing: border-box;
    font-size: 30px;
    color: #595959;
    border-bottom: 20px solid #f0f2f5;
  }
  > div:nth-of-type(3) {
    padding: 30px;
    box-sizing: border-box;
    textarea {
      width: 100%;
      height: 400px;
      border: 1px solid #d9d9d9;
      border-radius: 15px;
      font-size: 30px;
      padding: 30px;
      box-sizing: border-box;
      margin-bottom: 260px;
    }
    p {
      width: 100%;
      height: 90px;
      text-align: center;
      line-height: 90px;
      border: 1px solid #0097fa;
      color: #0097fa;
    }
  }
}

.jh_questionFk /deep/.van-radio {
  display: flex;
  padding: 30px 0px;
  box-sizing: border-box;
  height: 100%;
}
.jh_questionFk /deep/ .van-radio__label {
  display: flex;
  align-items: center;
}
.jh_questionFk /deep/.van-radio--horizontal {
  margin-right: 50px;
}
// 问题反馈结束
</style>
