<template>
  <div class="jh_classCont">
    <div class="jh_top">
      <returnHome :title="title"></returnHome>
    </div>
    <div class="jh_classCont_content">
      <ul class="jh_classCont_content_ul">
        <!-- //1 -->
        <li>
          <p class="jh_star">
            <van-icon name="star-o" size="18px" @click="addCollect" v-if="is_collect==0" />
            <van-icon name="star" v-else color="#eb6100" size="18px" @click="unCollect" />
          </p>
          <p>{{courseInfoList.title}}</p>
          <p v-if="courseInfoList.price">
            <img src="../../assets/20191HHDExgz0u1567065946.png" alt class="jh_money_img" />
            {{courseInfoList.price==0?'免费':`${courseInfoList.price/100}.00`}}
          </p>
          <p v-else>
            <img src="../../assets/20191HHDExgz0u1567065946.png" alt class="jh_money_img" />
            免费
          </p>

          <p>
            <span>共{{courseInfoList.total_periods}}课时</span>|
            <span>{{courseInfoList.sales_num}}人报名</span>
          </p>
        </li>
        <!-- //2 -->
        <router-link
          :to="{path:'/teacherCont',query:{id:value.teacher_id}}"
          tag="li"
          v-for="(value,index) in teachersInfo"
          :key="index"
        >
          <p>教学团队</p>
          <p>
            <img :src="value.teacher_avatar" alt />
          </p>

          <p>{{value.teacher_name}}</p>
        </router-link>
        <!-- //3 -->
        <li class="jh_jieshao">
          <p>课程介绍</p>
          <p v-html="courseInfoList.course_details"></p>
        </li>
        <!-- //4 -->
        <li class="jh_dagang">
          <p>课程大纲</p>
          <div class="jh_courseChapter">
            <div
              class="jh_courseChapter_cont"
              v-for="(value,index) in courseChapterList"
              :key="index"
            >
              <p @click="goVideo({id:id,video_id:value.video_id,title:courseInfoList.title})">
                <i class="jh_dian"></i>
                <span>回放</span>
                <span>{{value.periods_title}}</span>
              </p>
              <router-link
                :to="{path:'/video',query:{id:id,video_id:value.video_id,title:courseInfoList.title}}"
                tag="p"
              ></router-link>

              <p>
                <span v-for="(val,info) in value.teachers" :key="info">{{val.teacher_name}}</span>
                <span v-if="value.teachers.length>0">{{value.start_play}}-{{value.end_play}}</span>
              </p>
            </div>
          </div>
        </li>
      </ul>
      <!-- 评论 -->
      <div class="jh_commentList">
        <p>课程评论</p>
        <ul class="jh_noComment" v-if="courseCommentList.length==0">
          <li>
            <img src="../../assets/empty.0d284c2e.png" alt />
            <p>暂无评论</p>
          </li>
        </ul>
        <ul v-else>
          <li v-for="(value,index) in courseCommentList" :key="index">
            <img :src="value.avatar" alt />
            <div>
              <p>
                <span>{{value.nickname}}</span>
                <span>
                  <van-icon
                    :name="value.grade>info?'star':'star-o'"
                    color="#eb6100"
                    v-for="(val,info) in 5"
                    :key="info"
                  />
                </span>
                <span>{{value.created_at*1000|timer}}</span>
              </p>
              <p>{{value.content}}</p>
            </div>
          </li>
        </ul>
      </div>
      <!-- 评论结束 -->
    </div>
    <!-- 立即学习 -->
    <div class="jh_nowStudy" @click="nowEnter" v-if="studyShow">立即报名</div>
    <div class="jh_nowStudy" v-else @click="goStudy">立即学习</div>
    <div class="jh_share" v-if="$store.state.isShow" @click="unshow">
      <qrCode></qrCode>
    </div>
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import {
  courseComment,
  courseInfo,
  courseChapter,
  addCollect,
  Collect,
  unCollect,
  nowEnter
} from "../../http/api";
import qrCode from "../../components/qrCode/qrCode";
export default {
  components: {
    returnHome,
    qrCode
  },
  data() {
    return {
      studyShow: true,
      course_id: "",
      isshow: false,
      title: "",
      id: "",
      course_type: "",
      limit: 10,
      page: 1,
      CollectData: "", //收藏码
      is_collect: "", //收藏状态
      courseInfoList: [], //课程详情
      courseCommentList: [], //评价详情
      teachersInfo: [], //
      courseChapterList: [] //课程大纲
    };
  },
  created() {
    this.title = this.$route.meta.title;
    this.id = this.$route.query.id;
    this.course_type = this.$route.query.course_type;
    this.courseInfo();
    this.find();
  },
  mounted() {
    this.courseComment();

    this.courseChapter();
  },
  methods: {
    goVideo({ id, video_id, title }) {
      if (localStorage.getItem("token") === null) {
        this.$router.push("/regLogin");
        return false;
      } else {
        this.$router.push({
          path: "/video",
          query: { id: id, video_id: video_id, title: title }
        });
      }
    },
    unshow() {
      this.$store.commit("unshow", false);
    },
    //   获取课程详情
    courseInfo() {
      let token = localStorage.getItem("token");
      return courseInfo(this.id, token).then(res => {
        this.course_id = res.data.data.info.id;
        this.courseInfoList = res.data.data.info;
        this.teachersInfo = res.data.data.teachers;
        if (res.data.data.info.is_buy === 1) {
          this.studyShow = false;
        } else {
          this.studyShow = true;
        }
      });
    },
    //    获取课程评价
    courseComment() {
      let token = localStorage.getItem("token");
      return courseComment(this.id, this.limit, this.page).then(res => {
        this.courseCommentList = res.data.data.list;
      });
    },
    // 获取课程大纲
    courseChapter() {
      let token = localStorage.getItem("token");
      return courseChapter(token, this.id).then(res => {
        this.courseChapterList = res.data.data;
      });
    },
    // 添加收藏
    addCollect() {
      if (localStorage.getItem("token") === null) {
        this.$router.push("/regLogin");
        return false;
      }
      let token = localStorage.getItem("token");
      return addCollect(token, this.id).then(res => {
        if (res.data.code == 200) {
          this.CollectData = res.data.data;
          this.$toast.success({
            message: "收藏成功"
          });
          this.find();
        }
      });
    },
    find() {
      let token = localStorage.getItem("token");
      return courseInfo(this.id, token).then(res => {
        this.CollectData = res.data.data.info.collect_id;
        this.is_collect = res.data.data.info.is_collect;
      });
    },
    // 取消收藏
    unCollect() {
      let token = localStorage.getItem("token");
      return unCollect(token, this.CollectData, this.is_collect).then(res => {
        this.$toast.success({
          message: "取消收藏"
        });
        this.find();
      });
    },
    // 立即报名
    nowEnter() {
      if (localStorage.getItem("token") === null) {
        this.$router.push("/regLogin");
        return false;
      }
      if (this.courseInfoList.price !== 0) {
        this.$router.push({
          path: "/purchase",
          query: {
            id: this.id,
            course_type: this.course_type
          }
        });
      } else {
        let token = localStorage.getItem("token");
        return nowEnter(token, this.id).then(res => {
          console.log(res);
          if (res.data.code == 200) {
            console.log(res);
            this.courseInfo();
          }
        });
      }
    },
    // 立即学习
    goStudy() {
      this.$router.push({
        path: "/studyDetail",
        query: {
          course_id: this.course_id,
          course_type: this.course_type
        }
      });
    }
  }
};
</script>

<style scoped lang="scss">
.jh_commentList > .jh_noComment {
  width: 100%;
  li {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 0px;
    img {
      width: 300px;
      height: 300px;
    }
    p {
      font-size: 30px;
      color: #8c8c8c;
      margin-top: 50px;
    }
  }
}
.jh_classCont_content {
  width: 100%;
}
.jh_classCont_content_ul {
  width: 100%;
  li {
    margin-bottom: 30px;
    background: #fff;
    padding: 30px;
    box-sizing: border-box;
  }
  li:nth-of-type(1) {
    .jh_money_img {
      width: 18px;
      height: 18px;
      border-radius: 0px;
      margin-right: 10px;
    }
    position: relative;
    .jh_star {
      position: absolute;
      right: 20px;
      top: 15px;
    }
    p {
      line-height: 60px;
    }
    p:nth-of-type(2) {
      font-size: 35px;
      color: #333;
      width: 90%;
    }
    p:nth-of-type(3) {
      font-size: 35px;
      color: #eb6100;
    }
    p:nth-of-type(4) {
      font-size: 28px;
      color: rgba(0, 0, 0, 0.45);
    }
  }
  li:nth-of-type(2) {
    p {
      line-height: 50px;
    }
    p:nth-of-type(1) {
      font-size: 30px;
      color: #262626;
      margin-bottom: 20px;
    }
    img {
      width: 80px;
      height: 80px;
      border-radius: 50%;
    }
    p:nth-of-type(3),
    p:nth-of-type(2) {
      padding: 0px 0px 0px 20px;
      box-sizing: border-box;
      font-size: 24px;
      color: #595959;
    }
  }
  li:nth-of-type(3) {
    p:nth-of-type(1) {
      margin-bottom: 20px;
    }
    p {
      font-size: 30px;
      color: #262626;
      line-height: 40px;
    }
    p:nth-of-type(2) {
      padding-left: 10px;
      box-sizing: border-box;
    }
  }
  li:nth-of-type(4) {
    > p:nth-of-type(1) {
      margin-bottom: 20px;
    }
    p {
      font-size: 30px;
      color: #262626;
      line-height: 40px;
    }
    .jh_courseChapter {
      width: 100%;
      padding: 0px 60px;
      box-sizing: border-box;
      .jh_courseChapter_cont {
        margin-bottom: 40px;
      }
      p {
        display: flex;
        align-items: center;
        span {
          display: block;
          font-size: 26px;
          color: #8c8c8c;
        }
      }
      // 回放
      p:nth-of-type(1) {
        position: relative;
        .jh_dian {
          position: absolute;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          background: #ea7a2f;
          top: 50%;
          margin-top: -5px;
          left: -30px;
        }
        span:nth-of-type(1) {
          width: 70px;
          height: 38px;
          border-radius: 10px;
          background: #ea7a2f;
          font-size: 22px;
          color: #fff;
          text-align: center;
          line-height: 45px;
        }
        span:nth-of-type(2) {
          padding: 0px 24px;
          box-sizing: border-box;
          font-size: 22px;
        }
      }
      p:nth-of-type(2) {
        line-height: 50px;
        padding: 0px 24px;
        box-sizing: border-box;
        span:nth-of-type(2) {
          padding: 0px 24px;
          box-sizing: border-box;
        }
      }
    }
  }
}
.jh_jieshao /deep/ img {
  width: 100%;
}

.jh_commentList {
  margin-bottom: 120px;
  background: #fff;
  width: 100%;
  padding: 10px 30px;
  box-sizing: border-box;
  > p {
    font-size: 30px;
    line-height: 68px;
  }
  ul {
    width: 100%;
    li {
      display: flex;
      padding: 0px 20px;
      box-sizing: border-box;
      margin-bottom: 40px;
      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-right: 20px;
      }
      div {
        display: flex;
        flex-direction: column;
        flex: 1;
        p:nth-of-type(1) {
          display: flex;
          justify-content: space-between;
          span:nth-of-type(1) {
            font-size: 30px;
            color: #333;
          }
          span:nth-of-type(3) {
            font-size: 22px;
            color: #999;
          }
        }
        p:nth-of-type(2) {
          color: #999;
          font-size: 26px;
        }
      }
    }
  }
}
.jh_nowStudy {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  height: 83px;
  line-height: 83px;
  font-size: 34px;
  text-align: center;
  background: #eb6100;
  color: #fff;
}
.jh_share {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99999999999999999999999;
}
</style>
