<template>
  <div class="jh_exchange">
    <!-- 导航 -->
    <div>
      <returnHome :title="title"></returnHome>
    </div>
    <!-- 导航结束 -->
    <!-- 兑换区域 -->
    <div class="jh_exchange_card">
      <div>
        <p>卡号:</p>
        <input type="text" v-model="card_number" />
      </div>
      <div>
        <p>密码:</p>
        <input type="password" v-model="card_pwd" />
      </div>
      <div @click="nowChange">立即兑换</div>
    </div>
    <!-- 兑换区域结束 -->
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { studyCard } from "../../http/api";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      title: "学习卡兑换",
      card_number: "", //卡号
      card_pwd: "" //密码
    };
  },
  created() {},
  mounted() {},
  methods: {
    nowChange() {
      let token = localStorage.getItem("token");
      return studyCard(token, this.card_number, this.card_pwd).then(res => {
        if (res.data.code == 200) {
          this.$toast("兑换成功");
        } else {
          this.$toast(res.data.msg);
        }
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_exchange {
  width: 100%;
}
// 兑换区域
.jh_exchange_card {
  width: 100%;
  margin-top: 20px;
  background: #fff;
  height: 540px;
  padding: 0px 30px;
  box-sizing: border-box;
  > div:nth-of-type(1) {
    border-bottom: 1px solid #f5f5f5;
  }
  div:not(:last-of-type) {
    height: 115px;
    display: flex;
    align-items: center;
    color: rgba(0, 0, 0, 0.85);
    font-size: 30px;
    input {
      border: none;
      margin-left: 15px;
    }
  }
  > div:nth-last-of-type(1) {
    width: 690px;
    height: 90px;
    background: #eb6100;
    border-radius: 10px;
    text-align: center !important;
    line-height: 90px;
    color: #fff;
    margin-top: 150px;
  }
}
// 兑换区域结束
</style>
