<template>
  <div class="jh_order">
    <div>
      <returnHome :title="title"></returnHome>
    </div>
    <div class="jh_order_nav">
      <van-tabs>
        <van-tab :title="value.name" v-for="(value,index) in nav" :key="index">
          <div slot="title" @click="navToggle(value.order_status)">{{value.cont}}</div>
          <div class="jh_order_cont" v-if="orderList.length!=0">
            <ul>
                <router-link tag="li" :to="{path:'/orderDetail',query:{order_id:value.id,order_type:order_type}}" v-for="(value,index) in orderList" :key="index">
                <div>
                  <p>订单编号：{{value.order_number}}</p>
                  <p>{{value.pay_status==2?'交易完成':'支付失败'}}</p>
                </div>
                <div>{{value.title}}</div>
                <div>时间：{{value.created_at*1000|timeC}}</div>
                <div>
                  <span>实付款：</span>
                  <img src="@/assets/20191HHDExgz0u1567065946.png" alt />
                  <span>{{value.order_price}}</span>
                </div>
                </router-link>
            </ul>
            <p>没有更多了</p>
          </div>
          <div class="jh_noOrder" v-else>
            <img src="@/assets/empty.0d284c2e.png" alt />
            <p>暂无订单</p>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import returnHome from "../../components/return/return";
import { getOrder } from "../../http/api";
export default {
  components: {
    returnHome
  },
  data() {
    return {
      title: "", //标题,
      nav: [
        { cont: "全部订单", order_status: "0" },
        { cont: "待支付", order_status: "1" },
        { cont: "已完成", order_status: "2" },
        { cont: "以取消", order_status: "3" }
      ],
      order_status: 0, //订单状态
      order_type: "", //订单类型,
      orderList: [],
      page: 1,
      limit: 30
    };
  },
  created() {
    this.title = this.$route.meta.title;
    this.order_type = this.$route.query.order_type; //订单类型
  },
  mounted() {
    this.getOrder();
  },
  methods: {
    navToggle(order_status) {
      this.order_status = order_status;
      this.getOrder();
    },
    //   获取订单
    getOrder() {
      let token = localStorage.getItem("token");
      return getOrder(
        token,
        this.limit,
        this.page,
        this.order_status,
        this.order_type
      ).then(res => {
        this.orderList = res.data.data.list;
      });
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_order {
  width: 100%;

  background: #fff;
  height:100%;
  overflow: auto;
}
// 导航区域
.jh_order_nav {
  width: 100%;
}
.jh_order_nav /deep/.van-tab--active {
  color: #eb6100;
}
.jh_order_nav /deep/.van-tabs__wrap,
.van-tabs__wrap--scrollable {
  height: 90px;
  border-bottom: 3px solid #f5f5f5;
  box-sizing: border-box;
}
.jh_order_nav /deep/.van-tabs__wrap--scrollable .van-tabs__nav--complete {
  padding: 0px;
}
.jh_order_nav /deep/.van-tab__text {
  font-size: 32px;
  height: 90px;
  line-height: 90px;
}
// 导航区域结束

// 订单列表
.jh_order_cont {
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  > p {
    font-size: 28px;
    width: 100%;
    text-align: center;
    color: #969799;
  }
  ul {
    width: 100%;
    li {
      img {
        width: 25px;
        height: 25px;
        margin-right: 10px;
      }
      margin-bottom: 30px;
      width: 100%;
      padding: 25px;
      box-sizing: border-box;
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      height: 250px;
      div:not(:last-of-type) {
        margin-bottom: 10px;
      }
      div {
        font-size: 26px;
        color: #8c8c8c;
      }
      div:nth-of-type(1) {
        font-size: 28px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #f5f5f5;
        padding-bottom: 20px;
        box-sizing: border-box;
        p:nth-of-type(2){
            color:#ea7a2f;
        }
      }
      div:nth-of-type(2) {
        width: 100%;
        font-size: 32px;
        color: #333;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
}
// 订单列表结束
.jh_noOrder {
  width: 100%;
  height: 100%;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    width: 300px;
    height: 300px;
  }
  p {
    font-size: 30px;
    color: #8c8c8c;
    line-height: 80px;
  }
}
</style>
