<template>
  <div class="jh_myStudy">
    <!-- 头部导航区 -->
    <div class="jh_studyDetail_nav">
      <img src="@/assets/return.png" alt @click="goHome" />
      <p>{{$route.meta.title}}</p>
      <img src="@/assets/day.png" alt @click="goDate"/>
    </div>
    <!-- 头部导航结束 -->
    <!-- 学习导航 -->
    <div class="jh_study_nav">
      <van-tabs>
        <van-tab
          v-for="(value,index) in list.typeNum"
          :title="value.name+`(${value.num})`"
          :key="index"
        >
          <div slot="title" @click="navToggle(value.type)">{{value.name+`(${value.num})`}}</div>
          <div class="jh_staduy_cont" v-if="list.courseList.length>0">
            <ul>
                <router-link :to="{path:'/studyDetail',query:{course_id:val.course_id}}" v-for="(val,info) in list.courseList" :key="info" tag="li">
                       <div>{{val.title}}</div>
                <div>
                  <img src="@/assets/clock.png" alt />
                  <p>{{val.start_play_date*1000|timeA}}-{{val.end_play_date*1000|timeA}}</p>
                  <p>| 共{{val.section_num}}课时</p>
                </div>
                <div>
                  <div>
                    <van-progress inactive :percentage="val.progress_rate" pivot-text />
                  </div>
                  <p>已学习{{val.progress_rate}}%</p>
                </div>
                </router-link>
             
            </ul>
          </div>
          <div v-else class="jh_noList">
                <img src="@/assets/empty.0d284c2e.png" alt="">
                <p>还没有课程，快去选择课程学习吧</p>
                <router-link to="/course" tag="div">选择课程</router-link>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import { myStudy_nav } from "../../http/api";
export default {
  data() {
    return {
      list: [], //列表数据和课程数据,
      type: 2 //请求数据的type值
    };
  },
  created() {},
  mounted() {
    this.myStudy_nav();
  },
  methods: {
    goDate(){
      this.$router.push('/studyCalendar')
    },
    //   返回
    goHome() {
      this.$router.push("/login");
    },
    // 获取导航列表
    myStudy_nav() {
      let token = localStorage.getItem("token");
      return myStudy_nav(token, this.type).then(res => {
        this.list = res.data.data;
      });
    },
    // 切换导航
    navToggle(type) {
      this.type = type;
      this.myStudy_nav();
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_myStudy {
  width: 100%;
  background: #fff;
  height: 100%;
  // 导航区域
  .jh_studyDetail_nav {
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 30px;
    box-sizing: border-box;
    border-bottom: 3px solid #f5f5f5;
    p {
      width: 70%;
      text-align: center;
      color: #595959;
    }
    img:nth-of-type(1) {
      width: 20px;
      height: 34px;
    }
    img:nth-of-type(2) {
      width: 35px;
      height: 35px;
    }
  }
  // 导航区域结束
}
// 学习导航区域
.jh_study_nav {
  width: 100%;
}
.jh_study_nav /deep/.van-tab--active {
  color: #eb6100;
}
.jh_study_nav /deep/.van-tabs__wrap,
.van-tabs__wrap--scrollable {
  height: 90px;
  border-bottom: 3px solid #f5f5f5;
  box-sizing: border-box;
}
.jh_study_nav /deep/.van-tabs__wrap--scrollable .van-tabs__nav--complete {
  padding: 0px;
}
.jh_study_nav /deep/.van-tab__text {
  font-size: 32px;
  height: 90px;
  line-height: 90px;
}
.jh_study_nav /deep/ .van-tabs__line {
  bottom: 0px;
}
// 学习导航区域结束

// 内容区域
.jh_staduy_cont /deep/.van-progress,
.van-progress__portion {
  width: 100%;
}
.jh_staduy_cont {
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  background: #fefefe;
  ul {
    width: 100%;
    li:not(:last-of-type) {
      margin-bottom: 30px;
    }
    li {
      border-radius: 20px;
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
      height: 250px;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 50px 0px 40px 30px;
      box-sizing: border-box;
      div {
        display: flex;
        font-size: 32px;
        align-items: center;
        img {
          width: 28px;
          height: 28px;
          margin-right: 10px;
        }
        p {
          margin-right: 10px;
        }
      }
      div:nth-of-type(1) {
        color: #333;
      }
      div:nth-of-type(2) {
        color: #666;
        font-size: 26px;
      }
      div:nth-of-type(3) {
        > div {
          width: 30%;
          margin-right: 20px;
        }
        p {
          font-size: 24px;
          color: #8c8c8c;
        }
      }
    }
  }
}
// 内容其余结束

// 没有课程区域
.jh_noList{
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-top:200px;
    box-sizing: border-box;
    img{
      width:310px;
      height:310px;
      
    }
    p{
        font-size: 30px;
        color:#8c8c8c;
        margin:40px 0px 75px 0px ;
    }
    div{
        width:175px;
        height:60px;
        background: #eb6100;
        color:#fff;
        font-size: 24px;
        text-align: center;
        line-height: 60px;
        border-radius:10px;
    }
}
</style>
