<template>
  <div class="jh_customer">
    <div class="jh_customer_nav">
      <p>给我们留言</p>
      <p @click="goHome">﹀</p>
    </div>
    <!-- 客服区域 -->
    <div class="jh_customer_cont" v-if="isShow">
      <p>您好，很抱歉我们暂时无法为您提供服务，如需帮助，请留言，我们将尽快联系并解决您的问题</p>
      <ul>
        <li>
          <p>手机</p>
          <input type="text" placeholder="请输入" v-model="tel" />
        </li>
        <li>
          <p>留言内容</p>
          <textarea placeholder="请输入" v-model="cont"></textarea>
        </li>
      </ul>

      <div class="jh_customer_add" @click="add">提交</div>
    </div>
    <!-- 客服区域结束 -->
    <!-- 留言成功区域 -->
    <div class="jh_success" v-else>
      <h3>留言成功</h3>
      <p>我们会尽快联系你</p>
      <p @click="goHome">关闭</p>
      <p @click="again">再次留言</p>
    </div>
    <!-- 留言成功区域 -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: true,
      tel: "",
      cont: ""
    };
  },
  created() {},
  mounted() {},
  methods: {
    //   提交
    add() {
      let token = localStorage.getItem("token");
      if(this.tel===''||this.cont===''){
            this.$toast("内容不能为空");
            return false;
      }
      if (!/^1[3456789]\d{9}$/.test(this.tel)) {
        this.$toast("手机格式不正确");
        return false;
      }
      this.$axios({
        url: "https://new-api.meiqia.com/client/tickets_v2?ent_id=149768",
        headers: {
          "Content-Type": "application/json",
          DeviceType: "H5",
          Authorization: `Bearer ${token}`
        },
        method: "post",
        data: {
          channel: "widget",
          client_info: { tel: this.tel },
          content: this.cont,
          enterprise_id: 149768,
          reserve_token: null,
          track_id: "1gZMfxVifsXK3yNrUiw5RkJSIcY",
          visit_id: "1ghoxH5JXtahl4ivwebqlr4dZqT"
        }
      }).then(res => {
        this.isShow = false;
      });
    },
    goHome() {
      this.isShow = true;
      this.$router.push("/login");
    },

    again() {
      this.isShow = true;
    }
  }
};
</script>

<style scoped lang='scss'>
.jh_customer {
  width: 100%;
}
// 导航区域
.jh_customer_nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0px 30px;
  height: 130px;
  background: rgb(0, 122, 255);
  box-sizing: border-box;
  font-size: 34px;
  color: #fff;
  align-items: center;
}
// 导航区域结束

// 留言区域
.jh_customer_cont {
  width: 100%;
  padding: 40px 0px;
  box-sizing: border-box;
  .jh_customer_add {
    width: 660px;
    height: 90px;
    background: #007aff;
    border-radius: 10px;
    text-align: center;
    line-height: 90px;
    color: #fff;
    margin: 20px auto;
  }
  > p {
    font-size: 27px;
    color: rgb(102, 102, 102);
    padding: 0px 30px;
    box-sizing: border-box;
    margin-bottom: 40px;
  }
  ul {
    li {
      textarea,
      input {
        width: 100%;
        border: none;
        padding: 0px 30px;
        box-sizing: border-box;
      }
      input {
        height: 90px;
        background: #fff;
      }
      textarea {
        height: 200px;
        background: #fff;
      }
      p {
        padding: 0px 30px;
        box-sizing: border-box;
        line-height: 100px;
      }
    }
  }
}
// 留言区域结束
// 成功
.jh_success {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 450px;
  padding: 30px 0px;
  box-sizing: border-box;
  align-items: center;
  justify-content: space-between;
  font-size: 38px;
  p:nth-of-type(2) {
    width: 350px;
    height: 80px;
    border-radius: 10px;
    text-align: center;
    line-height: 80px;
    color: #fff;
    background: #007aff;
  }
  p:nth-of-type(3) {
    font-size: 30px;
    text-decoration: underline;
  }
}
// 成功
</style>
